    isPINLocked(cmd, data, value, source) {
        const transactionId = data.transactionId;
        const be2FunctArray = [[this.enumBE2Func.Cashless_IsPinLocked, [0, 0, 0, 0]]];
        const be2FuncReq = NgxCommMsgHelper.createBE2BasicFunctionArr(be2FunctArray);
        this.ngxComm.sendBe2Message(be2FuncReq, (err, data) => {
            if (err) {
                this.logger.error({
                    error: 'viewListener: Received an error in isPINLocked.',
                    data: err
                }, true);
            } else {
                const resp = data[NgxCommMsgHelper.BE2_BASIC_RESPONSE];
                resp.forEach((ele) => {
                    if (ele.functionCode !== this.enumBE2Func.Cashless_IsPinLocked) {
                        this.logger.warn({warn: 'Unknown BE2Response!', data: ''});
                    }
                    else {
                        const resp = this.addTransactionId(transactionId, {
                            cmd: 'isPINLockedResp',
                            ver: '1.0',
                            locked: Boolean(ele.retvals[0])
                        });
                        this.logger.debug({
                                debug: 'viewListener: Sending Cashless_IsPinLockedResp.',
                                data: resp
                            },
                            true);

                        this.sendMessage(source, resp);
                    }
                }, this);
            }
        });
    }

