import * as loglevel from 'loglevel';
import PubSub from "./PubSub";

'use strict';


/**
 * Logger
 *
 * Logger is a library to connect to the Logger. To use it the page
 * must have a query param of "loggerUrl".  The Contrail M5 logger requires a
 * JSON object, but this library doesn't enforce that.  Usage example can be
 * found in shell README.md
 *
 * @param loggerUrl {string} socket connection string in which to send logs.
 * @param deviceId {string} this device id used as part of each log sent.
 * @param minSeverity {string} minimum severity used to set the current log level.
 * @param paramsObj {Object} Params Object used to find the Device details.
 * @constructor
 */
export default class Logger {
    constructor(loggerUrl, deviceId, minSeverity, paramsObj, m5Config) {
        this.m5Config = m5Config;
        this.paramsObj = paramsObj ? paramsObj : {};

        // Commands
        this.COMMANDS = Object.freeze({
            REGISTER: "REGISTER",
            STATUS_UPDATE: "STATUSUPDATE",
            ACTION: "ACTION"
        });

        //Sub Commands for Action Command
        this.ACTION_SUB_COMMANDS = Object.freeze({
            HARD_RESET: "HARD_RESET",
            SOFT_RESET: "SOFT_RESET",
            DEBUG: "DEBUG"
        });


        this.severity = {
            HEARTBEAT: 0,
            DEBUG: 1,
            INFO: 2,
            WARN: 3,
            ERROR: 4,
            COMMAND: 999
        };
        this.reconnectInterval = 1000 / 2;
        this.attemptsToConnect = 0;
        this.connected = false;
        this.givenUp = false;
        this.loggerSocket = null;

        this.loggerUrl = loggerUrl;
        this.deviceId = deviceId;

        // If they didn't pass in minSeverity, 0. If they did and it's a number, use that. If not, check if what they passed in is in the severity enum.
        this.logSeverity = minSeverity === undefined ? 0 : minSeverity >= 0 ? minSeverity : (this.severity[minSeverity.toUpperCase()] || 0);

        this.refreshTime = new Date().getTime();
        this.initialMemoryInfo = this.getMemoryInfo();
        this.currentM5State = 'IDLE';
        this.remoteDebugEnabled = false;
        this.remoteDebugEndTime = null;
        this.remoteDebugSessionTime = 1800000; //30 mins
        this.M5RefreshTime = 2; //2 secs

        this.connect();

        /**
         * Logs unhandled errors.
         */
        window.onerror = (msg, url, lineNo, columnNo, error) => {
            const string = msg.toLowerCase();
            const substring = "script error";

            if (string.indexOf(substring) > -1) {
                this.error('Unhandled Script Error: See Browser Console for Detail.');
            } else {
                const message = [
                    'Message: ' + msg,
                    'URL: ' + url,
                    'Line: ' + lineNo,
                    'Column: ' + columnNo,
                    'Error object: ' + JSON.stringify(error)
                ].join(' - ');

                this.error(message);
            }

            return false;
        };
    }

    getMemoryInfo(){
        var memoryMetrics = null;
        var currentMemoryInfo = window.performance.memory;
        if(currentMemoryInfo) {
            memoryMetrics = {
                jsHeapSizeLimit : currentMemoryInfo.jsHeapSizeLimit  || null,
                totalJSHeapSize : currentMemoryInfo.totalJSHeapSize || null,
                usedJSHeapSize : currentMemoryInfo.usedJSHeapSize || null
            }
        }
        return memoryMetrics;
    }

    sendStatusUpdateCommand(currentState ) {
        this.currentM5State = currentState;
        let statusUpdateCommandData = {
            currentState: currentState|| null,
            initialMemoryInfo: this.initialMemoryInfo,
            currentMemoryInfo: this.getMemoryInfo()
        }
        let statusUpdate = {
            command : 'STATUSUPDATE',
            commandData : statusUpdateCommandData
        };
        this.command(statusUpdate);
    }

    sendRegistrationCommand() {
        let registerCommandPayload = {
            location : this.paramsObj.location || null,
            machineNumber : this.paramsObj.machineNumber || null,
            uid : this.paramsObj.uid || null,
            boardVersion  : this.paramsObj.boardVersion || null ,
            actualWidth  : this.paramsObj.actualWidth || null ,
            actualHeight  : this.paramsObj.actualHeight || null,
            displayWidth  : this.paramsObj.displayWidth || null,
            displayHeight  : this.paramsObj.displayHeight || null,
            screenType  : this.paramsObj.screenType || null,
            screenDescription  : this.paramsObj.screenDescription || null,
            mediaDisplayType  : this.paramsObj.mediaDisplayType || null,
            mediaDisplayPosition  : this.paramsObj.mediaDisplayPosition || null,
            xPosition  : this.paramsObj.xPosition || null,
            yPosition  : this.paramsObj.yPosition || null,
            contentWidth  : this.paramsObj.contentWidth || null,
            contentHeight  : this.paramsObj.contentHeight || null,
            mediaDisplayWidth  : this.paramsObj.mediaDisplayWidth || null,
            touchScreenCapable  : this.paramsObj.touchScreenCapable || null,
            audioCapable  : this.paramsObj.audioCapable || null,
            fullscreenCapable  : this.paramsObj.fullscreenCapable || null,
            bannerPosition  : this.paramsObj.bannerPosition || null,
            bannerHeight  : this.paramsObj.bannerHeight || null,
            debug  : this.paramsObj.debug || null,
            refreshTime : this.refreshTime
        };

        let registrationCommand = {
            command : 'REGISTER',
            commandData : registerCommandPayload
        };
        this.command(registrationCommand);
    }

    connect() {
        this.attemptsToConnect++;
        //loglevel reference is provided by shell
        loglevel.debug(`logger: connection attempt ${this.attemptsToConnect}`);
        try {
            this.loggerSocket = new WebSocket(this.loggerUrl);
            this.loggerSocket.onopen = () => {
                this.attemptsToConnect = 0;
                this.connected = true;
                this.givenUp = false;
                this.sendRegistrationCommand();
                this.sendStatusUpdateCommand(this.currentM5State);
            };
            this.loggerSocket.onclose = e => {
                loglevel.debug(e);
                this.reconnect();
            };
            this.loggerSocket.onerror = e => {
                loglevel.debug(e);
            };
            this.loggerSocket.onmessage = e => {
                this.handleNewMessage(e);
            };
        } catch (e) {
            this.givenUp = true;
            loglevel.error(e);
            loglevel.error('Cannot connect to supplied loggerUrl, logging to console');
        }
    };

    handleNewMessage(e){
        if(e && e.data) {
            var data = JSON.parse(e.data);
            if(data && data.command && data.command === this.COMMANDS.ACTION && data.commandData && data.commandData.commandSubtype ){
                if(data.commandData.commandSubtype === this.ACTION_SUB_COMMANDS.SOFT_RESET) {
                    this.handleSoftReset(data.commandData.commandSubtypeData);
                }
                else if(data.commandData.commandSubtype === this.ACTION_SUB_COMMANDS.HARD_RESET) {
                    this.handleHardReset(data.commandData.commandSubtypeData);
                }
                else if(data.commandData.commandSubtype === this.ACTION_SUB_COMMANDS.DEBUG){
                    this.handleDebugCommand(data.commandData.commandSubtypeData);
                }
            }
        }
        else {
            this.error('logger: handleNewMessage - Failed to handle command from the M5 Server' + JSON.stringify(e));
        }
    }

    handleSoftReset(data) {
        let min = 1;
        let timeToRefresh = (Math.floor(Math.random() * (data - min + 1)) + min) * this.M5RefreshTime;
        this.info('logger: ******Soft Reset Initiated from M5 Server, About to restart in '+ timeToRefresh + ' secs');
        setTimeout(function () {
            window.location.reload(true)
        }, timeToRefresh * 1000);
    }

    handleHardReset(data){
        let min = 1;
        let timeToRefresh = (Math.floor(Math.random() * (data - min + 1)) + min) * this.M5RefreshTime;
        this.info('logger: ******Hard Reset Initiated from M5 Server, About to stop pings to Adv in '+ timeToRefresh + ' secs');
        setTimeout(function () {
            PubSub.publish('serverForceStopHeartBeat',data);
        }, timeToRefresh * 1000);
    }

    handleDebugCommand(data){
        this.info('logger: ******Debug Initiated from Server for next 30 mins');
        this.remoteDebugEnabled = true;
        this.remoteDebugEndTime = new Date().getTime() + this.remoteDebugSessionTime;
    }

    reconnect() {
        if (this.attemptsToConnect === 10) {
            this.givenUp = true;
            loglevel.error('Couldn\'t connect to logger after 10 tries, giving up. Logging to console.');
        }
        setTimeout(() => {
            loglevel.debug('logger: reconnecting...');
            this.connect();
        }, this.reconnectInterval);
    };

    trySend(message) {
        if (!this.connected) {
            if (!this.givenUp) {
                setTimeout(() => {
                    this.trySend(message);
                }, 500);
            } else {
                loglevel.debug('Logger message: ' + JSON.stringify(message));
            }
            return;
        }
        this.loggerSocket.send(message);
    };


    log(msg, rec, sev, diagnosticGroup) {
        var isDiagnostic = diagnosticGroup && this.m5Config && this.m5Config.diagnosticTargets && this.m5Config.diagnosticTargets.includes(diagnosticGroup);
        if(this.remoteDebugEnabled) {
            var now = new Date().getTime();
            if(now >= this.remoteDebugEndTime) {
                this.remoteDebugEnabled = false;
                this.info('logger: ******Remote Debug session ended');
            }
        }
        else if (sev < this.logSeverity && (!isDiagnostic)){
            return;
        }
        const sendMessage = {
            time: new Date(),
            message: msg,
            severity: this.getTextForSeverity(sev),
            source: 'NgxComm-' + this.deviceId,
            received: !!rec
        };
        this.trySend(JSON.stringify(sendMessage));
    }

    heartbeat(msg, rec) {
        this.log(msg, rec, this.severity.HEARTBEAT);
    }

    debug(msg, rec, diagnosticGroup) {
        this.log(msg, rec, this.severity.DEBUG, diagnosticGroup);
    }
     
    info(msg, rec, diagnosticGroup) {
        this.log(msg, rec, this.severity.INFO, diagnosticGroup);
    }
     
    warn(msg, rec, diagnosticGroup) {
        this.log(msg, rec, this.severity.WARN, diagnosticGroup);
    }
     
    error(msg, rec, diagnosticGroup) {
        this.log(msg, rec, this.severity.ERROR, diagnosticGroup);
    }

    command(msg,rec) {
        this.log(msg, rec, this.severity.COMMAND);
    }

    /**
     * Gets the text name for a severity level.
     * @param sev The severity level
     * @returns {String} Plain text name of severity level
     */
    getTextForSeverity(sev) {
        let sevText;
        switch (sev) {
            case this.severity.HEARTBEAT:
                sevText = 'heartbeat';
                break;
            case this.severity.DEBUG:
                sevText = 'debug';
                break;
            case this.severity.INFO:
                sevText = 'info';
                break;
            case this.severity.WARN:
                sevText = 'warn';
                break;
            case this.severity.ERROR:
                sevText = 'error';
                break;
            case this.severity.COMMAND:
                sevText = 'command';
                break;
            default:
                sevText = 'info';
                break;
        }

        return sevText;
    };
};
