import Schemas from './Schemas';
'use strict';

const clone = (value) => {
    return JSON.parse(JSON.stringify(value));
};

const createRequest = (value) => {
    return {
        cmd: 'be2BasicFunction',
        ver: '1.0',
        be2BasicFunction: clone(value)
    };
};

const createErrorMessage = () => {
    return (msg, request, response, currentTicks, delay, maxTicks) => {
        return {
            error: msg,
            request: request,
            response: response,
            ticks: currentTicks,
            delay: delay,
            maxTicks: maxTicks
        };
    };
};

const createDebugMessage = () => {
    return (msg, request, response, currentTicks, delay, maxTicks) => {
        return {
            debug: msg,
            request: request,
            response: response,
            ticks: currentTicks,
            delay: delay,
            maxTicks: maxTicks
        };
    };
};

const createMetricMessage = () => {
    return (msg, successfulAttempts, failedAttempts, unsolicitedResponses, delay, maxTicks) => {
        let totalAttempts = successfulAttempts + failedAttempts;
        let successRate = "";
        if (totalAttempts > 0) {
            if (failedAttempts === 0) {
                successRate = "%100";
            } else {
                successRate = "%" + Math.trunc((successfulAttempts / totalAttempts) * 100)
            }
        } else {
            successRate = "N/A";
        }
        return {
            debug: msg,
            successfulRequests: successfulAttempts,
            unsuccessfulRequests: failedAttempts,
            unsolicitedResponses: unsolicitedResponses,
            successRate: successRate,
            delay: delay,
            maxTicks: maxTicks
        };
    };
};

/**
 * NGX Comm Be2 message processor - Will process one be2BasicFunction request at a time
 * (called the pending request). When a be2BasicResponse occurs the pending
 * request will be resolved.
 * @param sendMessage {function} An instance of NgxComm sendMessage function -  required
 * @param logger {object} An instance of Logger - required
 * @param config {object} A configuration object that dictates timeout (converted to maxTicks), interval delay, metric
 * interval delay, and whether to drop the queue on be2Request timeout - optional
 * @constructor NgxCommBe2MessageProcessor
 */
export default class NgxCommBe2MessageProcessor {
    constructor(sendMessage, logger, config) {

        //metrics
        this.metricCounter = 0;
        this.successfulRequests = 0;
        this.failedRequests = 0;
        this.unsolicitedResponses = 0;

        this.requests = [];
        this.pendingRequests = [];
        this.pendingResponses = [];
        this.logger = logger;

        this.dropQueueOnTimeout = config && typeof config.dropQueueOnTimeout === 'boolean' ? config.dropQueueOnTimeout : false;

        this.delay = 50;
        this.maxTicks = 50;
        this.metricInterval = 3600000;
        let timeout = 2500;
        if (config) {
            // interval min: 5, max: 1000 (1sec)
            if (config.interval && config.interval >= 5 && config.interval <= 1000) {
                this.delay = config.interval;
            }
            // timeout min: delay, max: 60000 (1min)
            if (config.timeout && config.timeout >= this.delay && config.timeout <= 60000) {
                this.maxTicks = config.timeout / this.delay;
            } else {
                this.maxTicks = timeout / this.delay;
            }
            // metric interval min: 60000 (1min), max: 86400000 (1day)
            if (config.metricInterval && config.metricInterval >= 60000 && config.metricInterval <= 86400000) {
                this.metricInterval = config.metricInterval;
            }
        }

        this.errorMsg = createErrorMessage();
        this.debugMsg = createDebugMessage();
        this.metricMsg = createMetricMessage();

        this.tickId = window.setInterval(() => {

            const sendPendingRequest = (requestObj) => {
                requestObj.pending = false;
                sendMessage(requestObj.request);
            };

            const timeoutRequests = () => {
                let reqStr = this.dropQueueOnTimeout ? 'All requests' : 'Request';
                let errorObj = this.errorMsg(
                    'BE2 Basic Function request timed out. ' + reqStr + ' removed from the queue.',
                    this.requests[0].request,
                    null,
                    this.requests[0].ticks,
                    this.delay,
                    this.maxTicks
                );
                this.logger.error(errorObj, true);
                if (this.dropQueueOnTimeout) {
                    this.requests.forEach((request) => {
                        this.failedRequests++;
                        if (typeof request.callback === 'function') {
                            errorObj = this.errorMsg(
                                'BE2 Basic Function request timed out. Request removed from the queue.',
                                request.request,
                                null,
                                request.ticks,
                                this.delay,
                                this.maxTicks
                            );
                            request.callback.call(undefined, errorObj);
                        }
                    });
                    this.requests = [];
                } else {
                    this.failedRequests++;
                    let request = this.requests[0];
                    if (typeof request.callback === 'function') {
                        request.callback.call(undefined, errorObj);
                    }
                    this.requests.splice(0, 1);
                }

            };

            let errorObj = null;

            // snapshot pending responses
            let responses = [...this.pendingResponses];
            this.pendingResponses = [];

            // process responses (there could be multiple responses even though there's only one active request)
            for (let i = responses.length - 1; i > -1; i--) {
                try {
                    // if request matches response
                    let currentRequest = this.requests[0];
                    if (this.isRequest(currentRequest.request, responses[i])) {
                        this.successfulRequests++;
                        if (typeof currentRequest.callback === 'function') {
                            currentRequest.callback.call(undefined, null, responses[i]);
                        }
                        const metricObj = {
                            debug: 'Response from BE2 matched the active request. Total Response time: ' + (currentRequest.ticks * this.delay) + 'ms. ',
                            totalTicks: currentRequest.ticks,
                            delay: this.delay,
                            maxTicks: this.maxTicks
                        };
                        this.logger.debug(metricObj, true);
                        this.requests.splice(0, 1);
                        break;
                    }
                    else {
                        errorObj = this.errorMsg(
                            'Be2 Basic Response processed but no matching request ' +
                            'available. Rejecting message.',
                            responses[i],
                            currentRequest
                        );
                        this.logger.error(errorObj, true);
                        this.unsolicitedResponses++;
                    }
                } catch (e) {
                    errorObj = this.errorMsg(
                        'An exception occurred while processing active be2BasicResponses. ' + e
                    );
                    this.logger.error(errorObj, true);
                }
            }

            // add pending requests
            this.requests = this.requests.concat(this.pendingRequests);
            this.pendingRequests = [];

            // process current request
            try {
                if (this.requests.length > 0) {
                    let currentRequest = this.requests[0];
                    if (currentRequest.pending) {
                        sendPendingRequest(currentRequest);
                    }
                    else if (currentRequest.ticks >= this.maxTicks) {
                        // if a request times out, clear it or all requests dependent on dropQueueOnTimeout
                        timeoutRequests();
                    }
                    else {
                        currentRequest.ticks++;
                    }
                }
            } catch (e) {
                errorObj = this.errorMsg(
                    'An exception occurred while processing active be2BasicFunction requests.' + e
                );
                this.logger.error(errorObj, true);
            }

        }, this.delay);

        window.setInterval(() => {
            this.metricCounter++;
            let metricObj = this.metricMsg(
                "BE2 Request/Response health check: " + this.metricCounter,
                this.successfulRequests,
                this.failedRequests,
                this.unsolicitedResponses,
                this.delay,
                this.maxTicks
            );
            this.logger.debug(metricObj, true);
        }, this.metricInterval);
    }

    /**
     * @memberOf NgxCommBe2MessageProcessor
     * @method makeRequest
     * @instance
     * @param data {Array} An array of be2BasicFunctions
     * @param callback {Function} A callback in the style of node error first
     * callbacks
     */
    makeRequest(data, callback) {
        const request = createRequest(data);
        Schemas.validate(request, 'be2BasicFunction').then(() => {
            this.pendingRequests.push({
                request: request,
                callback: callback,
                pending: true,
                ticks: 0
            });
        }, () => {
            const error = this.errorMsg(
                'Be2 Basic Function is invalid according to the schema. ' +
                'Rejecting message.',
                request
            );
            this.logger.error(error, true);
            if (typeof callback === 'function') {
                callback.call(undefined, error);
            }
        });
    };

    /**
     * Process the be2 basic response. Will call the callback associated with
     * the pending be2 basic function request.
     * @memberOf NgxCommBe2MessageProcessor
     * @method processResponse
     * @instance
     * @param response {Object} A be2BasicResponse object
     */
    processResponse(response) {
        Schemas.validate(response, 'be2BasicResponse').then(() => {
            const errorObj = this.errorMsg(
                'Be2 Basic Response received but there are no active requests. ' +
                'Rejecting message.',
                response
            );
            if (this.requests.length > 0  && !this.requests[0].pending) {
                this.pendingResponses.push(response);
            } else {
                this.logger.error(errorObj, true);
                this.unsolicitedResponses++;
            }
        }, () => {
            const error = this.errorMsg(
                'Be2 Basic Response is invalid according to the schema. ' +
                'Rejecting message.',
                response
            );
            this.logger.error(error, true);
        });
    };

    /**
     * Determine if a specific be2BasicResponse matches a be2BasicFunction, will match even for
     * disabled and invalid be2BasicFunction call special cases.
     * This is intended for private class usage only, but is exposed for unit testing.
     * @memberOf NgxCommBe2MessageProcessor
     * @method isRequest
     * @instance
     * @param request {Object} A be2BasicRequest object
     * @param response {Object} A be2BasicResponse object
     */
    isRequest(request, response) {
        if (request.be2BasicFunction.length !== response.be2BasicResponse.length) {
            return false;
        }
        let i;
        let message = null;
        const requestCodes = request.be2BasicFunction.map(code => code.functionCode);
        const responseCodes = response.be2BasicResponse.map(code => code.functionCode);
        for (i = 0; i < requestCodes.length; i++) {
            if (requestCodes[i] !== responseCodes[i]) {
                if (responseCodes[i] === 0) {
                    message = this.debugMsg(
                        'WARN: NgxCommBe2MessageProcessor received a response for the invalid be2BasicFunction: ' +
                        requestCodes[i] + '. It\'s likely this function should not be called.'
                    );
                    this.logger.debug(message, true);
                } else {
                    return false;
                }
            } else if (response.be2BasicResponse[i].retvals[0] === -1 && response.be2BasicResponse[i].retvals[1] === -1 &&
            response.be2BasicResponse[i].retvals[2] === -1 && response.be2BasicResponse[i].retvals[3] === -1) {
                message = this.debugMsg(
                    'WARN: NgxCommBe2MessageProcessor received a response for the disabled be2BasicFunction: ' +
                    responseCodes[i] + '. It\'s likely this function should not be called.'
                );
                this.logger.debug(message, true);
            }
        }
        return true;
    };

    /**
     * Cleanup intervals. Typically should be called in Unit tests before
     * NgxCommBe2MessageProcessor instance is destroyed.
     * @memberOf NgxCommBe2MessageProcessor
     * @method cleanup
     */
    cleanup() {
        window.clearInterval(this.tickId);
    };
};
