import NgxCommMsgHelper from './NgxCommMsgHelper';
import Param from './Param';
import Util from './Util';
import WorkersController from "./WorkersController";
import screenIds from 'igt-nexgen-codes/lib/screenIds';
import PubSub from './PubSub';

'use strict';


// using tag a is more robust than URL object, it can receive relative url.
let parser1 = document.createElement("a");
let parser2 = document.createElement("a");

/**
 * ViewControllers provides access to a number of view controller types
 * @param {object} NgxCommMsgHelper instance
 * @return {Object} The list of view controllers available for use.
 */
export default class ViewControllers {
    static compareURLs(url1, url2) {
        parser1.href = url1.toLocaleLowerCase();
        parser2.href = url2.toLocaleLowerCase();
        let result1 = parser1.protocol == parser2.protocol &&
            parser1.host == parser2.host &&
            parser1.pathname == parser2.pathname &&
            parser1.search == parser2.search;
        let result2 = parser1.hash == parser2.hash;

        return {
            exactSame: result1 && result2,
            needsRefresh: !result1,
            onlyHashChanged: result1 && !result2
        }

    };
    /**
     * Checks for an empty object.
     * @param obj {Object} The object.
     * @return {Boolean} The result.
     */
    static isEmptyObject(obj) {
        return obj === null || Object.keys(obj).length === 0;
    }

    /**
     * Create the url.
     * @param pageUrl {Object} The page url.
     * @param url {String} The url.
     * @return {String} The new url.
     */
    static createUrl(pageUrl, url) {
        if (typeof url === 'undefined') {
            return pageUrl.toString();
        }
        let newUrl;
        if (pageUrl !== undefined) {
            // reuse parser1 becase of the single threading runtime 
            parser1.href = url
            let newSearch = parser1.search == "" ?  `?${pageUrl}` :   `${parser1.search}&${pageUrl}`;
            newUrl = parser1.protocol + "//" + parser1.hostname + (parser1.port == "" ? "": `:${parser1.port}`) + parser1.pathname + newSearch + parser1.hash;
        } else {
            newUrl = url;
        }
        return newUrl;
    }

    /**
     * Register for updates.
     * @param state {Object} The iframe element to persist and load.
     * @param ngxComm {Object} An instance ngxComm which maintains the content interface connection.
     * @param subManager{Object} An instance subManager which maintains the current content.
     */
    static registerForUpdates(state, ngxComm, subManager) {
        const subscription = subManager.getSubscription().subscription;
        const triggerIds = subscription.screenTrigger;
        const tokenIds = subscription.tokenUpdate;
        const userTokenIds = subscription.userTokenUpdate;

        // register for screen triggers that are currently being subscribed to
        triggerIds.forEach(id => {
            ngxComm.registerScreenTrigger(id, state.sendMessage);
        });
        // Registering all common tokens for the viewports.
        tokenIds.forEach(id => {
            ngxComm.registerTokenUpdate(id, state.sendMessage);
        });
        // Registering all common user tokens for the viewports.
        userTokenIds.forEach(id => {
            ngxComm.registerUserTokenUpdate(id, state.sendMessage);
        });
        // Subscribe to windowState message
        ngxComm.registerWindowState(state.sendMessage);
        // Subscribe to vpc player data update message
        ngxComm.registerVpcPlayerData(state.sendMessage);
        // Subscribe to patron data
        ngxComm.registerPatronData(state.sendMessage);
        // Subscribe to patron data ext
        ngxComm.registerPatronDataExt(state.sendMessage);
        // Subscribe to QR code message
        ngxComm.registerQRCode(state.sendMessage);
        // Subscribe to level update message
        ngxComm.registerLevelUpdate(state.sendMessage);
        // Subscribe to ismUpdate message
        ngxComm.registerIsmUpdate(state.sendMessage);
        // Subscribe to registerGameEvent message
        ngxComm.registerGameEvent(state.sendMessage);
        // Subscribe to status update message
        ngxComm.registerStatusUpdate(state.sendMessage);
        // Subscribe to draw status message
        ngxComm.registerDrawStatus(state.sendMessage);
        // Subscribe to server addresses status message
        ngxComm.registerServerAddresses(state.sendMessage);
        // Subscribe to force card out ack status message
        ngxComm.registerForceCardOutAck(state.sendMessage);
        // Subscribe to game start status message
        ngxComm.registerGameStart(state.sendMessage);
        // Subscribe to game end status message
        ngxComm.registerGameEnd(state.sendMessage);
        // Subscribe to bonus hit status message
        ngxComm.registerBonusHit(state.sendMessage);
    }

    /**
     * Formats an event.
     * @param event {Object} The event.
     * @return {Object} The formatted event.
     */
    static formatEvent(event) {
        return (event && event instanceof CustomEvent) ? event.detail : event;
    }

    /**
     * Sends the latest.
     * @param state {Object} The iframe element to persist and load.
     * @param ngxComm {Object} An instance ngxComm which maintains the content interface connection.
     * @param subManager{Object} An instance subManager which maintains the current content.
     * @param events{Array} An array of the events.
     */
    static getLatestAndSend(state, ngxComm, subManager, events = []) {
        const subscription = subManager.getSubscription().subscription;
        const tokenIds = subscription.tokenUpdate;

        if(events && 'length' in events){
            events.forEach(event => {
                state.sendMessage(this.formatEvent(event));
            });
        }

        tokenIds.forEach(id => {
            const value = ngxComm.getCurrentTokenValue(id);
            if (typeof value !== 'undefined') {
                state.sendMessage(NgxCommMsgHelper.createTokenUpdate(
                    id,
                    value
                ));
            }
        });

        let data;

        // Player data
        data = ngxComm.getCurrentVpcPlayerData();
        if (!this.isEmptyObject(data)) {
            state.sendMessage(data);
        }

        // Status update
        data = ngxComm.getCurrentStatusUpdate();
        if (!this.isEmptyObject(data)) {
            state.sendMessage(data);
        }

        // Level update
        data = ngxComm.getCurrentLevelUpdate();
        if (!this.isEmptyObject(data)) {
            state.sendMessage(data);
        }

        // Bonus hit
        data = ngxComm.getCurrentBonusHit();
        if (!this.isEmptyObject(data)) {
            state.sendMessage(data);
        }

        // Patron data
        data = ngxComm.getCurrentPatronData();
        if (!this.isEmptyObject(data)) {
            state.sendMessage(data);
        }

        // Patron data ext
        data = ngxComm.getCurrentPatronDataExt();
        if (!this.isEmptyObject(data)) {
            state.sendMessage(data);
        }

        // Window State
        data = ngxComm.getCurrentWindowState();
        if (!this.isEmptyObject(data)) {
            state.sendMessage(data);
        }

        // Draw Status
        data = ngxComm.getCurrentDrawStatus();
        if (!this.isEmptyObject(data)) {
            state.sendMessage(data);
        }

        // IsmUpdate
        data = ngxComm.getCurrentIsmUpdateData();
        if (!this.isEmptyObject(data)) {
            state.sendMessage(data);
        }
    }

    /**
     * Sets the src attribute and load the iFrame.
     * @param ele {Object} The DOM element.
     * @param url {String} The source url.
     * @return {Promise} The src attribute value.
     */
    static load(ele, url) {
        return new Promise((resolve) => {
            ele.setAttribute('src', url);
            ele.onload = function () {
                resolve(this.src);
            };
        });
    }

    /**
     * Loads the iFrame and sends the latest events.
     * When the onload event happens it will get latest content interface values and send.
     * @param state {Object} The iframe element to persist and load.
     * @param url {String} The source url.
     * @param ngxComm {Object} An instance ngxComm which maintains the content interface connection.
     * @param subManager{Object} An instance subManager which maintains the current content.
     * @param events{Array} An array of the events.
     * @return {Promise} The source url.
     */
    static loadAndSendLatest(state, url, ngxComm, subManager, m5config, events = []) {
        return new Promise((resolve, reject) => {
            try {
                //Prevent the iFrame from being reloaded if the url's are identical and don't load content if the url doesn't exist.
                if (state.element.contentWindow.location.href && url !== "") {
                    url = this.createUrl(state.pageUrl, url);
                    let compareResults = this.compareURLs(url, state.element.contentWindow.location.href);
                    if (compareResults.needsRefresh) {
                        this.load(state.element, url)
                            .then(srcUrl => {
                                this.getLatestAndSend(state, ngxComm, subManager, events);
                                // listen for onload events that would be caused by user navigation
                                // fe. if content executed `window.location.href='xtracredit.html'`
                                // this onload will fire and send latest
                                state.element.onload = () => {
                                    this.getLatestAndSend(state, ngxComm, subManager, events);
                                };
                                resolve(srcUrl);
                            })
                            .catch(reject);
                            if(m5config.autoHideFullscreenWhenNewContentTriggered){
                                PubSub.publish('hide-full-screen');
                            }
                    } else {
                        if (compareResults.onlyHashChanged) {
                            state.element.setAttribute('src', url);
                            this.getLatestAndSend(state, ngxComm, subManager, events);
                        }
                        resolve(url);
                    }
                } else {
                    reject();
                }
            }
            catch (e) {
                reject(e);
            }
        });
    }

    /**
     * Provides the sendMessage method for composition.
     * @param state {Object} data object
     */
    static canSendMessage(state) {
        return {
            /**
             * Sends message to the content.
             * @param event {Object} the event to send.
             */
            sendMessage: (event) => {
                const formattedData = this.formatEvent(event);

                if (state.element !== null && typeof state.element !== 'undefined' && state.element.contentWindow !== null && typeof state.element.contentWindow !== 'undefined') {
                    Util.postMessage(state.element.contentWindow, formattedData);
                    WorkersController.Instance().broadcastMessage(formattedData);
                } else {
                    state.logger.warn({
                        warn: `ViewController: ${state.name} - Unable to send message to content, the element or content window is not defined`,
                        data: formattedData
                    }, false);
                }

            }
        };
    }

    /**
     * Provides the setupIframe method for composition.
     * @param state {Object} The iframe element to persist and load.
     * @return {Object} The setup iFrame function.
     */
    static canSetupIframe(state) {
        return {
            /**
             * Persists the iframe element and loads the src url in the iframe.
             * @param element {Object} The state element.
             * @param initialUrl The initial url.
             * @return {Promise} The load iFrame result.
             */
            setupIframe: async (element, initialUrl) => {
                state.element = element;
                let newUrl = this.createUrl(state.pageUrl, initialUrl);
                state.logger.debug({ info: 'Switched iFrame source to: ', data: newUrl }, false);
                await this.load(state.element, newUrl);
            }
        }
    }

    /**
     * Provides the setupIframe method for composition.
     * @param state {Object} The iframe element to persist and load.
     * @param ngxComm {Object} An instance ngxComm which maintains the content interface connection.
     * @param subManager{Object} An instance subManager which maintains the current content.
     * @return {Object} The setup iFrame function.
     */
    static canSetupIframeWStartupTokens(state, ngxComm, subManager, m5Config) {
        return {
            /**
             * Persists the iframe element and loads the src url in the iframe.
             * When the onload event happens it will get latest content interface values and send.
             * @param element {Object} The state element.
             * @param initialUrl The initial url.
             * @return {Promise} The load iFrame result.
             */
            setupIframe: async (element, initialUrl) => {
                state.element = element;
                await this.loadAndSendLatest(state, initialUrl, ngxComm, subManager, m5Config);
            }
        }
    }

    /**
     * The Base View Controller that provides sendMessage and setupIframe functionality.
     * @param logger {Object} A logger instance used to log information.
     * @param pageUrl {Object} The url params to add to all loaded content.
     * @param name {String} The name the view controller used as part of the logging.
     * @return {Object} The View Controller object.
     */
    static BaseVC(logger, pageUrl, name) {
        const state = {
            name: name,
            logger: logger,
            pageUrl: pageUrl,
            urlParams: Param.getUrlParamsObject(pageUrl),
            element: null
        };

        return Object.assign(
            state,
            this.canSendMessage(state),
            this.canSetupIframe(state)
        );
    }


    /**
     * View Controller with registration provides the same functionality as the BaseVC.
     * In addition it will automatically send content interface updates to the loaded content.
     * @param logger {Object} A logger instance used to log information.
     * @param pageUrl {Object} The url params to add to all loaded content.
     * @param name {String} The name the view controller used as part of the logging.
     * @param ngxComm {Object} An instance ngxComm which maintains the content interface connection.
     * @param subManager{Object} An instance subManager which maintains the current content.
     * @return {Object} The View Controller object.
     */
    static VCWRegistration(logger, pageUrl, name, ngxComm, subManager, m5Config) {
        const state = {
            name: name,
            logger: logger,
            pageUrl: pageUrl,
            urlParams: Param.getUrlParamsObject(pageUrl),
            element: null,
            currentTriggerId: null,
            currentUrl: null
        };

        Object.assign(
            state,
            this.canSendMessage(state),
            this.canSetupIframeWStartupTokens(state, ngxComm, subManager, m5Config)
        );

        this.registerForUpdates(state, ngxComm, subManager);

        return state;
    }

    /**
     * View Controller with registration and Trigger provides the same functionality as the VCWRegistration.
     * In addition it will automatically change the content source when screen triggers occur.
     * @param logger {Object} A logger instance used to log information.
     * @param pageUrl {Object} The url params to add to all loaded content.
     * @param name {String} The name the view controller used as part of the logging.
     * @param ngxComm {Object} An instance ngxComm which maintains the content interface connection.
     * @param subManager{Object} An instance subManager which maintains the current content.
     * @param urlMap{Object} An object that maps url's to screen trigger ids.
     * @return {Object} The View Controller object.
     * @return {Object} M5 Config.
     */
    static VCWRegAndTrigger(logger, pageUrl, name, ngxComm, subManager, urlMap, m5Config) {
        const state = {
            name: name,
            logger: logger,
            pageUrl: pageUrl,
            urlParams: Param.getUrlParamsObject(pageUrl),
            element: null
        };

        Object.assign(
            state,
            this.canSendMessage(state),
            this.canSetupIframeWStartupTokens(state, ngxComm, subManager, m5Config)
        );

        this.registerForUpdates(state, ngxComm, subManager);

        const subscription = subManager.getSubscription().subscription;
        const triggerIds = subscription.screenTrigger;

        // register for screen triggers that are currently being subscribed to
        triggerIds.forEach(id => {
            ngxComm.registerScreenTrigger(id, event => {
                const data = this.formatEvent(event);
                const triggerId = data.screenTrigger.screenId;
                if (typeof urlMap.screenIdMap[triggerId] === 'string') {
                    let url = urlMap.screenIdMap[triggerId];
                    url = url === urlMap.baseUrl ? "" : url;
                    if (state.currentTriggerId !== triggerId) {
                        state.currentTriggerId = triggerId;

                        //Disable sending subscription everytime
                        if (m5Config && m5Config.enableAdvNgxSubscriptionPerScreen) {
                            subManager.resetSubscription();
                            ngxComm.sendSubscription();
                        }

                        //Force Show Service window
                        if (m5Config && m5Config.forceOpenServiceWindow && m5Config.forceOpenServiceWindow.enabled) {
                            if (m5Config.forceOpenServiceWindow.screenTriggerList && m5Config.forceOpenServiceWindow.screenTriggerList.length > 0) {
                                if (m5Config.forceOpenServiceWindow.screenTriggerList.includes(triggerId)) {
                                    this.forceOpenServiceWindow(ngxComm);
                                };
                            }
                        }

                        this.loadAndSendLatest(state, url, ngxComm, subManager, m5Config, [event]).then(url => {
                            logger.debug({ debug: 'Switched iFrame source to: ', data: url }, false);
                        });
                        logger.debug({ debug: 'Switching iFrame source to: ', data: url }, false);
                        this.getLatestAndSend(state, ngxComm, subManager, [event]); // for spa functionality
                    }
                    else { // if trigger is the same update content but don't do load (spa functionality)
                        this.getLatestAndSend(state, ngxComm, subManager, [event]);
                        logger.debug({ debug: 'Sending trigger event and tokens to iFrame: ', data: event }, false);
                    }
                    /* For Victorian Pre-Commitment, autoHide needs to the set to true and then turned off when the screen is not a pre-commitment screen */
                    if ((state.currentTriggerId >= screenIds.VPC_PIN_SCREEN) && (state.currentTriggerId <= screenIds.VPC_CARD_OUT_SCREEN)) {
                        this.setAutoHideSubscription(subManager, ngxComm, true);
                    }
                    else {
                        this.setAutoHideSubscription(subManager, ngxComm, false);
                    }

                }
                else { // if not in url map update the content (subscribe all triggers functionality)
                    this.getLatestAndSend(state, ngxComm, subManager, [event]);
                    logger.debug({ debug: 'Sending unknown event to iFrame: ', data: event }, false);
                }
            });
        });

        return state;
    }

    static setAutoHideSubscription(subManager, ngxComm, value) {
        if (!subManager.isAttrEqual('disableAutoHide', value)) {
            subManager.setAttribute('disableAutoHide', value);
            ngxComm.sendSubscription();
        }
    };

    static forceOpenServiceWindow(ngxComm) {
        const msg = NgxCommMsgHelper.createShowHost(true);
        ngxComm.sendMessage(msg);
    }
}
