'use strict';

/**
 *
 * NGX Comm Message Helper - helps creating messages that are to be sent to NGX.
 * Class for exposing helper functions and attributes for creating NGX Comm messages.
 *
 * @type {{BE2_BASIC_FUNCTION: string, BE2_BASIC_RESPONSE: string, enumBE2BasicFunc: {MENU_IsENTEROnPINReq: number,
 * PIN_GetStateInfo: number, PIN_Check_PIN_Needed: number, PIN_ValidatePIN: number, PIN_GetPinStatus: number,
 * PIN_GetCardType: number, PIN_ForcePIN: number, PIN_NotEntered: number, MENU_GetXPCParameters: number,
 * MENU_Card_Duplicate: number, PT_Get_Player_Data: number, PT_Get_Player_Menu_State: number,
 * GetXtraCreditBalance: number, MoveCredits: number, CancelXtraCredit: number, MP_PointsToCash: number,
 * MP_PointsToXtraCredits: number}, createBE2BasicFunctionArr: Function, createBE2BasicFunctionReq: Function,
 * getBE2BasicFunctionMap: Function}}
 */
export default class NgxCommMsgHelper {
    static get BE2_BASIC_FUNCTION() {
        return 'be2BasicFunction';
    }

    static get MSG_VERSION() {
        return '1.0';
    }

    static get BE2_BASIC_RESPONSE() {
        return 'be2BasicResponse';
    }

    static get enumBE2BasicFunc() {
        return {
            FJP_Init: 0,
            FJP_Op: 1,
            FJP_GetCurrentHandpayParams: 2,
            FJP_Event:3,
            GetPointPlayBalance: 12,
            ValueToPoints: 13,
            ConvertPoints: 14,
            GetXtraCreditBalance: 15,
            MoveCredits: 16,
            LockPin: 17,
            MP_Toggle: 19,
            PT_Get_Player_Menu_State: 20,
            GetCashlessParameters: 22,
            Cashless_TransferFromGame: 23,
            Cashless_TransferFromCard: 24,
            MP_DenomCheck: 25,
            MP_PointsToCash: 26,
            MP_GetEgmDenom: 27,
            MP_SetBonusLight: 28,
            Cashless_Pin_Verify: 29,
            Cashless_IsPinLocked: 30,
            GetCashlessLimits: 31,
            GetCashlessFlags: 32,
            CashlessPinValid: 33,
            Cashless_Pin_Check_Needed: 46,
            GetPointPlayLimitedBalance: 47,
            PT_GetPlayerLevel: 48,
            GetMoreCashlessLimits: 49,
            PT_Get_Player_Data: 50,
            MENU_GetXPCParameters: 56,
            MENU_Card_Duplicate: 57,
            FJP_IsJPWitnessRequired: 61,
            MP_PointsToXtraCredits: 70,
            PT_Get_Bonusing_Status: 71,
            MENU_IsENTEROnPINReq: 72,
            PIN_GetStateInfo: 73,
            PIN_Check_PIN_Needed: 75,
            PIN_ValidatePIN: 76,
            PIN_GetPinStatus: 77,
            PIN_GetCardType: 78,
            Cashless_ForceUnlock: 84,
            PIN_ForcePIN: 85,
            PIN_NotEntered: 87,
            Cashless_Get_Config_Data: 88,
            LOLSCH_PatronRequestUnlock: 99,
            MP_PointsToPayment: 100,
            MP_CheckPointPayCode: 101,
            MENU_SetAutoPlayStatus: 102,
            MENU_GetAutoPlayStatus: 103,
            PT_Get_Point_Pay_Available: 104,
            PayToCreditEnableEgm: 105,
            Cashless_GetStatus: 106,
            PIN_OverridePINScreen: 107,
            PIN_SetForcePIN: 108,
            MISC_ToneABI: 109,
            MISC_BezelColor: 110,
            CancelXtraCredit: 111,
            TransferXtraCredit: 114,
            ContentLockGame: 115,
            OverrideW2GAccrualScreen: 116
        }
    }

    /**
     * Helper function to create a BE2 basic function JSON array.
     * @param be2BasicFunctionArr An array of BE2 basic functions. Number of Array elements = 1 to 5.
     * Structure of array: [
     *  [<int>fnCode, <int array>[param1, param2, param3, param4]],
     *  [<int>fnCode, <int array>[param1, param2, param3, param4]]
     * ]
     * @returns {Array} JSON array with BE2 basic functions
     */
    static createBE2BasicFunctionArr(be2BasicFunctionArr) {
        const data = [];
        for (let i = 0; i < be2BasicFunctionArr.length; i++) {
            data.push({
                'functionCode': be2BasicFunctionArr[i][0],
                'arguments': be2BasicFunctionArr[i][1]
            });
        }

        return data;
    }

    /**
     * Helper function to create a BE2 basic function request.
     * @param be2FunctArray [{functionCode: 0x11, arguments: [0, 0, 0, 0]}] Array of BE2 function code object to be sent to NextGen device.
     * @returns {{cmd: string, ver: string, be2BasicFunction: *}}
     */
    static createBE2BasicFunctionReq(be2FunctArray) {
        return {
            'cmd': NgxCommMsgHelper.BE2_BASIC_FUNCTION,
            'ver': NgxCommMsgHelper.MSG_VERSION,
            'be2BasicFunction': be2FunctArray
        };
    }

    /**
     * Returns a map of function codes for easy access.
     * This is needed because according to protocol functionCode order in BE2 response array is not guaranteed to be
     * same as the BE2 request array.
     * NOTE: Assumes that all the functions in functs are present in be2BasicResp and so doesn't check for related errors.
     * @param functs An array of function codes for which a result is present in BE2 response.
     * @param be2BasicFunctArray The BE2 basic function array in the BE2 response object.
     * @returns {{}} An Object created as a map. [<int>functionCode, <int array>retvals]
     */
    static getBE2BasicFunctionMap(functs, be2BasicFunctArray) {
        const respMap = {};
        for (let i = 0; i < functs.length; i++) {
            for (let j = 0; j < be2BasicFunctArray.length; j++) {
                if (functs[i] === be2BasicFunctArray[j].functionCode) {
                    respMap[functs[i]] = be2BasicFunctArray[j].retvals;
                    break;
                }
            }
        }

        return respMap;
    }

    /**
     * Creates a host event button press data message.
     * @param {number} hostEventId The host event id to use.
     * @param {number} subEventId The sub event id to use.
     * @param {boolean} onlineOnly The online only flag to use.
     * @param {number} bonusId The bonus id to use.
     * @returns {Object} A host event button press message object that includes the hostEventId, subEventId, onlineOnly and bonusId.
     **/
    static createHostEventButtonPress(hostEventId, subEventId, onlineOnly, bonusId) {
        return {
            cmd: 'hostEventButtonPress',
            ver: '1.0',
            hostEventButtonPress: {
                hostEventId: hostEventId,
                subEventId: subEventId,
                onlineOnly: onlineOnly,
                bonusId: bonusId
            }
        };
    }

    /**
     * Creates a vpc PIN data message.
     * @param {string} pin The value of the PIN.
     * @returns {Object} A vpc PIN data message object that includes the PIN provided.
     **/
    static createVpcPin(pin) {
        return {
            cmd: 'vpcPIN',
            ver: '1.0',
            pin: pin
        };
    }

    /**
     * Creates a showWindow data message.
     * @param {boolean} showWindow The showWindow action.
     * @returns {Object} A showWindow message object that includes the showWindow action boolean.
     **/
    static createShowHost(showWindow) {
        return {
            cmd: 'showWindow',
            ver: '1.0',
            showWindow: showWindow
        };
    }

    /**
     * Creates a vpc get player data message.
     * @returns {Object} A vpc get player data message.
     **/
    static createVpcGetPlayerData() {
        return {
            cmd: 'vpcGetPlayerData',
            ver: '1.0'
        };
    }

    /**
     * Creates a vpc button press data message.
     * @param {string} value The vpc button press value to send.
     * @returns {Object} A vpc button press message object that includes the value provided.
     **/
    static createVpcButtonPress(value) {
        return {
            cmd: 'vpcButtonPress',
            ver: '1.0',
            vpcButtonPress: value
        };
    }

    /**
     * Creates a get server addresses data message.
     * @param {Array} value An array of string values that represent the server address
     * namespace requesting.
     * @returns {Object} A get server addresses data object that includes the value provided.
     **/
    static createGetServerAddresses(value) {
        return {
            cmd: 'getServerAddresses',
            ver: '1.0',
            getServerAddresses: value
        };
    }

    /**
     * Creates a force card out data message.
     * @returns {Object} A force card out data object.
     **/
    static createForceCardOut() {
        return {
            cmd: 'forceCardOut',
            ver: '1.0'
        };
    }

    /**
     * Creates a screen trigger message.
     *
     * @param screenId The screenId to send down
     * @param displayMessage The display message to send down
     * @param messageTokens (Object) extra messages to be passed in screen trigger
     * @returns {Object} A screen trigger message data object.
     **/
    static createScreenTrigger(screenId, displayMessage, messageTokens) {
        return {
            cmd: 'screenTrigger',
            screenTrigger: {
                screenId: this.normalizeId(screenId),
                displayMessage: displayMessage,
                messageTokens: messageTokens
            }
        };
    }

    /**
     * Creates a token update message.
     *
     * @param tokenId The tokenId to send down
     * @param value The value to send down
     * @returns {Object} A token update message data object.
     **/
    static createTokenUpdate(tokenId, value) {
        return {
            cmd: 'tokenUpdate',
            tokenUpdate: [
                {
                    id: this.normalizeId(tokenId),
                    value: value
                }
            ]
        };
    }

    /**
     * Creates a button press data message.
     *
     * @param keyPressId The keyPressId to use.
     * @returns {Object} A button press message data object.
     **/
    static createButtonPress(keyPressId) {
        return {
            cmd: 'buttonPress',
            ver: '1.0',
            buttonPress: keyPressId
        };
    }

    /**
     * Creates a get QR code message.
     *
     * @returns {Object} A get QR code message.
     **/
    static createGetQRCode() {
        return {
            cmd: 'getQRcode',
            ver: '1.0'
        };
    }

    /**
     * Creates a user token update message.
     *
     * @param tokenId The tokenId to send down
     * @param value The value to send down
     * @returns {Object} A user token update message data object.
     **/
    static createUserTokenUpdate(tokenId, value) {
        return {
            cmd: 'userTokenUpdate',
            userTokenUpdate: [
                {
                    id: this.normalizeId(tokenId),
                    value: value
                }
            ]
        };
    }

    /**
     * Creates a normalized hex string.
     * @param id The id can be  string of number.
     * @returns string
     */
    static normalizeId(id) {
        if (typeof id === 'string') {
            return NgxCommMsgHelper.createIdString(parseInt(id, 16));
        } else {
            return NgxCommMsgHelper.createIdString(id);
        }
    }

    /**
     * Creates a normalized hex string from a number.
     * @param idint The id can of type int or number.
     * @returns string
     */
    static createIdString(idint) {
        const value = idint.toString(16);
        let padded = value;

        if (value.length === 1) {
            padded = '0' + value;
        }
        return '0x' + padded.toUpperCase();
    }

    static createBE2BasicRespMsg(funcCode, argArray) {
        return {
            "cmd": this.BE2_BASIC_RESPONSE,
            "ver": this.MSG_VERSION,
            "be2BasicResponse": [
                {
                    "functionCode": funcCode,
                    "retvals": [argArray[0], argArray[1], argArray[2], argArray[3]]
                }
            ]
        };
    }

    static getBE2BasicRespMsg(be2FunctArray) {
        const resp = {
            "cmd": this.BE2_BASIC_RESPONSE,
            "ver": this.MSG_VERSION,
            "be2BasicResponse": []
        };

        for (let i = 0; i < be2FunctArray.length; i++) {
            resp.be2BasicResponse.push({
                    "functionCode": be2FunctArray[i][0],
                    "retvals": be2FunctArray[i][1]
                }
            );
        }
        return resp;
    }
};
