const gameEvents = [];

const generateUserTokenList = function (ngxSubscriptionConfig) {
    const list = [];

    if(ngxSubscriptionConfig && ngxSubscriptionConfig.userTokenUpdate && ngxSubscriptionConfig.userTokenUpdate.enabled && ngxSubscriptionConfig.userTokenUpdate.list){
        for (let i = 0; i < ngxSubscriptionConfig.userTokenUpdate.list.length; i++) {
            list.push(`0x${ngxSubscriptionConfig.userTokenUpdate.list[i].toString(16).toUpperCase().padStart(2, "0")}`);
        }
    }
    return list;
};
const clone = function (value) {
    return JSON.parse(JSON.stringify(value));
};

const isArrayAttrEqual = function (attr, value, subObj) {
    const attrValue = subObj.subscription[attr];
    if (attrValue.length !== value.length) {
        return false;
    }
    const result = value.map(NgxCommMsgHelper.normalizeId)
        .filter(function (item) {
            return attrValue.indexOf(item) !== -1;
        });

    return result.length === attrValue.length;
};

const isBoolAttrEqual = function (attr, value, subObj) {
    return subObj.subscription[attr] === value;
};

const doesArrayAttrContainId = function (attr, id, subObj) {
    const attrValue = subObj.subscription[attr];
    if (typeof attrValue === 'undefined') {
        return false;
    }
    if (Object.prototype.toString.call(attrValue) !== '[object Array]') {
        return false;
    }
    if (attrValue.length === 0) {
        return false;
    }

    return (attrValue.indexOf(NgxCommMsgHelper.normalizeId(id)) !== -1);
};

const generateDefaultSub = function (subscribeAllTriggers, screenIdList, legacyIdList, ngxSubscriptionConfig) {
    let screenTriggers = subscribeAllTriggers === true
        // use known triggers if subscribe all triggers is true
        ? clone(knownTriggerList)
        // otherwise use the screen id list
        : screenIdList.map(NgxCommMsgHelper.normalizeId);

    // Add to known trigger list anything that might be missing from the screen id list
    if (subscribeAllTriggers === true) {
        screenIdList.forEach(id => {
            if (screenTriggers.indexOf(id) === -1) {
                screenTriggers.push(id);
            }
        });
        // filter out any legacy ids so that they are not subscribed to
        screenTriggers = screenTriggers.filter(id => legacyIdList.indexOf(id) === -1);
    }

    return {
        cmd: 'subscription',
        ver: '1.0',
        subscription: {
            // filter out all legacy screen ids
            screenTrigger: screenTriggers.map(NgxCommMsgHelper.normalizeId),
            tokenUpdate: getTokenSubList(ngxSubscriptionConfig),
            gameEvent: gameEvents.map(NgxCommMsgHelper.normalizeId),
            vpcPlayerData: isSubscriptionEnabled('vpcPlayerData', ngxSubscriptionConfig),
            statusUpdate: isSubscriptionEnabled('statusUpdate', ngxSubscriptionConfig),
            levelUpdate: isSubscriptionEnabled('levelUpdate', ngxSubscriptionConfig),
            bonusHit: isSubscriptionEnabled('bonusHit', ngxSubscriptionConfig),
            gameStart: isSubscriptionEnabled('gameStart', ngxSubscriptionConfig),
            gameEnd: isSubscriptionEnabled('gameEnd', ngxSubscriptionConfig),
            patronData: isSubscriptionEnabled('patronData', ngxSubscriptionConfig),
            fullscreenState: isSubscriptionEnabled('fullscreenState', ngxSubscriptionConfig),
            windowState: isSubscriptionEnabled('windowState', ngxSubscriptionConfig),
            drawStatus: isSubscriptionEnabled('drawStatus', ngxSubscriptionConfig),
            ismUpdate: isSubscriptionEnabled('ismUpdate', ngxSubscriptionConfig),
            disableAutoHide: isSubscriptionEnabled('disableAutoHide', ngxSubscriptionConfig),
            languageUpdate: isSubscriptionEnabled('languageUpdate', ngxSubscriptionConfig),
            userTokenUpdate: generateUserTokenList(ngxSubscriptionConfig ),
            cardlessStatus: isSubscriptionEnabled('cardlessStatus', ngxSubscriptionConfig)
        }
    };
};

const isSubscriptionEnabled = function (sub, ngxSubscriptionConfig){
    var subEnabled = true;
    if(ngxSubscriptionConfig && ngxSubscriptionConfig.hasOwnProperty(sub)){
        subEnabled = ngxSubscriptionConfig[sub];
    }
    return subEnabled;
}

const getTokenSubList = function (ngxSubscriptionConfig) {
    const list = [];
    if(ngxSubscriptionConfig && ngxSubscriptionConfig.tokenUpdate && ngxSubscriptionConfig.tokenUpdate.enabled && ngxSubscriptionConfig.tokenUpdate.list){
        ngxSubscriptionConfig.tokenUpdate.list.map(function(item){
            if(item.enabled && item.id){
                list.push(NgxCommMsgHelper.normalizeId(item.id));
            }
        })
    }
    return list;
}

/**
 * NgxCommSubscriptionManager - manages the content interface subscription
 * @param screenIdList {Array} list of screenIds.
 * @param logger {Object} Logger service instance.
 * @param subscribeAllTriggers {Boolean} Subscribe to all screenTriggers flag.
 * @param legacyIdList {Array} list of legacyIds.
 * @constructor
 */
export default class NgxCommSubscriptionManager {
    constructor(screenIdList, logger, subscribeAllTriggers = false, legacyIdList = [], schemas = Schemas, ngxSubscriptionConfig) {
        if (!screenIdList) {
            throw 'screen id list is not optional';
        }

        this.schemas = schemas;
        this.logger = logger;
        this.screenIdList = screenIdList;
        this.subscribeAllTriggers = subscribeAllTriggers;
        this.legacyIdList = legacyIdList;
        this.ngxSubscriptionConfig = ngxSubscriptionConfig;

        this.currentSub = generateDefaultSub(this.subscribeAllTriggers, this.screenIdList, this.legacyIdList, this.ngxSubscriptionConfig);

        this.schemas.validate(this.currentSub, 'subscription').then(() => {}, (err) => {
            throw err;
        });
    };

    /**
     * Get the current subscription
     *
     * @return Object The current subscription.
     **/
    getSubscription() {
        return clone(this.currentSub);
    };

    /**
     * Reset the current subscription to its default values
     *
     * @return The current subscription after bein reset.
     **/
    resetSubscription() {
        this.currentSub = generateDefaultSub(this.subscribeAllTriggers, this.screenIdList, this.legacyIdList, this.ngxSubscriptionConfig);
    };

    /**
     * Tests if the current subscription attribute is equal to the value passed
     *
     * @param attr The attribute of the subscription to test against.
     * @param value The value used to test for.
     * @return boolean indicating if the attribute is equal or not.
     **/
    isAttrEqual(attr, value) {
        if (this.currentSub.subscription[attr] instanceof Array) {
            return isArrayAttrEqual(attr, value, this.currentSub);
        }
        return isBoolAttrEqual(attr, value, this.currentSub);
    };

    /**
     * Tests if the current subscription attribute contains the value passed. This is useful
     * for evaluating string array attributes like screenTrigger, tokenUpdate and gameEvent.
     *
     * @param attr The attribute of the subscription to test against.
     * @param id The id used to test for.
     * @return boolean indicating if the attribute contains the id or not.
     **/
    doesAttrContainId(attr, id) {
        if (this.currentSub.subscription[attr] instanceof Array) {
            return doesArrayAttrContainId(attr, NgxCommMsgHelper.normalizeId(id), this.currentSub);
        }
        return isBoolAttrEqual(attr, NgxCommMsgHelper.normalizeId(id), this.currentSub);
    };

    /**
     * Sets the current subscription attribute to the specified value, then runs
     * a schema validation of the result. If the schema validation fails an error
     * will be logged and the attribute specified will not be set.
     *
     * @param attribute The attribute of the subscription to set.
     * @param value The value of that subscription attribute.
     **/
    setAttribute(attribute, value) {
        const temp = clone(this.currentSub);
        temp.subscription[attribute] = value instanceof Array ?
            value.map(NgxCommMsgHelper.normalizeId)
            : value;
        this.schemas.validate(temp, 'subscription').then(() => {
            this.currentSub = temp;
        }, () => {
            this.logger.error(
                {
                    error: 'Setting subscription attribute resulted in a ' +
                    'invalid subscription according to schema. The ' +
                    'attribute value will not be set.',
                    data: {attribute: attribute, value: value}
                },
                true
            );
        });
    };

    /**
     * Add id to the the current subscription by attribute name, then runs
     * a schema validation of the result. If the schema validation fails an error
     * will be logged and the attribute specified will not be added.
     *
     * @param attribute The attribute of the subscription to set, this will be an attribute that is
     * an array of string ids. fe. screenTriger, gameEvent or tokenUpdate.
     * @param id The id value of that subscription attribute, a hex id as a string fe. '0x1A'
     **/
    addId(attribute, id) {
        return new Promise( (resolve, reject) => {
            if (Object.prototype.toString.call(this.currentSub.subscription[attribute]) !== '[object Array]') {
                return reject(new Error('Unable to add Id, attribute is not an array.'));
            }
            if (doesArrayAttrContainId(attribute, id, this.currentSub)) {
                this.logger.warn(
                    {
                        warn: 'Adding subscription id that already exists. The attibute value will ' +
                        'not be updated.',
                        data: {attribute: attribute, id: id}
                    },
                    true
                );
                return resolve();
            }
            const temp = clone(this.currentSub);
            temp.subscription[attribute].push(NgxCommMsgHelper.normalizeId(id));
            this.schemas.validate(temp, 'subscription').then(() => {
                this.currentSub = temp;
                resolve();
            }).catch(() => {
                reject( new Error('Adding subscription id resulted in a ' +
                        'invalid subscription according to schema. The ' +
                        'attibute value will not be updated.') );
            });
        });
    };
}
