/**
 * Util is a static class filled with helper functions.
 */
export default class Util {
    /**
     * Sends a message to the targeted window using the post message api.
     *
     * @param targetWindow The window object the message will be sent to.
     * @param message The message to be sent.
     **/
    static postMessage(targetWindow, message) {
        targetWindow.postMessage( message, '*');
    };

    static async asyncForEach(array, callback) {
        for (let index = 0; index < array.length; index++) {
            await callback(array[index], index, array)
        }
    };

}
