import NgxCommMsgHelper from './NgxCommMsgHelper';
import screenIds from 'igt-nexgen-codes/lib/screenIds';
'use strict';

/**
 * Param is a static help object that parses external varibles. It prefers
 * getting the values from a urlMap object but will fallback and pull from the
 * page url string. It does this to be backwards compatible with older content
 * sets.
 */
export default class Param {
    /**
     * Provides an object containing all available urlparameters from the page url.
     *
     * @param pageUrl The page url string to parse for the given name.
     * @return object with the query paramters contained in the page url string.
     **/

    static getUrlParamsObject(pageUrl) {
        const urlParamsObj = {};
        if (typeof pageUrl !== "undefined") {
            let match;
            const pl = /\+/g;
            const search = /([^&=]+)=?([^&]*)/g;
            const decode = function (s) {
                return decodeURIComponent(s.replace(pl, " "));
            };
            const query = pageUrl.substring(0);

            while (match = search.exec(query))
                urlParamsObj[decode(match[1])] = decode(match[2]);
        }
        return urlParamsObj;
    };

    /**
     * Provides the value from the page url given the values name.
     *
     * @param name The name of the page param to find.
     * @param pageUrl The page url string to parse for the given name.
     * @return string value of the given name or undefined if not found.
     * simulator.
     **/
    static urlParam(name, pageUrl) {
        const results = new RegExp('[\\?&]' + name + '=([^&#]*)').exec(pageUrl);
        if (!results) {
            return undefined;
        }
        const result = results[1];
        if (typeof result !== 'undefined') {
            return decodeURIComponent(result);
        }
    };

    /**
     * Provides the host url, used to connect to the content interface API or
     * simulator.
     *
     * @param urlMap The urlMap that may provide the host url.
     * @param pageUrl The page url string may also provide the host url.
     * @return string that defines the url of the content interface API or
     * simulator.
     **/
    static getHostUrl(urlMap, pageUrl) {
        // the host param overrides the urlMap
        const hostUrl = Param.urlParam('host', pageUrl);
        if (typeof hostUrl !== 'undefined') {
            return hostUrl;
        }

        const ref = urlMap !== undefined ? urlMap : {};
        const retValue = ref.ciHost;

        if (typeof retValue !== 'undefined') {
            return retValue;
        }

        // setup a default value
        const port = Param.urlParam('localWebSocketPort', pageUrl);
        return `wss://localhost:${(typeof port !== 'undefined') ? port : '7001'}`;
    };

    /**
     * Provides the logger url, used to connect to the m5 logging service.
     *
     * @param urlMap The urlMap that may provide the logger url.
     * @param pageUrl The page url string may also provide the logger url.
     * @return string that defines the url of the logging service or
     * undefined.
     **/
    static getLoggerUrl(urlMap, pageUrl) {
        const ref = urlMap !== undefined ? urlMap : {};
        let retValue = ref.loggerUrl;

        // prefer the url map
        if (typeof retValue !== 'undefined') {
            return retValue;
        }

        // backwards compatible
        retValue = Param.urlParam('loggerUrl', pageUrl);
        if (typeof retValue !== 'undefined') {
            return retValue;
        }
    };

    /**
     * Provides the log level, used to determine what log levels to report to
     * the m5 logger service.
     *
     * @param urlMap The urlMap that may provide the log level.
     * @param pageUrl The page url string may also provide the log level.
     * @return A string that defines the log level or undefined.
     **/
    static getLogLevel(urlMap, pageUrl) {
        const ref = urlMap !== undefined ? urlMap : {};
        let retValue = ref.logLevel;

        // prefer the url map
        if (typeof retValue !== 'undefined') {
            return retValue;
        }

        // backwards compatible
        retValue = Param.urlParam('logLevel', pageUrl);
        if (typeof retValue !== 'undefined') {
            return retValue;
        }
    };

    /**
     * Provides the device id used to identify this device when logging events.
     *
     * @param pageUrl The page url string provides the device id in different
     * ways depending on the version of the system.
     * @return string that defines the device id.
     **/
    static getDeviceId(pageUrl) {
        let retValue = Param.urlParam('machineNumber', pageUrl);
        // prefer machine number if possible, this is the content manager
        // setting the value
        if (typeof retValue !== 'undefined') {
            return retValue;
        }

        // backwards compatible, Param is the tuner service setting the value
        retValue = Param.urlParam('deviceId', pageUrl);
        if (typeof retValue !== 'undefined') {
            return retValue;
        }

        // setup a default value
        return 'UNASSIGNED';
    };

    static getFloorLoc(pageUrl) {
        let retValue = Param.urlParam('location', pageUrl);       
        if (typeof retValue !== 'undefined') {
            return retValue;
        }
        return 'UNKNOWN-LOCATION';
    }

    /**
     * Removes parameters from the page url.
     *
     * @param pageUrl The page url string.
     * @param params A list of parameters to be removed.
     * @return string page url string with unwanted params filtered out.
     **/
    static filterPageUrl(pageUrl, params = []) {
        return pageUrl
            .replace(/^.*\?(.*)$/, "$1")
            .split('&')
            .filter(pv => params.indexOf(pv.split('=')[0]) === -1)
            .join('&');
    };

    /**
     * Normalizes the urlMap. Replacing friendly names with ids and updating the urls where
     * necessary.
     *
     * @param {object} urlMap object to be normalized.
     **/
    static normalizeUrlMap(urlMap) {
        let screenId;
        const screenIdRX = /^0[xX][0-9a-fA-F]{2}$/;
        // clone the entire object so that no attributes are missing, then replace with normalized
        const result = JSON.parse(JSON.stringify(urlMap));
        result.screenIdMap = {};
        result.indexUrl = urlMap.baseUrl ? urlMap.baseUrl + urlMap.indexUrl
            : urlMap.indexUrl;
        result.bannerUrl = urlMap.baseUrl ? urlMap.baseUrl + urlMap.bannerUrl
            : urlMap.bannerUrl;
        result.pinContentUrl = urlMap.baseUrl ? urlMap.baseUrl + urlMap.pinContentUrl
            : urlMap.pinContentUrl;
        result.systemUrl = urlMap.baseUrl ? urlMap.baseUrl + urlMap.systemUrl
            : urlMap.systemUrl;
        const screenMap = urlMap.screenIdMap;
        const newScreenIdMap = {};
        Object.keys(screenMap).map(function (screenId) {
            const url = urlMap.baseUrl ? urlMap.baseUrl + screenMap[screenId]
                : screenMap[screenId];
            if (screenIdRX.test(screenId)) {
                newScreenIdMap[NgxCommMsgHelper.normalizeId(screenId)] = url;
            } else {
                const screenIdLookup = screenIds[screenId.toUpperCase()];
                if (!screenIdLookup) {
                    throw 'Unable to resolve friendly screen name "' + screenId +
                    '" to hex screen id.';
                }
                newScreenIdMap[screenIdLookup] = url;
            }
        });
        result.screenIdMap = newScreenIdMap;
        if (urlMap.legacyIdList) {
            result.legacyIdList = urlMap.legacyIdList.map(id => {
                if (screenIdRX.test(id)) {
                    return id;
                } else {
                    const lookup = screenIds[id.toUpperCase()];
                    if (!lookup) {
                        throw 'Unable to resolve friendly screen name "' + screenId +
                        '" to hex screen id.';
                    }
                    return lookup
                }
            });
        }
        return result;
    };
}
