import screenIds from 'igt-nexgen-codes/lib/screenIds';
'use strict';

/**
 * This module persists a list of screen triggers and tokens values. It will also maintain the order
 * in which these items are received. If a trigger or token id already exist the value and order
 * will be updated, meaning we are only storing the last occurrence (not a full history). The cache
 * is cleared on trigger idle, but can also be cleared manually at any time.
 */

export default class ContentCache {
    constructor() {
        this.TRIGGER_TYPE = 'trigger';
        this.TOKEN_TYPE = 'token';
        this.tokens = {};
        this.lastTokenId = null;
        this.triggers = {};
        this.lastTriggerId = null;
        this.ordinal = 0;
        //Ephemeral content state that is cleared after a carded session.
        this.contentState = {};
    }

    sortByOrdinal(a, b) {
        return a.ordinal - b.ordinal;
    };

    removeOrdinal(item) {
        delete item['ordinal'];
        return item;
    };

    addStateElement(id, value, expireTime) {
        return new Promise((resolve) => {
            let timeoutInstance;

            timeoutInstance = setTimeout(function () {
                delete this.contentState[id];
            }.bind(this), expireTime);

            value = {
                value: value,
                timeoutinstance: timeoutInstance
            };
            //if the state element previously existed, check and remove previous timeout if it exists
            if (typeof this.contentState[id] !== 'undefined' && typeof this.contentState[id].timeoutinstance !== 'undefined') {
                clearTimeout(this.contentState[id].timeoutinstance);
            }

            this.contentState[id] = value;
            return resolve();
        });
    };

    removeStateElement(id) {
        return new Promise((resolve) => {
            //if the state element previously existed, check and remove previous timeout if it exists
            if (typeof this.contentState[id] !== 'undefined' && typeof this.contentState[id].timeoutinstance !== 'undefined') {
                clearTimeout(this.contentState[id].timeoutinstance);
            }

            delete this.contentState[id];
            return resolve();
        });
    };

    add(type, id, value, messageTokens) {
        // clear content cache on idle screen trigger
        if (type === this.TRIGGER_TYPE && id === screenIds['IDLE_SCREEN']) {
            this.clear();
        }

        if (type === this.TRIGGER_TYPE) {
            this.triggers[id] = {value: value, ordinal: this.ordinal++, messageTokens: messageTokens};
            this.lastTriggerId = id;
        }

        if (type === this.TOKEN_TYPE) {
            this.tokens[id] = {value: value, ordinal: this.ordinal++};
            this.lastTokenId = id;
        }
    }

    /**
     * Stores a token in the content cache.
     * @param id The id of the token.
     * @param value The value of the token.
     */
    storeToken(id, value) {
        this.add(this.TOKEN_TYPE, id, value);
    }

    /**
     * Retrieve a list of tokens (id and value). The list is ordered. The first item in the
     * list occurred first, the last item is the most recent item.
     * @return Array ordered Array of tokens
     */
    getTokens() {
        const ids = Object.keys(this.tokens);
        const list = [];
        ids.forEach(id => {
            list.push({
                id: id,
                value: this.tokens[id].value,
                ordinal: this.tokens[id].ordinal
            });
        }, this);
        return list.sort(this.sortByOrdinal).map(this.removeOrdinal);
    }

    /**
     * Retrieve the most recent token (id and value).
     * @return Object An object with id and value representing the most recent token added to the
     * content cache. Undefined if no tokens have been added or recently cleared.
     */
    getLastToken() {
        if (this.lastTokenId === null) {
            return undefined;
        }
        return {
            id: this.lastTokenId,
            value: this.tokens[this.lastTokenId].value,
        };
    }

    /**
     * Stores a trigger in the content cache.
     * @param id The id of the trigger.
     * @param value The value of the trigger.
     * @param msgTokens The SPIN specific message token
     */
    storeTrigger(id, value, msgTokens = {}) {
        this.add(this.TRIGGER_TYPE, id, value, msgTokens);
    }

    /**
     * Retrieve a list of triggers (id and value). The list is ordered. The first item in the
     * list occurred first, the last item is the most recent item.
     * @return Array an ordered Array of triggers
     */
    getTriggers() {
        const ids = Object.keys(this.triggers);
        const list = [];
        ids.forEach(id => {
            list.push({
                id: id,
                value: this.triggers[id].value,
                ordinal: this.triggers[id].ordinal,
                messageTokens: this.triggers[id].messageTokens
            });
        }, this);
        return list.sort(this.sortByOrdinal).map(this.removeOrdinal);
    }

    /**
     * Retrieve the most recent trigger (id and value).
     * @return Object An object with id and value representing the most recent trigger added to the
     * content cache. Undefined if no triggers exist or recently cleared.
     */
    getLastTrigger() {
        if (this.lastTriggerId === null) {
            return undefined;
        }
        return {
            id: this.lastTriggerId,
            value: this.triggers[this.lastTriggerId].value,
            messageTokens: this.triggers[this.lastTriggerId].messageTokens,
        };
    }

    /**
     * Retrieve a combined list of tokens and triggers (id, value and type). The list is
     * ordered. The first item in the list occurred first, the last item is the most recent item.
     * @return Array an ordered Array of tokens and triggers
     */
    getAll() {
        let list = [];
        const triggerids = Object.keys(this.triggers);
        const tokenids = Object.keys(this.tokens);
        triggerids.forEach(id => {
            list.push({
                type: this.TRIGGER_TYPE,
                id: id,
                value: this.triggers[id].value,
                ordinal: this.triggers[id].ordinal,
                messageTokens: this.triggers[id].messageTokens
            });
        }, this);
        tokenids.forEach(id => {
            list.push({
                type: this.TOKEN_TYPE,
                id: id,
                value: this.tokens[id].value,
                ordinal: this.tokens[id].ordinal
            });
        }, this);
        return list.sort(this.sortByOrdinal).map(this.removeOrdinal);
    }

    /**
     * Clears the content cache, after a clear the get functions will return an empty array.
     */
    clear() {
        this.tokens = {};
        this.triggers = {};
        this.lastTokenId = null;
        this.lastTriggerId = null;
        this.ordinal = 0;
    }

    /**
     * Stores a state element in the contentState.
     * @param id The id of the element.
     * @param value The value of the element.
     * @param expireTime (optional) The time in milliseconds till the element expires, 0 = 24 hours
     * @return Promise contentState after a promise
     */
    storeStateElement(id, value, expireTime) {
        return new Promise((resolve) => {
            expireTime = expireTime || 0;
            if (expireTime === 0)
                expireTime = 86400000;//set timeout to 24 hours
            this.addStateElement(id, value, expireTime).then(() => {
                return resolve(this.contentState);
            });
        });
    }

    /**
     * Retrieve an element from the contentState
     * @param id The id of the element.
     * if undefined, will return undefined
     * @return the value of the element by id
     */
    getStateElement(id) {
        if (typeof this.contentState[id] === 'undefined') {
            return undefined;
        }
        return this.contentState[id].value;
    }

    /**
     * Retrieve the contentState object
     * @return Object contentState
     */
    getContentState() {
        return this.contentState;
    }

    /**
     * Remove an element from the contentState
     * @param id The id of the element
     * @return Promise contentState after a promise
     */
    deleteStateElement(id) {
        return new Promise((resolve) => {
            this.removeStateElement(id).then(() => {
                return resolve(this.contentState);
            });
        });
    }

    /**
     * Clear the contentState
     * @return Object empty ContentState
     */
    clearContentState() {
        this.contentState = {};
        return this.contentState;
    }
}

