'use strict';

/**
 * JSON File parser used to fetch and send
 */
export default class JSONFileParser {
    /**
     * promise function that attempts to get json from a specific file location
     * @param MapUrl {string} string file location
     * @returns Promise
     */
    static getJSONFile (MapUrl) {
        return new Promise (function (resolve, reject) {
            fetch(MapUrl).then(function(response) {
                if(!response.error) {
                    resolve (response.json());
                }
                throw new Error(response.error);
            }).catch(function(error) {
                reject(error);
            });
        });
    };
}
