import NgxCommMsgHelper from '../NgxCommMsgHelper';
import PubSub from '../PubSub';

'use strict';

const createMsg = (stateBool) => {
    const state = stateBool || false;
    return {
        cmd: "showFullscreen",
        ver: "1.0",
        showFullscreen: state
    };
};

const LOCKED = "locked";
const UNLOCKED = "unlocked";
const PENDING_UNLOCK = "pendingUnlock";
const PENDING_LOCK = "pendingLock";

export default class FullScreenToggle {
    /**
     * FullScreenToggle handles and allows for changes between full screen and normal screen modes.
     * You can directly call its exposed functions for toggle, show or hide. You can also use amplify
     * to publish a toggle-full-screen, show-full-screen or hide-full-screen topic.
     * @constructor FullScreenToggle
     */
    constructor(ngxComm, shellCache, logger, urlParams) {
        var self = this;
        this.gameLockStatus = UNLOCKED;
        this.userPreferredFullScreenState = false;
        this.watchProperty("gameLockStatus",  function () {
            this.syncFullScreenState();
        })
        this.watchProperty("userPreferredFullScreenState", function (value) {
            if (value) {
                this.sendGameLockMsg(this);
            } else {
                this.sendGameUnlockMsg(this);
            }
            this.syncFullScreenState();
        })
        Object.defineProperty(this, "shouldShowFullscreen", {
            get() {
                return this.userPreferredFullScreenState && this.gameLockStatus == LOCKED;
            }
        })

        this.checkGameLockInterval = setInterval(function() {
            // self.logger.debug({debug: "should check gameLockStatus: "+ (self.userPreferredFullScreenState && !self.gameLockStatus)});
            if(self.userPreferredFullScreenState && self.gameLockStatus!=LOCKED || !self.userPreferredFullScreenState && self.gameLockStatus != UNLOCKED){
                self.sendLockToBE2(true, 0, self, true);
            }
        }, 2000);
        
        this.ngxComm = ngxComm;
        this.logger = logger;
        this.urlParams = urlParams;
        this.shellCache = shellCache;
        
        //Full Screen event handlers.
        this.TOGGLE_FULL_SCREEN_TOPIC = 'toggle-full-screen';
        this.SHOW_FULL_SCREEN_TOPIC = 'show-full-screen';
        this.HIDE_FULL_SCREEN_TOPIC = 'hide-full-screen';
        
        this.gameLockTimeout = -1;
        this.defaultGameLockDurationInterval = 300;
        this.defaultGameLockRequestInterval = 60;
        this.defaultGameLockProcessingTime = 3;
        
        //Set the default state of full screen
        this.fullscreenState = false;
        this.watchProperty("fullscreenState", function(){
            self.syncFullScreenState();
        })
        this.showBanner = false;
        this.shellCache.storeShellStateElement('fullscreenState', this.fullscreenState, 0);
        
        // subcribe to topics and assocate callbacks
        this.toggle = this.toggleFullScreen.bind(this);
        this.show = this.showFullScreen.bind(this);
        this.hide = this.hideFullScreen.bind(this);
        PubSub.subscribe(this.TOGGLE_FULL_SCREEN_TOPIC, this.toggle);
        PubSub.subscribe(this.SHOW_FULL_SCREEN_TOPIC, this.show);
        PubSub.subscribe(this.HIDE_FULL_SCREEN_TOPIC, this.hide);
        
        // Register for full screen state changes from the host.
        // Forward the command to the outer shell so that the outer shell can change
        // the iframe width/height but also set the full screen state here so that
        // we can toggle.
        ngxComm.registerFullScreenState(this.registerFullScreenStateCB.bind(this));
        self.sendLockToBE2(true, 0, self, true);
    };
    
    registerFullScreenStateCB(data) {
        let sendData;
        if (data && data instanceof CustomEvent) {
            sendData = data.detail;
        } else {
            sendData = data;
        }

        this.fullscreenState = sendData['fullscreenState'];
        //cache fullscreenState
        this.shellCache.storeShellStateElement('fullscreenState', this.fullscreenState, 0).then(() => {
            this.logger.debug(`fullscreenState: ${this.fullscreenState}`);
        });

        // adjust service window fullscreen state
        this.setSWFullScreenState(this.fullscreenState, this.urlParams, this.showBanner);

        this.logger.debug({
            debug: 'fullscreenState has been changed to ' + this.fullscreenState,
            data: sendData
        });

        window.postMessage(sendData, '*');
    }

    /**
     * create game lock message to be sent to be2 with timeout
     * @memberof FullScreenToggle
     * @method sendGameLockMsg
     * @param fullScreenToggleInstance The instance of the fullScreenToggle Object.
     * @param duration The time duration to lock the game for.
     */
    sendGameLockMsg(fullScreenToggleInstance, duration) {
        let request = fullScreenToggleInstance.defaultGameLockRequestInterval;
        const processTime = fullScreenToggleInstance.defaultGameLockProcessingTime;

        //If no timeLock specified, use the default game lock duration.
        if (!duration) {
            duration = fullScreenToggleInstance.defaultGameLockDurationInterval;
        }

        //if we're toggling fullscreen, we need to re-enable timelock.
        if (this.gameLockTimeout !== -1) {
            window.clearTimeout(fullScreenToggleInstance.gameLockTimeout);
        }

        //if request interval is greater than the lock duration, set it to the duration less the processing time.
        if (request > (duration - processTime)) {
            request = (duration - processTime);
        }

        fullScreenToggleInstance.gameLockTimeout = window.setTimeout(() => {
            this.sendGameLockMsg(fullScreenToggleInstance, duration)
        }, (request * 1000));

        fullScreenToggleInstance.sendLockToBE2(true, duration, fullScreenToggleInstance);
    };

    /**
     * create game unlock message to be sent to be2 with timeout
     * @memberof FullScreenToggle
     * @method sendGameLockMsg
     * @param fullScreenToggleInstance The instance of the fullScreenToggle Object.
     * @param timeLock Seconds to issue a timeout and re-call, as well as the time to lock the game for.
     */
    sendGameUnlockMsg(fullScreenToggleInstance, timeLock) {
        if (fullScreenToggleInstance.gameLockTimeout !== -1) {
            //clear existing timeout if we toggle off fullscreen
            window.clearTimeout(fullScreenToggleInstance.gameLockTimeout);
        }

        if (!timeLock) {
            timeLock = fullScreenToggleInstance.defaultGameLockDurationInterval;
        }
        fullScreenToggleInstance.sendLockToBE2(false, timeLock, fullScreenToggleInstance);
    };

    isBe2Error(err) {
        if (err) {
            this.logger.error({
                error: 'fullscreenToggle: error while trying to setGameLock status.',
                data: err
            }, true);
            return true;
        }
        return false;
    };

    /**
     * create BE2basicFunctionArr and send to BE2
     * @memberof FullScreenToggle
     * @method sendGameLockMsg
     * @param enable either locks or unlocks the game
     * @param timeLock Seconds lock the game if we are doing a gamelock
     * @param fullScreenToggleInstance The instance of the fullScreenToggle Object.
     */

    sendLockToBE2(enable, timeLock, fullScreenToggleInstance, checkOnly = false) {
        //build be2 function first
        const lockval = (enable === true) ? 0 : 1;
        const be2FunctArray = checkOnly ?
        [[NgxCommMsgHelper.enumBE2BasicFunc.ContentLockGame, [2, timeLock, 0, 0]]] :   
        [
            [NgxCommMsgHelper.enumBE2BasicFunc.ContentLockGame, [lockval, timeLock, 0, 0]],
            [NgxCommMsgHelper.enumBE2BasicFunc.ContentLockGame, [2, timeLock, 0, 0]] // 2=LOCK STATUS
        ];
        const be2FuncReq = NgxCommMsgHelper.createBE2BasicFunctionArr(be2FunctArray);
        const enumBE2Func = NgxCommMsgHelper.enumBE2BasicFunc;
        fullScreenToggleInstance.ngxComm.sendBe2Message(be2FuncReq, (err, data) => {
            if (this.isBe2Error(err)) {
                return;
            }
            const be2BasicResp = data[NgxCommMsgHelper.BE2_BASIC_RESPONSE];

            be2BasicResp.forEach(element => {
                if (element.functionCode === enumBE2Func.ContentLockGame) {
                    const isStatus = (element.retvals[0] === 2);
                    if (isStatus) {
                        if (element.retvals[2] === 2) {
                            this.gameLockStatus = LOCKED;
                        }
                        else if(element.retvals[2] === 3){
                            this.gameLockStatus = UNLOCKED;
                        }else{
                            this.gameLockStatus = enable ? PENDING_LOCK : PENDING_UNLOCK;
                        }
                        fullScreenToggleInstance.logger.debug({
                            debug: 'BE2 gameLockStatus: ' + this.gameLockStatus,
                            data: element
                        });
                    }else{
                        const enableType = (element.retvals[0] === 0);
                        fullScreenToggleInstance.logger.debug({
                            debug: 'BE2 setGameLock: ' + enableType,
                            data: element
                        });
                    }
                } else {
                    logger.warn({ warn: 'Unknown BE2Response!', data: '' });
                }
            });
        });
    };

    

    /**
     * set service window to fullscreenState with hidden/visible banner
     * @memberof FullScreenToggle
     * @method setSWFullScreenState
     * @param fullscreenState The state of fullscreen (true/false)
     * @param urlParams The url parameters
     * @param showBanner Whether to show the banner or not on fullscreen.
     */

    setSWFullScreenState(fullscreenState, urlParams, showBanner = false) {
        // TODO: needs refactoring 
        const banner = document.getElementById("BannerFrame");
        const wrapperFrame = document.getElementById("WrapperFrame");
        const wrapperFrameOverlay = document.getElementById("WrapperFrameOverLay");
        const pinContentFrame = document.getElementById("PinContentFrame");
        const shellBackgroundFrame = document.getElementById("ShellBackgroundFrame");
        const hasParams = (typeof urlParams.bannerPosition !== 'undefined' && typeof urlParams.bannerHeight !== 'undefined');
        const isTFT = (urlParams.contentWidth === "640px" && urlParams.contentHeight === "240px");

        // If TFT resolution OR banner URL Params not present AND banner present.
        if ((fullscreenState && hasParams && !isTFT)) {
            let heightMinusBanner;

            //Default to top-left, set display width.
            wrapperFrame.style.top = '0px';
            wrapperFrame.style.left = '0px';
            wrapperFrame.style.width = urlParams.displayWidth + "px";
            wrapperFrameOverlay.style.top = '0px';
            wrapperFrameOverlay.style.left = '0px';
            wrapperFrameOverlay.style.width = urlParams.displayWidth + "px";
            pinContentFrame.style.top = '0px';
            pinContentFrame.style.left = '0px';
            pinContentFrame.style.width = urlParams.displayWidth + "px";
            shellBackgroundFrame.style.top = '0px';
            shellBackgroundFrame.style.left = '0px';
            shellBackgroundFrame.style.width = urlParams.displayWidth + "px";

            if (!showBanner) {
                heightMinusBanner = urlParams.displayHeight + "px";
                banner.style.visibility = "hidden";
            }
            else {
                banner.style.visibility = 'visible';
                heightMinusBanner = parseInt(urlParams.displayHeight) - parseInt(urlParams.bannerHeight) + "px";
                //Set the positioning to top for bannerHeight.
                if (urlParams.bannerPosition === 'top') {
                    wrapperFrame.style.top = urlParams.bannerHeight + "px";
                    wrapperFrameOverlay.style.top = urlParams.bannerHeight + "px";
                    pinContentFrame.style.top = urlParams.bannerHeight + "px";
                    shellBackgroundFrame.style.top = urlParams.bannerHeight + "px";
                }
            }

            wrapperFrame.style.height = heightMinusBanner;
            wrapperFrameOverlay.style.height = heightMinusBanner;
            pinContentFrame.style.height = heightMinusBanner;
            shellBackgroundFrame.style.height = heightMinusBanner;
        }
    };

    /**
     * set service window to fullscreenState with hidden/visible banner
     * @memberof FullScreenToggle
     * @method syncFullScreenState
     * @instance
     */
    syncFullScreenState() {
        var needsSync = [LOCKED, UNLOCKED].includes(this.gameLockStatus) && this.shouldShowFullscreen == this.userPreferredFullScreenState && this.userPreferredFullScreenState != this.fullscreenState; 
        if (needsSync) {
            const msg = createMsg(this.shouldShowFullscreen);
            this.ngxComm.sendMessage(msg);
        }
    }

    /**
     * Toggle Full Screen. Call this when you want to go from screen mode current to the opposite.
     * @memberof FullScreenToggle
     * @method toggleFullScreen
     * @instance
     */
    toggleFullScreen(data) {
        this.showBanner = data.detail.showBanner;
        this.userPreferredFullScreenState = !this.userPreferredFullScreenState
    };

    /**
     * Show Full Screen. Call this when you want to go from normal screen mode to full screen mode.
     * @memberof FullScreenToggle
     * @method showFullScreen
     * @instance
     */
    showFullScreen(data) {
        this.showBanner = data.detail.showBanner;
        this.userPreferredFullScreenState = true;
    };


    /**
     * Hide Full Screen. Call this when you want to go from normal screen mode to full screen mode.
     * @memberof FullScreenToggle
     * @method hideFullScreen
     * @instance
     */
    hideFullScreen() {
        this.userPreferredFullScreenState = false;
    };

    /**
     * Cleanup subscriptions. Typically should be called in Unit tests before
     * FullScreenToggle instance is destroyed.
     * @memberof FullScreenToggle
     * @method cleanup
     * @instance
     */
    cleanup() {
        PubSub.unsubscribe(this.TOGGLE_FULL_SCREEN_TOPIC, this.toggle);
        PubSub.unsubscribe(this.SHOW_FULL_SCREEN_TOPIC, this.show);
        PubSub.unsubscribe(this.HIDE_FULL_SCREEN_TOPIC, this.hide);
        this.unwatchAllProperties();
        this.ngxComm && this.ngxComm.unwatchProperty("gamePlayActive");
        if (this.gameLockTimeout !== -1) {
            window.clearTimeout(this.gameLockTimeout);
            this.gameLockTimeout = -1;
        }
        if(this.checkGameLockInterval){
            clearInterval(this.checkGameLockInterval);
            delete this.checkGameLockInterval;
        }
    };
}
