// https://github.com/uxitten/polyfill/blob/master/string.polyfill.js
// https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/String/repeat
if (!String.prototype.padEnd) {
    String.prototype.padEnd = function padEnd(targetLength,padString) {
        targetLength = targetLength>>0; //floor if number or convert non-number to 0;
        padString = String(padString || ' ');
        if (this.length > targetLength) {
            return String(this);
        }
        else {
            targetLength = targetLength-this.length;
            if (targetLength > padString.length) {
                padString += padString.repeat(targetLength/padString.length); //append to original to ensure we are longer than needed
            }
            return String(this) + padString.slice(0,targetLength);
        }
    };
}

// https://github.com/uxitten/polyfill/blob/master/string.polyfill.js
// https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/String/padStart
if (!String.prototype.padStart) {
    String.prototype.padStart = function padStart(targetLength,padString) {
        targetLength = targetLength>>0; //floor if number or convert non-number to 0;
        padString = String(padString || ' ');
        if (this.length > targetLength) {
            return String(this);
        }
        else {
            targetLength = targetLength-this.length;
            if (targetLength > padString.length) {
                padString += padString.repeat(targetLength/padString.length); //append to original to ensure we are longer than needed
            }
            return padString.slice(0,targetLength) + String(this);
        }
    };
}

// https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Object/assign
if (typeof Object.assign !== 'function') {
  // Must be writable: true, enumerable: false, configurable: true
  Object.defineProperty(Object, "assign", {
    value: function assign(target, varArgs) { // .length of function is 2
      'use strict';
      if (target == null) { // TypeError if undefined or null
        throw new TypeError('Cannot convert undefined or null to object');
      }

      const to = Object(target);

      for (let index = 1; index < arguments.length; index++) {
        const nextSource = arguments[index];

        if (nextSource != null) { // Skip over if undefined or null
          for (const nextKey in nextSource) {
            // Avoid bugs when hasOwnProperty is shadowed
            if (Object.prototype.hasOwnProperty.call(nextSource, nextKey)) {
              to[nextKey] = nextSource[nextKey];
            }
          }
        }
      }
      return to;
    },
    writable: true,
    configurable: true
  });
}

if (!Object.prototype.watchProperty) {
    Object.defineProperty(Object.prototype, "unwatchProperty", {
		enumerable: false,
        configurable: true,
        value: function (prop) {
            if(!("__watchers__" in this)) return;
            if (this.__watchers__.hasOwnProperty(prop)) {
                delete this.__watchers__[prop];
                var val = this[prop];
                delete this[prop]; // remove accessors      
                this[prop] = val;
			}
			if(Object.keys(this.__watchers__).length == 0){
				delete this.__watchers__;
			}
        }
    });
    Object.defineProperty(Object.prototype, "unwatchAllProperties", {
		enumerable: false,
        configurable: true,
        value: function () {
            var self = this;
            this.__watchers__ && Object.keys(this.__watchers__).forEach(function (item) {
                self.unwatchProperty(item)
            })
        }
    });
    Object.defineProperty(Object.prototype, "watchProperty", {
        enumerable: false,
        configurable: true,
        value: function (prop, didChange, option = {beforeChange: undefined, immediate: false}) {
            let beforeChange = option && option.beforeChange;
            let immediate = option && option.immediate;
            
            let self = this;
            self.__watchers__ = self.__watchers__ || {};
            let _watcher = {didChange, beforeChange};
            var unwatches = [];
            if (Array.isArray(prop)) {
                prop.forEach(function (item) {
                    var oldValues = []
                    unwatches.push(self.watchProperty(item, function () {
                        var newValues = prop.map(function (key) { return self[key] })
                        didChange.call(this, newValues, oldValues, prop)
                    }, {beforeChange: function () {
                        oldValues = prop.map(function (key) { return self[key] })
                    }, immediate}));
                })
                return function(){
                    unwatches.forEach(function (item) {
                        typeof item == "function" && item();
                    })
                }
            }
            self.__watchers__[prop] = self.__watchers__[prop] || [];
            self.__watchers__[prop].push(_watcher)
            var currentValue = self[prop],
                getter = function () {
                    return currentValue;
                },
                setter = function (value) {
                    if(currentValue == value ) return value;
                    let oldValue = currentValue;
                    self.__watchers__[prop].forEach(function(watcher){
                        let beforeChange = watcher.beforeChange;
                        beforeChange && beforeChange.call(self, value, oldValue, prop);
                    })
                    currentValue = value;
                    self.__watchers__[prop].forEach(function(watcher){
                        let didChange = watcher.didChange;
                        didChange && didChange.call(self, value, oldValue, prop);
                    })
                    return value
                };
            if (delete self[prop]) { // can't watch constants        
                Object.defineProperty(self, prop, {
                    get: getter,
                    set: setter,
                    enumerable: true,
                    configurable: true
                });
            }
            if(immediate){
                beforeChange && beforeChange.call(self, currentValue, currentValue, prop);
                didChange && didChange.call(self, currentValue, currentValue, prop);
            }
            function unwatch(){
                if("__watchers__" in self && prop in self.__watchers__){
                    let idx = self.__watchers__[prop].indexOf(_watcher);
                    if(idx != -1){
                        self.__watchers__[prop].splice(idx, 1);
                    }
                    if(self.__watchers__[prop].length == 0){
                        var val = self[prop];
                        delete self[prop];// remove accessors      
                        delete self.__watchers__[prop]; 
                        self[prop] = val;
                    }
                    if(Object.keys(self.__watchers__).length == 0){
                        delete self.__watchers__;
                    }

                }
            }
            return unwatch;
        }
    });
}