import NgxCommMsgHelper from '../NgxCommMsgHelper';
import PubSub from '../PubSub';
import tokenIds from 'igt-nexgen-codes/lib/tokenIds';

'use strict';

// Constants
const VALIDATE_PIN_SCREEN_TRIGGER = '0x83';
// card ID token
const CARD_ID_TOKEN_UPDATE = '0x45';
// Subscribed to by PIN service to initiate validate PIN flow
const VALIDATE_PIN_TOPIC = 'validatePIN';
const CREATE_PIN_SESSION_TOPIC = 'createPinSession';
// PIN service publishes on this to respond to content with validate PIN response
const VALIDATE_PIN_RESP_TOPIC = 'validatePINResp';
const CREATE_PIN_SESSION_RESP_TOPIC = 'createPinSessionResp';
// PIN service publishes on this to show PIN Entry screen content
const SHOW_PIN_ENTRY_SCREEN_TOPIC = 'showPINEntryScreen';
// PIN service publishes on this to hide PIN Entry screen content
const HIDE_PIN_ENTRY_SCREEN_TOPIC = 'hidePINEntryScreen';
// Subscribed to by PIN service to hide PIN Entry screen frame
const PIN_ENTRY_COMPLETE_TOPIC = 'pinEntryComplete';
// Subscribed to by PIN service to get PIN entry response
const PIN_ENTRY_RESPONSE_TOPIC = 'pinEntryResp';
// PIN Screen Trigger
const PIN_ST = '0x83';

const enumPINStatus = {
    VALID: 1,
    INVALID: 2,
    LOCKED: 3,
    NO_RESPONSE: 4,
    NOT_ENTERED: 5
};

const enumBE2Func = NgxCommMsgHelper.enumBE2BasicFunc;

const createValidatePINResp = (cmd, status) => {
    return {
        cmd: cmd,
        ver: '1.0',
        status: status
    };
};

/**
 * PINService contains PIN related work-flows between shell services, apps, content, and nexgen.
 * Other services can also perform PIN validation using appropriate amplify request.
 * @param ngxComm {object} Comm Interface service instance
 * @param logger {object} Logger service instance
 * @param sessionService {object} Session service instance
 * @constructor
 */
export default class PINService {
    constructor(ngxComm, sessionService, logger, appStore, screenTriggerIds) {
        this.ngxComm = ngxComm;
        this.sessionService = sessionService;
        this.logger = logger;
        this.appStore = appStore;

        // DEV NOTES:
        // 1. Since Pin entry content is modal, assuming that only one pinValidation can be triggered
        // at a time, until the whole workflow completes.
        // 2. Assuming PIN Validation request from content, screen trigger, and other shell services is
        // mutually exclusive.
        // i.e. Only one of those initiators will send a request at any given point in time. The user
        // activity workflows ensure this.



        // Private vars

        this.attempt = 0;
        this.pinStatusCheckIntervalID = 0;
        // NOTE: This could be moved to a new Shell specific section in urlMap.json, to allow app devs
        // to specify screen triggers on which to close the PIN entry content.
        const cancelValidatePINScreenTriggers = ['0x47' /*Idle Screen*/];

        this.sessionBased = false;

        // Properties
        // Keeping track of subscribers for clean up. This is needed when each unit test creates a new
        // instance of PINService
        this.subscribers = [];

        this.overridePinInterval = 0;
        this.overridePinRetryInterval = 5000;
        this.overridePinInterval = 300000; // 5 minutes
        this.overridePin = screenTriggerIds.includes(PIN_ST) ? 1 : 0;
        this.overridePinArray = NgxCommMsgHelper.createBE2BasicFunctionArr([
            [enumBE2Func.PIN_OverridePINScreen, [this.overridePin, 0, 0, 0]]
        ]);

        if (this.overridePin) {
            ngxComm.registerScreenTrigger(VALIDATE_PIN_SCREEN_TRIGGER, this.beginValidatePIN.bind(this));
        }
        ngxComm.registerTokenUpdate(CARD_ID_TOKEN_UPDATE, this.cancelPINEntryOnCardOut.bind(this));

        // if urlMap contains sub for PIN screen then subscribe, otherwise delegate to BE2 for PIN flows
        this.ngxComm.sendBe2Message(this.overridePinArray, this.processOverridePinResponse.bind(this));

        // fire and forget 15 minutes second interval for sending PIN_OverridePINScreen
        this.overridePinIntervalId = setInterval(() => {
            this.ngxComm.sendBe2Message(this.overridePinArray, this.doNothing.bind(this));
            this.logger.debug({
                debug: 'PINService: Periodic PIN_OverridePINScreen(' + this.overridePin + ') sent. Interval: ' + this.overridePinInterval,
                data: null
            });
        }, this.overridePinInterval);

        this.subscribe = (type, callback) => {
            this.subscribers.push({type: type, cb: callback});
            PubSub.subscribe(type, callback);
        };

        // Subscribe to receive relevant messages
        this.subscribe(VALIDATE_PIN_TOPIC, e => {
            this.beginValidatePIN(e);
        });

        this.subscribe(CREATE_PIN_SESSION_TOPIC, e => {
            this.sessionBased = true;
            this.beginValidatePIN(e)
        });

        //Subscribe to pin complete event
        this.subscribe(PIN_ENTRY_COMPLETE_TOPIC, this.hidePINContent.bind(this));

        // Subscribe to pin entry response
        const cb = this.validatePIN().bind(this);
        this.subscribe(PIN_ENTRY_RESPONSE_TOPIC, cb);

        // Cancel Validate PIN flow for desired Screen Triggers. e.g. Idle Screen
        for (let i = 0; i < cancelValidatePINScreenTriggers.length; i++) {
            ngxComm.registerScreenTrigger(cancelValidatePINScreenTriggers[i], this.cancelValidatePIN.bind(this));
        }
    }

    // Get PIN Status retry logic vars
    get GET_PIN_STATUS_MAX_ATTEMPTS() {
        return 15;
    }

    get GET_PIN_STATUS_INTERVAL() {
        return 1000;
    }

    // Private Functions
    processOverridePinResponse(err, data) {
        if (err) {
            setTimeout(() => {
                this.logger.error(
                    {
                        error: 'PINService: Received an error in processOverridePinResponse. Retrying PIN Override with ' + this.overridePin,
                        data: err
                    },
                    true
                );
                this.ngxComm.sendBe2Message(this.overridePinArray, this.processOverridePinResponse.bind(this));
            }, this.overridePinRetryInterval);
        } else {
            this.logger.debug({
                debug: 'PINService: Successfully sent PIN_OverridePINScreen(' + this.overridePin + ').',
                data: data
            });
        }
    }

    publishResponse(status) {

        // if pin session is not valid delete the current pin session if it exists.
        if (status !== enumPINStatus.VALID) {
            this.sessionService.deletePINSession();
        }

        if (this.sessionBased) {
            const response = createValidatePINResp(CREATE_PIN_SESSION_RESP_TOPIC, status);
            if (status === enumPINStatus.VALID) {
                const cardId = this.ngxComm.getCurrentTokenValue(tokenIds.PLAYERS_CARD_NUMBER);
                const preferredName = this.ngxComm.getCurrentTokenValue(tokenIds.PLAYERS_PREFERRED_NAME);
                // if valid create a new pin session, if there is an existing PIN session it will
                // immediately resolve with that pin session.
                this.sessionService.createPINSession(`${cardId}_${preferredName}`).then(token => {
                    response.token = token;
                    PubSub.publish(
                        CREATE_PIN_SESSION_RESP_TOPIC,
                        response
                    );
                    PubSub.publish(
                        VALIDATE_PIN_RESP_TOPIC,
                        createValidatePINResp(VALIDATE_PIN_RESP_TOPIC, status)
                    );
                }).catch(e => {
                    this.logger.error({
                        error: 'Received an error while trying to create a PIN session',
                        data: e
                    });
                    PubSub.publish(
                        CREATE_PIN_SESSION_RESP_TOPIC,
                        response
                    );
                    PubSub.publish(
                        VALIDATE_PIN_RESP_TOPIC,
                        createValidatePINResp(VALIDATE_PIN_RESP_TOPIC, status)
                    );
                });
            } else {
                PubSub.publish(
                    CREATE_PIN_SESSION_RESP_TOPIC,
                    response
                );
                PubSub.publish(
                    VALIDATE_PIN_RESP_TOPIC,
                    createValidatePINResp(VALIDATE_PIN_RESP_TOPIC, status)
                );
            }
            this.sessionBased = false;
        } else {
            PubSub.publish(
                VALIDATE_PIN_RESP_TOPIC,
                createValidatePINResp(VALIDATE_PIN_RESP_TOPIC, status)
            );
        }

    }

    processBeginValidatePINResp(PIN, forcePin) {
        return (err, data) => {
            if (err) {
                this.logger.error(
                    {
                        error: 'PINService: Received an error in processBeginValidatePINResp.',
                        data: err
                    },
                    true
                );
                return;
            }

            forcePin = forcePin || false;
            const be2BasicResp = data[NgxCommMsgHelper.BE2_BASIC_RESPONSE];
            // Check for number of function codes in the BE2 response.
            // NOTE: Ideally message level checking should be done in ngxComm.
            if (be2BasicResp.length !== 4) {
                this.logger.error(
                    {
                        error: 'PINService: Received BE2 response with invalid number of function ' +
                        'code(s). Expected response with four function codes.',
                        data: data
                    },
                    true
                );
                return;
            }

            const functs = [
                enumBE2Func.PIN_GetStateInfo,
                enumBE2Func.PIN_ForcePIN,
                enumBE2Func.PIN_Check_PIN_Needed,
                enumBE2Func.MENU_IsENTEROnPINReq
            ];
            const functMap = NgxCommMsgHelper.getBE2BasicFunctionMap(functs, be2BasicResp);
            this.logger.debug({debug: 'PINService: Function map is: ', data: functMap}, true);

            //Store the current state to App Store
            this.appStore.UpdatePinParams((functMap[enumBE2Func.PIN_GetStateInfo][2] === 1),
                (functMap[enumBE2Func.PIN_ForcePIN][0] === 1),
                (functMap[enumBE2Func.PIN_Check_PIN_Needed][0] === 1),
                (functMap[enumBE2Func.MENU_IsENTEROnPINReq][0] === 1),
                functMap[enumBE2Func.MENU_IsENTEROnPINReq][1]);

            // If PIN is Locked, send PIN_NotEntered and validatePINResponse of Locked
            if (functMap[enumBE2Func.PIN_GetStateInfo][2] === 1) {
                this.logger.debug(
                    {
                        debug: 'PINService: PIN is locked. Sending BE2 request PIN_NotEntered, PIN_OverridePINScreen(1)' +
                        'publishing PIN LOCKED.'
                    },
                    true
                );

                const be2FunctArray = NgxCommMsgHelper.createBE2BasicFunctionArr([
                    [enumBE2Func.PIN_NotEntered, [0, 0, 0, 0]]
                ]);
                this.ngxComm.sendBe2Message(be2FunctArray, this.doNothing.bind(this));
                this.publishResponse(enumPINStatus.LOCKED);

                return;
            }

            // forcepin is false and dopinnow = 0 - pin entry not forced; and pinNeeded = 0 - pin not needed means no need for PIN validation.
            if (
                functMap[enumBE2Func.PIN_ForcePIN][0] === 0 &&
                functMap[enumBE2Func.PIN_Check_PIN_Needed][0] === 0 && !forcePin
            ) {
                this.logger.debug(
                    {debug: 'PINService: PIN entry NOT needed. PIN is already valid.'},
                    true
                );
                this.publishResponse(enumPINStatus.VALID);

                return;
            }

            // If pin is defined then we don't need to show pin screen, instead skip to validate pin
            // step.
            if (typeof PIN !== 'undefined') {
                this.logger.debug({debug: 'PINService: Pin is already defined. Skip to PIN entry response step.'}, true);
                PubSub.publish(PIN_ENTRY_RESPONSE_TOPIC, {
                    cmd: "pinEntryResp",
                    ver: "1.0",
                    PIN: PIN,
                    cancelled: false
                });
                return;
            }

            // NOTE: Looking at various permutations, we could check if PIN is forced or needed. But
            // there is no need for the check. Better to err and ask for PIN validation.

            // If either PIN is forced or needed, show PIN entry content. Pass pin length if
            // available from BE2.
            this.logger.debug(
                {
                    debug: 'PINService: dopinnow=1 or pinNeeded=1. Showing PIN entry content with ' +
                    'PIN length=' + functMap[enumBE2Func.MENU_IsENTEROnPINReq][1]
                },
                true
            );

            // Check if user needs to press the "Enter" button for Pin entry.
            const isEnterRequired = functMap[enumBE2Func.MENU_IsENTEROnPINReq][0] === 1;

            PubSub.publish(SHOW_PIN_ENTRY_SCREEN_TOPIC, {
                cmd: SHOW_PIN_ENTRY_SCREEN_TOPIC,
                ver: '1.0',
                enterRequired: isEnterRequired,
                pinLength: functMap[enumBE2Func.MENU_IsENTEROnPINReq][1]
            });

            //show PinContentFrame iframe which masks content
            document.getElementById("PinContentFrame").style.display = "inline";
        };
    }

    /**
     * Cancel PIN Entry if 0x45 token shows value 0 (card-out occurred)
     */
    cancelPINEntryOnCardOut(e) {
        if (typeof e !== 'undefined' && e.detail.hasOwnProperty('tokenUpdate')) {
            let tokenUpdate = e.detail.tokenUpdate;
            if (tokenUpdate[0].value === "0") {
                this.cancelValidatePIN();
                this.logger.debug({
                    debug: 'PINService: Card out detected, sending cancel PIN.',
                    data: {cardId: tokenUpdate[0].value}
                });
            }
        }
    }

    /**
     * Begin PIN validation process by gathering pre-check information.
     */
    beginValidatePIN(e) {
        this.logger.debug(
            {
                debug: 'PINService: Sending BE2 request PIN_GetStateInfo, PIN_ForcePIN, ' +
                'PIN_Check_PIN_Needed, MENU_IsENTEROnPINReq.',
                data: ''
            },
            true
        );

        // NOTE: To avoid keeping state in PIN service, we will check for dopinnow for both content
        // and screen trigger initiated validatePIN
        const be2FunctArray = NgxCommMsgHelper.createBE2BasicFunctionArr([
            [enumBE2Func.PIN_GetStateInfo, [0, 0, 0, 0]],
            [enumBE2Func.PIN_ForcePIN, [0, 0, 0, 0]],
            [enumBE2Func.PIN_Check_PIN_Needed, [0, 0, 0, 0]],
            [enumBE2Func.MENU_IsENTEROnPINReq, [0, 0, 0, 0]]
        ]);

        let forcePin = false;
        if(e.detail.hasOwnProperty('forcePin')){
            forcePin = e.detail.forcePin;
        }
        this.ngxComm.sendBe2Message(be2FunctArray, this.processBeginValidatePINResp(e.detail.PIN, forcePin).bind(this));
    }

    /**
     * Validate the entered pin.
     * @returns {Object} - publishes validatePINResponse with pin status.
     */
    validatePIN() {
        return function (e) {
            const pinEntryResp = e.detail;
            let be2FunctArray;
            if (this.isPinValidationTransaction()) {
                this.logger.warn({
                    warn: 'PINService: Pin validation transaction is in process. Rejecting ' +
                    'subsequent call to validate pin.',
                    data: pinEntryResp
                });
                return;
            }
            if (
                pinEntryResp === null ||
                typeof pinEntryResp === 'undefined' ||
                typeof pinEntryResp.cmd === 'undefined' ||
                typeof pinEntryResp.cancelled === 'undefined'
            ) {
                this.logger.error({
                    error: 'PINService: PIN entry response is invalid. Rejecting message. ' +
                    'Sending BE2 request PIN_NotEntered, publishing PIN NOT_ENTERED.',
                    data: pinEntryResp
                });

                const be2FunctArray = NgxCommMsgHelper.createBE2BasicFunctionArr([
                    [enumBE2Func.PIN_NotEntered, [0, 0, 0, 0]]
                ]);
                this.ngxComm.sendBe2Message(be2FunctArray, this.doNothing.bind(this));
                this.publishResponse(enumPINStatus.NOT_ENTERED);

                return;
            }

            // PIN entry was cancelled, send PIN_NotEntered and validatePINResponse of NotEntered
            if (pinEntryResp.cancelled) {
                this.logger.debug(
                    {
                        debug: 'PINService: PIN was not entered. Sending BE2 request ' +
                        'PIN_NotEntered, publishing PIN NOT_ENTERED.',
                        data: pinEntryResp
                    },
                    true
                );

                const be2FunctArray = NgxCommMsgHelper.createBE2BasicFunctionArr([
                    [enumBE2Func.PIN_NotEntered, [0, 0, 0, 0]]
                ]);
                this.ngxComm.sendBe2Message(be2FunctArray, this.doNothing.bind(this));
                this.publishResponse(enumPINStatus.NOT_ENTERED);

                return;
            }

            this.logger.debug({
                debug: 'PINService: Sending BE2 request PIN_ValidatePIN with Pin cancelled: ' +
                (pinEntryResp.cancelled ? 'true' : 'false')
            }, true);

            // PIN entered, call validate PIN and get pin status after defined time interval for n times
            be2FunctArray = NgxCommMsgHelper.createBE2BasicFunctionArr([
                [
                    NgxCommMsgHelper.enumBE2BasicFunc.PIN_ValidatePIN,
                    [parseInt(pinEntryResp.PIN, 10), pinEntryResp.PIN.length, 0, 0]
                ]
            ]);
            this.ngxComm.sendBe2Message(be2FunctArray, this.doNothing.bind(this));

            // Reinitialize attempt count and interval ID
            this.logger.debug({debug: 'PINService: Setting timer interval.'}, true);
            this.attempt = 0;
            this.pinStatusCheckIntervalID = window.setInterval(() => {
                ++this.attempt;

                // Max attempts exceeded. Either BE2 is offline, not responding, or has sent WAITING status
                // every time.
                if (this.attempt > this.GET_PIN_STATUS_MAX_ATTEMPTS) {
                    // Stop timer to avoid further triggers
                    window.clearInterval(this.pinStatusCheckIntervalID);
                    this.pinStatusCheckIntervalID = 0;

                    this.logger.error({
                        error: 'PINService: getPINStatus max attempts exceeded. Publishing ' +
                        'validatePINResp with NO_RESPONSE.'
                    }, true);
                    this.publishResponse(enumPINStatus.NO_RESPONSE);
                } else {
                    // Retry to check status
                    this.logger.debug({
                        debug: 'PINService: Sending BE2 request PIN_GetPinStatus. Attempt number= ' +
                        this.attempt
                    }, true);

                    const be2FunctArray = NgxCommMsgHelper.createBE2BasicFunctionArr([
                        [NgxCommMsgHelper.enumBE2BasicFunc.PIN_GetPinStatus, [0, 0, 0, 0]]
                    ]);
                    this.ngxComm.sendBe2Message(be2FunctArray, this.processValidatePINStatusResp.bind(this));
                }
            }, this.GET_PIN_STATUS_INTERVAL);
        };
    }

    /**
     * Processes BE2 response for PIN status request.
     * @param err An error object that if defined should describe the error that occurred
     * @param data BE2 json response object for PIN status request
     */
    processValidatePINStatusResp(err, data) {
        if (err) {
            this.logger.error({
                error: 'PINService: Received an error in processValidatePINStatusResp.',
                data: err
            }, true);
            return;
        }

        const be2BasicResp = data[NgxCommMsgHelper.BE2_BASIC_RESPONSE];
        // NOTE: Ideally message level checking should be done in ngxComm.
        if (be2BasicResp.length !== 1 || be2BasicResp[0].functionCode !== 77) {
            this.logger.error({
                error: 'PINService: Received a BE2 response with invalid function code(s). ' +
                'Expected response for function code 77 - PIN_GetPinStatus.',
                data: data
            }, true);
            return;
        }

        const status = be2BasicResp[0].retvals[0];
        // If we get a status other than 0 (WAITING), stop the timer
        // NOTE: For status = 0, no need to explicitly send a validatePINResp with WAITING. It is implied.
        if (status !== 0) {
            // Clear setInterval to avoid further triggers
            window.clearInterval(this.pinStatusCheckIntervalID);
            this.pinStatusCheckIntervalID = 0;

            if (status === 1) {
                this.logger.debug({debug: 'PINService: PIN entered is Valid .', data: data}, true);
                this.publishResponse(enumPINStatus.VALID);
            } else if (status === 2) {
                this.logger.debug({debug: 'PINService: PIN entered is Invalid .', data: data}, true);
                this.publishResponse(enumPINStatus.INVALID);
            } else if (status === 3) {
                this.logger.debug({debug: 'PINService: PIN is locked.', data: data}, true);
                this.publishResponse(enumPINStatus.LOCKED);
            } else if (status === 4) {
                this.logger.debug({
                    info: 'PINService: No Response received from BE2 for validate ' +
                    'PIN request.',
                    data: data
                }, true);
                this.publishResponse(enumPINStatus.NO_RESPONSE);
            } else if(status === 5){
                this.logger.debug({debug: 'PINService: No PIN entered to be canceled.', data: data}, true);
                this.publishResponse(enumPINStatus.NOT_ENTERED);
            }
             else {
                this.logger.error({error: 'PINService: Received unknown PIN status.', data: data}, true);
            }
        }
    }

    /**
     * Cancel PIN validation process by sending a message to PIN entry content.
     */
    cancelValidatePIN() {
        this.logger.debug({debug: 'PINService: Sending hide message to PIN entry content.'});

        PubSub.publish(HIDE_PIN_ENTRY_SCREEN_TOPIC, {
            cmd: HIDE_PIN_ENTRY_SCREEN_TOPIC,
            ver: '1.0'
        });

        this.hidePINContent();
    }

    /**
     * Hide PIN content.
     */
    hidePINContent() {
        this.logger.debug({debug: 'PINService: Hiding PIN entry content.'});

        //hide PinContentFrame iframe which masks content
        document.getElementById('PinContentFrame').style.display = "none";
    }

    //----------------- Private Helper Functions
    /**
     * Function to do nothing on BE2 responses of no interest.
     */
    doNothing(err, data) {
        if (err) {
            this.logger.error({error: 'PINService: Received an error in doNothing.', data: err}, true);
            return;
        }
        this.logger.debug({debug: 'PINService: Received BE2 response.', data: data}, true);
    }

    /**
     * Checks to see if a pin validation transaction is in process.
     * @returns boolean if complete or no transaction in process otherwise false
     */
    isPinValidationTransaction() {
        return (this.pinStatusCheckIntervalID !== 0);
    }

    /**
     * Function to register pin validation response callback.
     * The callback will be invoked when PINService publishes validate pin response and
     * create pin session response topics.
     *
     * @param callback function to be called.
     **/
    registerValidatePINRespCB(callback) {
        this.subscribe(VALIDATE_PIN_RESP_TOPIC, callback);
        this.subscribe(CREATE_PIN_SESSION_RESP_TOPIC, callback);
    }

    /**
     * Function to register for show pin entry screen.
     * The callback will be invoked when PINService publishes on SHOW_PIN_ENTRY_SCREEN_TOPIC.
     *
     * @param callback function to be called.
     **/
    registerShowPINEntryScreenCB(callback) {
        this.subscribe(SHOW_PIN_ENTRY_SCREEN_TOPIC, callback);
    }

    /**
     * Function to register for hide pin entry screen.
     * The callback will be invoked when PINService publishes on HIDE_PIN_ENTRY_SCREEN_TOPIC.
     *
     * @param callback function to be called.
     **/
    registerHidePINEntryScreenCB(callback) {
        this.subscribe(HIDE_PIN_ENTRY_SCREEN_TOPIC, callback);
    }

    /**
     * Cleanup subscriptions. Typically should be called in Unit tests before PINService instance is
     * destroyed.
     */
    cleanup() {
        if (typeof this.subscribers !== 'undefined') {
            this.subscribers.forEach(function (item) {
                PubSub.unsubscribe(item.type, item.cb);
            });
        }
        if (this.overridePinIntervalId) {
            clearInterval(this.overridePinIntervalId);
        }
        this.logger.debug({debug: 'PINService: Cleaning up'}, true);
    }
}
