'use strict';

/**
 * pubSub is a publish/subscribe wrapper logic ontop of the window eventing
 * logic. It doesn't need to be instantiated, it is always available on the
 * global window object, as in pubSub.functionname().
 */
export default class PubSub{
    static publish(type, data) {
        const ev = new CustomEvent(type, {detail: data, bubbles: true, cancelable: false});
        window.dispatchEvent(ev);
    }

    static subscribe(type, callback, capture) {
        window.addEventListener(type, callback, capture || false);
    }

    static unsubscribe(type, callback) {
        window.removeEventListener(type, callback);
    }
}
