'use strict';

import NgxCommMsgHelper from '../NgxCommMsgHelper';
import PubSub from '../PubSub';

export default class AttendantService {
    constructor(ngxComm, logger) {
        this.ngxComm = ngxComm;
        this.logger = logger;
        this.CHECK_ATTENDANT_CARD_INSERTED_TOPIC = 'attendantCardInserted';
        this.CHECK_ATTENDANT_CARD_INSERTED_RESP_TOPIC = 'attendantCardInsertedResp';
        this.enumCardType = {
            NONE: 0x00,
            UNKNOWN: 0x01,
            PLAYER: 0x02,
            VIP: 0x03,
            DROP_CARD: 0x04,
            ATTENDANT: 0x05,
            SUPERVISOR: 0x06,
            WITNESS: 0x07,
            UNREG_PLAYER: 0x08,
            REG_PLAY: 0x09,
            REG_VIP_PLAYER: 0x0A,
        };
        this.enumBE2Func = NgxCommMsgHelper.enumBE2BasicFunc;
        this.attendantCardInsertedSubscribers = [];
        this.attendantCardInsertedSubscribersResp = [];
        let attendantCardInsertedFn = this.attendantCardInserted.bind(this);
        this.attendantCardInsertedSubscribers.push(attendantCardInsertedFn);
        PubSub.subscribe(this.CHECK_ATTENDANT_CARD_INSERTED_TOPIC, attendantCardInsertedFn);
    }
    
    attendantCardInserted() {
        this.logger.debug(
            {
                debug: 'Attendant Service: Sending BE2 Basic Function request [PIN_GetCardType].',
                data: ''
            },
            true
        );
        const cardTypeArray = [[this.enumBE2Func.PIN_GetCardType, [0, 0, 0, 0]]];
        const be2req = NgxCommMsgHelper.createBE2BasicFunctionArr(cardTypeArray);
        this.ngxComm.sendBe2Message(be2req, this.processCardTypeResp.bind(this));      
    }
    processCardTypeResp(err, data) {
        const resp = {
            cmd: 'attendantCardInsertedResp',
            ver: '1.0',
            attendantInserted : false,
            cardType: 0x00
        };
        if (err) {
            this.logger.error(
                {
                    error: 'AttendantService: Received an error in processCardTypeResp.',
                    data: err
                },
                true
            );
        } else {
            let be2BasicResp;

            be2BasicResp = data[NgxCommMsgHelper.BE2_BASIC_RESPONSE];

            if (be2BasicResp.length === 0) {
                this.logger.debug({debug: 'AttendantService: No BE2 responses.', data: data}, true);
                return;
            }

            be2BasicResp.forEach( element => {
                if (element.functionCode === this.enumBE2Func.PIN_GetCardType) {
                    switch (element.retvals[0]) {
                        case this.enumCardType.WITNESS: 
                        case this.enumCardType.SUPERVISOR:
                        case this.enumCardType.ATTENDANT:
                            resp.attendantInserted = true;    
                            resp.cardType = element.retvals[0];
                            this.sendCheckAttendantCardResp(resp);
                            break;
                         default:
                            resp.cardType = element.retvals[0];
                            this.sendCheckAttendantCardResp(resp);
                            break;
                    }
                } else {
                    this.logger.debug({debug: 'AttendantService: Unknown BE2Response encountered in processCardTypeResp.', data: ''}, true);
                }
            });
        }
    } 
    sendCheckAttendantCardResp(resp) {
        // Send back the data to the requester
        PubSub.publish(this.CHECK_ATTENDANT_CARD_INSERTED_RESP_TOPIC, resp);
    }
    // Public Functions
    /**
     * Function to register Attendant Card callback.
     * The callback will be invoked when Attendant Service publishes on
     * CHECK_ATTENDANT_CARD_INSERTED_RESP_TOPIC.
     *
     * @param callback function to be called when CardType result is available.
     **/
    registerCardTypeRespCB(callback) {
        this.attendantCardInsertedSubscribersResp.push(callback);
        PubSub.subscribe(this.CHECK_ATTENDANT_CARD_INSERTED_RESP_TOPIC, callback);
    }
    /**
     * Cleanup subscriptions. Typically should be called in Unit tests before AttendantService
     * instance is destroyed.
     */
    cleanup() {

        if (this.attendantCardInsertedSubscribersResp) {
            this.attendantCardInsertedSubscribersResp.forEach( item => {
                PubSub.unsubscribe(this.CHECK_ATTENDANT_CARD_INSERTED_RESP_TOPIC, item);
            });
        }

        if (this.attendantCardInsertedSubscribers) {
            this.attendantCardInsertedSubscribers.forEach((item) => {
                PubSub.unsubscribe(this.CHECK_ATTENDANT_CARD_INSERTED_TOPIC, item);
            });
        }
        this.logger.debug({debug: 'AttendantService: Cleaning up.'}, true);
    }
}