import screenIds from 'igt-nexgen-codes/lib/screenIds';

'use strict';
export default class SessionService {
    constructor(config, logger, ngxComm) {
        const evalConfig = (config) => {
            if (typeof config.auth !== 'string') {
                throw 'Auth path must be defined';
            }

            if (typeof config.invalidate !== 'string') {
                throw 'Invalidate path must be defined';
            }

            if (typeof config.retries !== 'number') {
                // default retries to 3
                config.retries = 3;
            }

            if (typeof config.retryDelay !== 'number') {
                // default retry Delay to 1 second
                config.retryDelay = 1000;
            }

            return JSON.parse(JSON.stringify(config));
        };

        this._config = evalConfig(config);
        this._logger = logger;
        this._accessTokenPIN = null;
        this._configured = true;

        this._logger.debug({debug: 'SessionService configuration', data: this._config});

        if (typeof this._config.endpoint !== 'string') {
            this._logger.debug('SessionService - config endpoint is not defined, will not attempt to make sessions');
            this._configured = false;
        } else {
            // delete pin session token on idle, abandoned and session
            ngxComm.registerScreenTrigger(screenIds.ABANDONED_CARD_SCREEN, this.deletePINSession.bind(this));
            ngxComm.registerScreenTrigger(screenIds.VPC_ABANDONDED_CARD_SCREEN, this.deletePINSession.bind(this));
            ngxComm.registerScreenTrigger(screenIds.VPC_IDLE_SCREEN, this.deletePINSession.bind(this));
            ngxComm.registerScreenTrigger(screenIds.IDLE_SCREEN, this.deletePINSession.bind(this));
        }
    }

    // getters

    get accessTokenPIN() {
        return this._accessTokenPIN;
    }

    // public

    createPINSession(identity) {
        if (!this._configured) {
            return Promise.reject('SessionService is not configured');
        }
        if (typeof identity !== 'string' || identity.length < 1) {
            return Promise.reject('create PIN session requires an identity');
        }
        // Must explicitly call deletePINSession or the existing token will be used if it exists.
        if (this._accessTokenPIN !== null) {
            return Promise.resolve(this._accessTokenPIN);
        }
        const start = Date.now();
        const retries = this._config.retries;
        const body = JSON.stringify({username: identity, password: 'password'});
        const headers = new Headers();
        const url = `${this._config.endpoint}${this._config.auth}`;
        headers.append('Content-Type', 'application/json');
        headers.append('Cache-Control', 'no-cache');
        const init = {
            headers: headers,
            mode: 'cors',
            method: 'POST',
            body: body
        };
        let attempt = 0;
        this._logger.debug({
            msg: `SessionService createPINSession calling ${init.method} on ${url}`,
        });
        return new Promise((resolve, reject) => {
            const retry = () => {
                attempt = attempt + 1;
                fetch(url, init)
                    .then(res => {
                        res.json().then(value => {
                            const timeTaken = Date.now() - start;
                            switch (res.status) {
                                case 200:
                                    this._createPINSessionSuccess(resolve, value, timeTaken, res.status);
                                    break;
                                case 400:
                                case 401:
                                case 404:
                                    this._createPINSessionError(reject, value, timeTaken, res.status);
                                    break;
                                case 500:
                                default:
                                    if (attempt <= retries) {
                                        setTimeout(() => {
                                            retry();
                                        }, this._config.retryDelay);
                                    } else {
                                        this._createPINSessionError(reject, value, timeTaken, res.status, attempt);
                                    }
                            }
                        });
                    })
                    .catch(e => {
                        if (attempt <= retries) {
                            setTimeout(() => {
                                retry();
                            }, this._config.retryDelay);
                        } else {
                            this._createPINSessionError(reject, e, Date.now() - start, null, attempt);
                        }
                    });
            };
            retry();
        });
    }

    deletePINSession() {
        if (!this._accessTokenPIN) {
            return;
        }
        const start = Date.now();
        const retries = this._config.retries;
        const delay = this._config.retryDelay;
        const logger = this._logger;
        const headers = new Headers();
        const url = `${this._config.endpoint}${this._config.invalidate}`;
        headers.append('Authorization', `Bearer ${this._accessTokenPIN.access_token}`);
        this._accessTokenPIN = null;
        const init = {
            headers: headers,
            mode: 'cors',
            method: 'DELETE',
        };
        let attempt = 0;
        this._logger.debug({
            msg: `SessionService deletePINSession calling ${init.method} on ${url}`,
        });
        const retry = () => {
            attempt = attempt + 1;
            fetch(url, init).then(res => {
                const timeTaken = Date.now() - start;
                switch (res.status) {
                    case 204:
                        const msg = {
                            msg: `SessionService deletePINSession success duration:${timeTaken} - attempts:${attempt} - status:${res.status}`,
                        };
                        if (timeTaken > 1000) {
                            logger.warn(msg);
                        } else {
                            logger.debug(msg);
                        }
                        break;
                    case 401:
                        res.json().then(data => {
                            logger.error({
                                msg: `SessionService deletePINSession error - duration:${timeTaken} - attempts:${attempt} - status:${res.status}`,
                                data: data
                            });
                        });
                        break;
                    default:
                        if (attempt <= retries) {
                            setTimeout(() => {
                                retry();
                            }, delay);
                        } else {
                            res.json().then(data => {
                                logger.error({
                                    msg: `SessionService deletePINSession error - duration:${timeTaken} - attempts:${attempt} - status:${res.status}`,
                                    data: data
                                });
                            });
                        }
                }
            }).catch(e => {
                if (attempt <= retries) {
                    setTimeout(() => {
                        retry();
                    }, delay);
                } else {
                    const timeTaken = Date.now() - start;
                    logger.error({
                        msg: `SessionService deletePINSession error - duration:${timeTaken} - attempts:${attempt} - status:${null}`,
                        data: e
                    });
                }
            });
        };
        retry();
    }

    cleanup() {
        this._config = null;
        this._logger = null;
        this._accessTokenPIN = null;
    }

    _createPINSessionSuccess(resolve, accessToken, timeTaken, status = null, attempts = 1) {
        const msg = {
            msg: `SessionService createPINSession success duration:${timeTaken} - attempts:${attempts} - status:${status}`,
        };
        if (timeTaken > 1000) {
            this._logger.warn(msg);
        } else {
            this._logger.debug(msg);
        }
        this._accessTokenPIN = accessToken;
        resolve(this._accessTokenPIN);
    }

    _createPINSessionError(reject, error, timeTaken, status = null, attempts = 1) {
        this._logger.error({
            msg: `SessionService createPINSession error - duration:${timeTaken} - attempts:${attempts} - status:${status}`,
            data: error
        });
        reject(error);
    }

}
