import NgxCommMsgHelper from '../NgxCommMsgHelper';
import screenIds from 'igt-nexgen-codes/lib/screenIds';

'use strict';

const DIAGNOSTIC_GROUP_W2G = "w2g";
const enumBE2Func = NgxCommMsgHelper.enumBE2BasicFunc;
const createBE2BasicReq = NgxCommMsgHelper.createBE2BasicFunctionArr;
const be2FuncCode = {
    override: enumBE2Func.OverrideW2GAccrualScreen,
    init: enumBE2Func.FJP_Init,
    operation: enumBE2Func.FJP_Op,
    getHandpayParams: enumBE2Func.FJP_GetCurrentHandpayParams,
    isWitnessRequired: enumBE2Func.FJP_IsJPWitnessRequired,
    event: enumBE2Func.FJP_Event,
    cardType: enumBE2Func.PIN_GetCardType

};

const fjp_param_options = {
    handpay : 0x00,
    pouchpay : 0x01,
    IsSelfServe : 0x02,
    handpayWithOverride : 0x80,
    pouchpayWithOverride : 0x81,
    IsKeyToCredit : 0x04
};

const card_types = {
    none : 0,
    unknown : 1,
    player : 2,
    vip: 3,
    drop_card : 4,
    attendant : 5,
    supervisor : 6,
    witness : 7,
    unregistered_player : 8,
    registered_player : 9
};

//FJP system pass through operations from BE2
const fjp_card_operations = {
    GET_FIRST_CARD : 0,
    VALIDATE_RETURN_CARD: 1,
    GET_WITNESS_CARD : 2,
    GET_FILL_AMOUNT : 3,
    GET_BAG_AMOUNT : 4,
    GET_SPINTEK_STATUS :5,
    GET_AUDIT_ENABLE : 6,
    GET_CALIBRATION_ENABLE : 7,
    GET_DOOR_STATE : 8,
    GET_AUX_COUNT : 9,
    CHECK_JACKPOT_LIMIT : 10,
    CHECK_JP_OVERRIDE : 11,
    GET_MONEY_ENABLE : 12,
    GET_EMPLOYEE_BITS : 13,
    GET_SELFSERVE_OPTIONS : 14,
    CALL_ATTENDANT :15,
    RESET_HANDPAY_TO_CREDIT_METER : 16,
    GET_REMOTE_HANDPAY_RESETCODE : 17,
    CANCEL_AMBIGUOUS_TRANSFTER_MAG_CARD : 30,
    ROLLBACK_AMBIGUOUS_TRANSFTER_MAG_CARD : 32,
    RETURN_LAST_CASHOUT_AMOUNT_MAGCARD : 34
};

//FJP Definitions
const fjp_kp_events = {
    KP_EVENT_GAME_WIN : 0x00,
    KP_EVENT_CANCEL_CREDIT : 0x01,
    KP_EVENT_PROGRESSIVE : 0x02,
    KP_EVENT_SHORT_PAY : 0x03,
    KP_EVENT_HOPPER_FILL : 0x04,
    KP_EVENT_AUX_FILL : 0x05,
    KP_EVENT_AUX_FILL_REPLENISH : 0x06,
    KP_EVENT_CANCEL_HOPPER_FILL	: 0x07,
    KP_EVENT_WITNESS : 0x08,
    KP_EVENT_MYSTERY : 0x09,
    KP_EVENT_PREEMPTIVE_FILL : 0x0A,
    KP_EVENT_PREEMPTIVE_AUX : 0x0B,
    KP_EVENT_GAME_HP_BONUS : 0x0C,
    KP_EVENT_NON_CASH_PRIZE : 0x0D,
    KP_EVENT_UNKNOWN : 0xFF
};

const remoteHandpayResetCodes = {
    HANDPAY_WAS_RESET : 0,
    UNABLE_TO_RESET_HANDPAY : 1,
    NOT_CURRENTLY_IN_HANPDAY : 2,
    PENDING : 0xFF
}

const NoBE2ResponseErr = Error('No BE2 Response');
const UnknownBE2ResponseErr = Error('Unknown BE2Response!');
const NotEnabledErr = Error('W2G Accrual Service is not enabled.');

let resetTimeout = 2000;
let enabled = true;
let waitForCardTypeId = -1;
let overrideInterval = -1;
let maxRetryAttempts = 15;
let w2gInSessionTime = 15000;
let fjp_param = 0;
let hp_type = 0;
let w2g_txnId= "-1";

const sendBe2Message = function(be2req, ngxComm) {
    return new Promise((resolve, reject) => {
        ngxComm.sendBe2Message(be2req, (err, data) => {
            if(err) {
                reject(err);
            } else {
                const be2BasicResp = data[NgxCommMsgHelper.BE2_BASIC_RESPONSE];
                if (be2BasicResp.length === 0) {
                    reject(NoBE2ResponseErr);
                }else{
                    resolve(be2BasicResp);
                }
            }
        });
    });
};

const initiateHandpayResetCycle = function(ngxComm) {
    return new Promise((resolve, reject) => {

        let attempt = 0;
        const be2req = createBE2BasicReq([[be2FuncCode.operation, [0, fjp_card_operations.GET_REMOTE_HANDPAY_RESETCODE, 0, 0]],]);
        const makeCall = () => {
            ngxComm.sendBe2Message(be2req, (err, data) => {
                attempt ++;
                if( attempt >= maxRetryAttempts ) {
                    reject(new Error('W2GAccrualService: initiateHandpayResetCycle response timeout'));
                    return;
                }
                if (err) {
                    reject(err);
                } else {
                    const be2BasicResp = data[NgxCommMsgHelper.BE2_BASIC_RESPONSE];
                    if (be2BasicResp.length === 0) {
                        reject(NoBE2ResponseErr);
                    }
                    be2BasicResp.forEach(function (element) {
                        if (element.functionCode === be2FuncCode.operation) {
                            if(element.retvals[2] !== remoteHandpayResetCodes.PENDING) {
                                resolve({success: true, handpayResetCode : element.retvals[2]});
                            }else{
                                setTimeout( () => makeCall(), resetTimeout);
                            }
                        } else {
                            reject(UnknownBE2ResponseErr);
                        }
                    });
                }
            });
        };
        makeCall();
    });
};

const publishFJPEvent = function(handpayAmount, kpEvent, witnessType, param, ngxComm) {
    return new Promise((resolve, reject) => {
        const be2req = createBE2BasicReq([[be2FuncCode.event, [handpayAmount, kpEvent, witnessType, param]],]);
        sendBe2Message(be2req, ngxComm).then(be2BasicResp => {
            be2BasicResp.forEach(function (element) {
                if (element.functionCode !== be2FuncCode.event) {
                    reject(UnknownBE2ResponseErr);
                }
                resolve(element.retvals);
            })
        }).catch(e => reject(e));
    });
};

const getCardType = (ngxComm) => {
    return new Promise((resolve, reject) => {
        const be2req = createBE2BasicReq([[be2FuncCode.cardType, [0, 0, 0, 0]],]);
        sendBe2Message(be2req, ngxComm).then( be2BasicResp => {
            be2BasicResp.forEach(function (element) {
                if (element.functionCode === be2FuncCode.cardType) {
                    resolve({cardType: element.retvals[0]});
                } else {
                    reject(UnknownBE2ResponseErr);
                }
            });
        }).catch( e => reject(e) );
    });
};

const waitForCardType = (type, interval, ngxComm) => {
    return new Promise((resolve, reject) => {
        const get = () => {
            getCardType(ngxComm).then( result => {
                if( type.indexOf( result.cardType ) !== -1 ) {
                    clearTimeout(waitForCardTypeId);
                    resolve();
                } else {
                    waitForCardTypeId = setTimeout( get, interval);
                }
            }).catch( e => {
                clearTimeout(waitForCardTypeId);
                reject(e);
            });
        };
        get();
    });
};

const callAttendant = (ngxComm) => {
    return new Promise((resolve, reject) => {
        const be2req = createBE2BasicReq([[be2FuncCode.operation, [0, fjp_card_operations.CALL_ATTENDANT, 0, 0]]]);
        sendBe2Message(be2req, ngxComm).then( be2BasicResp => {
            const response = {};
            be2BasicResp.forEach(function (element) {
                if (element.functionCode === be2FuncCode.operation) {
                    response.success = element.retvals[0] === 0;
                } else {
                    reject(UnknownBE2ResponseErr);
                }
            });
            resolve(response);
        }).catch( e => reject(e) );
    });
};

const checkAttendantJackpotLimits = (ngxComm, jackpotAmount) => {
    return new Promise((resolve, reject) => {
        const be2req = createBE2BasicReq([[be2FuncCode.operation, [0, fjp_card_operations.CHECK_JACKPOT_LIMIT, jackpotAmount, 0]]]);
        sendBe2Message(be2req, ngxComm).then( be2BasicResp => {
            const response = {};
            be2BasicResp.forEach(function (element) {
                if (element.functionCode === be2FuncCode.operation) {
                    response.success = element.retvals[0];
                } else {
                    reject(UnknownBE2ResponseErr);
                }
            });
            resolve(response);
        }).catch( e => reject(e) );
    });
};




const resetHandpayToCreditMeter = function(ngxComm, txnId) {
    return new Promise((resolve, reject) => {
        const be2req = createBE2BasicReq([[be2FuncCode.operation, [0, fjp_card_operations.RESET_HANDPAY_TO_CREDIT_METER, txnId, 0]],]);
        sendBe2Message(be2req, ngxComm).then(be2BasicResp => {
            be2BasicResp.forEach(function (element) {
                if (element.functionCode !== be2FuncCode.operation) {
                    reject(UnknownBE2ResponseErr);
                }
                resolve(element.retvals);
            })
        }).catch(e => reject(e));
    });
};

export default class W2GAccrualService{
    constructor(ngxComm, logger, config, appStore, m5Config) {
        this._ngxComm = ngxComm;
        this._logger = logger;
        this._config = config;
        this._m5Config = m5Config;
        this._appStore = appStore;
        resetTimeout = this._config.resetTimeout;
        enabled = (typeof this._config.enabled === 'boolean') ? this._config.enabled : true;
        this._waitForCardTypeInterval = this._config.waitForCardTypeInterval;
        this._sentW2GOverride = false;

        const w2gSessionOverride = () => {
            this._appStore.setJackpotInProgressStatus(false);
           if(!this._sentW2GOverride) {
               this._sentW2GOverride = true;
                override();
            }
        };

        const clearW2gSentFlag = () => {
            this._sentW2GOverride = false;
            this._appStore.setJackpotInProgressStatus(false);
            this.cleanup();
        };

        const w2gAccrualScreenTrigger = (id) => {
            this._appStore.setJackpotInProgressStatus(true);
            logger.debug('W2GAccrualService: Received W2G Accrual screen trigger for Txn Id : ' + id, null, DIAGNOSTIC_GROUP_W2G);
        };

        const override = () => {
            this.cleanup();
            this.overrideW2GAccrual(1).then(() => {
                logger.debug('W2GAccrualService: OverrideW2GAccrualScreen Success at screen trigger.', null, DIAGNOSTIC_GROUP_W2G);
            }).catch(e => {
                logger.error(
                    {
                        error: 'W2GAccrualService: Received an error while trying to override W2G Accrual menu at screen trigger.',
                        data: e.message
                    }
                    , null, DIAGNOSTIC_GROUP_W2G);
            });
        };

        if(enabled) {
            this._ngxComm.registerScreenTrigger(screenIds.SESSION_SCREEN, () => w2gSessionOverride());
            this._ngxComm.registerScreenTrigger(screenIds.XTRA_CREDIT_SESSION_SCREEN, () => w2gSessionOverride());
            this._ngxComm.registerScreenTrigger(screenIds.W2G_ACCRUAL_SCREEN, ({detail: {screenTrigger: st}}) => {
                this.resetW2GTxnId();
                if(st && st.displayMessage && st.displayMessage.length > 1) {
                    let txnId = st.displayMessage;
                    this.setW2GTxnId(txnId);
                    w2gAccrualScreenTrigger(txnId);
                }
                else {
                    logger.error({error: 'W2GAccrualService: Txn Id is missing in W2G Screen Trigger from Adv', data: st}, null, DIAGNOSTIC_GROUP_W2G);
                }

            });
            this._ngxComm.registerScreenTrigger(screenIds.IDLE_SCREEN, () => clearW2gSentFlag());
            override();
        }
    }

    setW2GTxnId(id) {
        this._logger.debug('W2GAccrualService: setW2GTxnId - updated W2G Txn Id from  ' + w2g_txnId + ' to ' + id , null, DIAGNOSTIC_GROUP_W2G);
        w2g_txnId = id;
    }

    getW2GTxnId() {
        return w2g_txnId;
    }

    resetW2GTxnId() {
        this._logger.debug('W2GAccrualService: resetW2GTxnId - reset W2G Txn Id from  ' + w2g_txnId + ' to ' + '-1' , null, DIAGNOSTIC_GROUP_W2G);
        w2g_txnId = "-1";
    }

    overrideW2GAccrual(value = 1) {
        if(!enabled) return Promise.reject(NotEnabledErr);
        return new Promise((resolve, reject) => {
            const be2req = createBE2BasicReq([[be2FuncCode.override, [value, 0, 0, 0]]]);
            sendBe2Message(be2req, this._ngxComm).then(be2BasicResp => {
                be2BasicResp.forEach(function (element) {
                    if (element.functionCode !== be2FuncCode.override) {
                        return reject(UnknownBE2ResponseErr);
                    }
                });
                resolve();
            }).catch( e => reject(e) );
        });
    }

    getSpecifics() {
        this._logger.debug('W2GAccrualService: W2G getSpecifics requested from content.', null, DIAGNOSTIC_GROUP_W2G);
        fjp_param = 0;
        if(!enabled) return Promise.reject(NotEnabledErr);
        return new Promise((resolve, reject) => {
            overrideInterval = setInterval( () => {
                this.overrideW2GAccrual(1).then( () => {
                    this._logger.debug('W2GAccrualService: OverrideW2GAccrualScreen Success to extend timer.', null, DIAGNOSTIC_GROUP_W2G);
                }).catch( e => {
                    this._logger.error(
                        {
                            error: 'W2GAccrualService: Received an error while trying to override W2G Accrual to extend timer.',
                            data: e.message
                        }
                        , null, DIAGNOSTIC_GROUP_W2G);
                });
            }, w2gInSessionTime);
            const be2req = createBE2BasicReq([
                [be2FuncCode.init, [0, 0, 0, 0]],
                [be2FuncCode.operation, [0, fjp_card_operations.GET_SELFSERVE_OPTIONS, 0, 0]],
                [be2FuncCode.getHandpayParams, [0, 0, 0, 0]]
            ]);
            sendBe2Message(be2req, this._ngxComm).then( be2BasicResp => {
                const response = {};
                response.w2gTransactionId = w2g_txnId;
                be2BasicResp.forEach(function (element) {
                    switch(element.functionCode) {
                        case be2FuncCode.init:
                            break;
                        case be2FuncCode.operation:
                            response.selfServeOption = element.retvals[0];
                            response.keytoCreditAvailable = element.retvals[2] === 1;
                            break;
                        case be2FuncCode.getHandpayParams:
                            response.handpayType = element.retvals[0];
                            hp_type = response.handpayType;
                            response.amountInCredits = element.retvals[1];
                            response.amountInCents = element.retvals[2];
                            response.w2gStatus = element.retvals[3];
                            break;
                        default:
                            return reject(UnknownBE2ResponseErr);
                    }
                });
                resolve(response);
            }).catch(e => reject(e));
        });
    }

    requestAttendant(handPayAmount) {
        this._logger.debug('W2GAccrualService: requestAttendant called from content for Hanpday Amount ' + handPayAmount, null, DIAGNOSTIC_GROUP_W2G);
        if(!enabled) return Promise.reject(NotEnabledErr);
        return new Promise( (resolve, reject) => {
            callAttendant(this._ngxComm).then( res => {
                this._logger.debug({debug: 'W2GAccrualService: call attendant result', data: res}, null, DIAGNOSTIC_GROUP_W2G);
                if (res.success) {
                    return waitForCardType( [card_types.attendant, card_types.supervisor, card_types.witness], this._waitForCardTypeInterval, this._ngxComm );
                }
                reject(new Error('W2GAccrualService: failed to call attendant'));
            }).then( res =>  {                
                this._logger.debug({debug: 'W2GAccrualService: wait for card type attendant result', data: res}, null, DIAGNOSTIC_GROUP_W2G);
                if(this._m5Config.w2g && this._m5Config.w2g.jackpotLimitsCheck) {
                    checkAttendantJackpotLimits(this._ngxComm, handPayAmount).then(resp => {
                        if(resp && resp.hasOwnProperty('success')) {
                            if (resp.success === 0) {
                                resolve({success: true});
                            } else {
                                resolve({success: false});
                            }
                        }
                        reject(new Error('W2GAccrualService: failed to check Attendant Authroization Limits'));
                    })
                }
                else {
                    resolve({success: true});
                }
            }).catch( e => reject(e) );
        });
    }

    resetToCreditMeter(handPayAmount,  creditHandPayAmount, witnessInitiated,  pinValidated = false) {
        let payAmount = 0;
        this._logger.debug('W2GAccrualService: resetToCreditMeter requested from content for amount : ' + handPayAmount + ' witnessInitiated ' + witnessInitiated + ' pinValidated ' + pinValidated +  ' Txn Id :' + w2g_txnId, null, DIAGNOSTIC_GROUP_W2G);

        if (!enabled) return Promise.reject(NotEnabledErr);
        fjp_param += pinValidated ? (fjp_param | fjp_param_options.IsSelfServe | fjp_param_options.IsKeyToCredit) : (fjp_param | fjp_param_options.IsKeyToCredit) ;
        return new Promise((resolve, reject) => {
            let type = '';
            if (hp_type === fjp_kp_events.KP_EVENT_GAME_WIN || hp_type === fjp_kp_events.KP_EVENT_CANCEL_CREDIT || hp_type === fjp_kp_events.KP_EVENT_SHORT_PAY) {
                payAmount = creditHandPayAmount;
                type = 'credits';
            } else {
                payAmount = handPayAmount;
                type = 'cents';
            }
            this._logger.debug('W2GAccrualService: 1st publishFJPEvent hp_type : ' + hp_type + ' - fjp_param : ' + fjp_param + ' - for amount : ' + payAmount + ' - in : ' + type, null, DIAGNOSTIC_GROUP_W2G);
            publishFJPEvent(payAmount, hp_type, 0, fjp_param, this._ngxComm).then((retvals) => {
                if (retvals[2] !== 0) {
                    return reject(new Error(`W2GAccrualService: FJPEvent Expected return value 0 for event , received ${retvals[2]}`));
                }
            }).then(() => {
                //TODO : check if this is required only for witness card (what about player and employee)
                if(witnessInitiated) {
                    this._logger.debug('W2GAccrualService: 2nd publishFJPEvent with KP_EVENT_WITNESS for amount : ' + payAmount, null, DIAGNOSTIC_GROUP_W2G);
                    return publishFJPEvent(payAmount, fjp_kp_events.KP_EVENT_WITNESS, 0, 0, this._ngxComm);
                }
            }).then(() => {
                this._logger.debug('W2GAccrualService: resetToCreditMeter with OP RESET_HANDPAY_TO_CREDIT_METER called for amount : ' + payAmount, null, DIAGNOSTIC_GROUP_W2G);
                if(w2g_txnId === "-1"){
                    this._logger.error( { error: 'W2GAccrualService: resetToCreditMeter - Invalid Txn Id', data: w2g_txnId }, null, DIAGNOSTIC_GROUP_W2G);
                }
                const binaryTxnId = parseInt(w2g_txnId, 16);
                this._logger.debug('W2GAccrualService: resetToCreditMeter w2g_txnId : ' + w2g_txnId + ' binaryTxnId : ' + binaryTxnId , null, DIAGNOSTIC_GROUP_W2G);
                return resetHandpayToCreditMeter(this._ngxComm, binaryTxnId);
            }).then(() => {
                this._logger.debug('W2GAccrualService: initiateHandpayResetCycle with op GET_REMOTE_HANDPAY_RESETCODE called for amount : ' + payAmount, null, DIAGNOSTIC_GROUP_W2G);
                return initiateHandpayResetCycle(this._ngxComm);
            }).then((handpayResetStatus) => {
                this.resetW2GTxnId();
                this.cleanup();
                this._logger.debug('W2GAccrualService: W2G Keyoff successfully completed for amount : ' + payAmount + ' Txn Id :' + w2g_txnId, null, DIAGNOSTIC_GROUP_W2G);
                resolve({success: true, handPayAmount: payAmount, handpayResetCode : handpayResetStatus.handpayResetCode});
            }).catch((e) => {
                this._logger.error( { error: 'W2GAccrualService: Received an error while trying to process resetToCreditMeter', data: e.message }, null, DIAGNOSTIC_GROUP_W2G);
                this.cleanup();
                this.overrideW2GAccrual(0).then(() => {
                    this._logger.debug('W2GAccrualService: OverrideW2GAccrualScreen Canceled at reset to credit meter.', null, DIAGNOSTIC_GROUP_W2G);
                }).catch((e) => {
                    this._logger.error({ error: 'W2GAccrualService: Received an error while trying to cancel Override W2G Accrual menu at reset to credit meter.', data: e.message }, null, DIAGNOSTIC_GROUP_W2G);
                });
                reject(e);
            });
        });
    }

    setAttendantReturn() {
        this._logger.debug('W2GAccrualService: setAttendantReturn called', null, DIAGNOSTIC_GROUP_W2G);
        if(!enabled) return Promise.reject(NotEnabledErr);
        return new Promise((resolve, reject) => {
            const be2req = createBE2BasicReq([
                [be2FuncCode.operation, [0, fjp_card_operations.GET_FIRST_CARD, 0, 0]]
            ]);
            sendBe2Message(be2req, this._ngxComm).then(be2BasicResp => {
                const response = {};
                be2BasicResp.forEach(function (element) {
                    if (element.functionCode === be2FuncCode.operation) {
                        response.success = element.retvals[0] === 0;
                    } else {
                        return reject(UnknownBE2ResponseErr);
                    }
                });
                resolve(response);
            }).catch( e => reject(e) );
        });

    }

    validateWitness() {
        this._logger.debug('W2GAccrualService: validateWitness called', null, DIAGNOSTIC_GROUP_W2G);
        if(!enabled) return Promise.reject(NotEnabledErr);
        return new Promise((resolve, reject) => {
            const be2req = createBE2BasicReq([
                [be2FuncCode.operation, [0, fjp_card_operations.GET_WITNESS_CARD, 0, 0]]
            ]);
            sendBe2Message(be2req, this._ngxComm).then(be2BasicResp => {
                const response = {};
                be2BasicResp.forEach(function (element) {
                    if (element.functionCode === be2FuncCode.operation) {
                        response.success = element.retvals[0] === 0;
                    } else {
                        return reject(UnknownBE2ResponseErr);
                    }
                });
                resolve(response);
            }).catch(e => reject(e));
        });
    }

    isWitnessRequired() {
        this._logger.debug('W2GAccrualService: isWitnessRequired called', null, DIAGNOSTIC_GROUP_W2G);
        if(!enabled) return Promise.reject(NotEnabledErr);
        return new Promise((resolve, reject) => {
            const be2req = createBE2BasicReq([
                [be2FuncCode.isWitnessRequired, [0, 0, 0, 0]]
            ]);
            sendBe2Message(be2req, this._ngxComm).then(be2BasicResp => {
                const response = {};
                be2BasicResp.forEach(function (element) {
                    if (element.functionCode === be2FuncCode.isWitnessRequired) {
                        response.required = element.retvals[0] === 1;
                    } else {
                        return reject(UnknownBE2ResponseErr);
                    }
                });
                resolve(response);
            }).catch(e => reject(e));
        });
    }

    gotoLegacy() {
        this._logger.debug('W2GAccrualService: gotoLegacy called', null, DIAGNOSTIC_GROUP_W2G);
        if(!enabled) return Promise.reject(NotEnabledErr);
        return new Promise((resolve, reject) => {
            const msg = NgxCommMsgHelper.createButtonPress('KEYPRESS_E');
            this.cleanup();
            this.overrideW2GAccrual(0).then( () => {
                this._logger.debug('W2GAccrualService: OverrideW2GAccrualScreen cancel Success at gotoLegacy.', null, DIAGNOSTIC_GROUP_W2G);
                this._ngxComm.sendMessage(msg);
                resolve();
            }).catch( e => reject(e) );
        });
    }

    cancelw2g() {
        this._logger.debug('W2GAccrualService: cancelw2g called', null, DIAGNOSTIC_GROUP_W2G);
        if(!enabled) return Promise.reject(NotEnabledErr);
        return new Promise((resolve, reject) => {
            this.cleanup();
            this.overrideW2GAccrual(0).then( () => {
                this._logger.debug('W2GAccrualService: OverrideW2GAccrualScreen cancel Success at cancelw2g.', null, DIAGNOSTIC_GROUP_W2G);
                resolve();
            }).catch( e => reject(e) );
        });
    }

    cleanup() {
        this._logger.debug('W2GAccrualService: M5 W2G cleanup called', null, DIAGNOSTIC_GROUP_W2G);
        clearInterval(overrideInterval);
        clearTimeout(waitForCardTypeId);
        waitForCardTypeId = -1;
        resetTimeout = this._config.resetTimeout;
        fjp_param = 0;
    }
}

