'use strict';

/**
 * statusMessageCache stores the custom definitions loaded from the statusMap.json file in shell
 */
export default class StatusMessageCache {
    constructor() {
        //used to store map from statusMap
        this.statusMessageMap = {};
    }

    /**
     * set the statusMessageMap object
     * @return contentState
     */
    setStatusMessages(statusMapData) {
        this.statusMessageMap = statusMapData;
    }

    /**
     * Retrieve the statusMessageMap object
     * @return contentState
     */
    getStatusMessages() {
        return this.statusMessageMap;
    }
}
