'use strict';
import ZSchema from 'z-schema';
import jsonschema from 'm5Schema/schemas/jsonschema.json';

import activateDiagnostics from 'm5Schema/schemas/activateDiagnostics.json';
import buttonPress from 'm5Schema/schemas/buttonPress.json';
import hostEventButtonPress from 'm5Schema/schemas/hostEventButtonPress.json';
import screenTrigger from 'm5Schema/schemas/screenTrigger.json';
import subscription from 'm5Schema/schemas/subscription.json';
import subscriptionAck from 'm5Schema/schemas/subscriptionAck.json';
import tokenUpdate from 'm5Schema/schemas/tokenUpdate.json';

import be2BasicFunction from 'm5Schema/schemas/m5/be2BasicFunction.json';
import be2BasicResponse from 'm5Schema/schemas/m5/be2BasicResponse.json';
import bonusHit from 'm5Schema/schemas/m5/bonusHit.json';
import drawStatus from 'm5Schema/schemas/m5/drawStatus.json';
import fullscreenState from 'm5Schema/schemas/m5/fullscreenState.json';
import gameEnd from 'm5Schema/schemas/m5/gameEnd.json';
import gameEvent from 'm5Schema/schemas/m5/gameEvent.json';
import gameStart from 'm5Schema/schemas/m5/gameStart.json';
import getServerAddresses from 'm5Schema/schemas/m5/getServerAddresses.json';
import ismUpdate from 'm5Schema/schemas/m5/ismUpdate.json';
import levelUpdate from 'm5Schema/schemas/m5/levelUpdate.json';
import patronData from 'm5Schema/schemas/m5/patronData.json';
import patronDataExt from 'm5Schema/schemas/m5/patronDataExt.json';
import serverAddresses from 'm5Schema/schemas/m5/serverAddresses.json';
import showWindow from 'm5Schema/schemas/m5/showWindow.json';
import statusUpdate from 'm5Schema/schemas/m5/statusUpdate.json';
import statusmap from 'm5Schema/schemas/m5/statusmap.json';
import urlmap from 'm5Schema/schemas/m5/urlmap.json';
import windowState from 'm5Schema/schemas/m5/windowState.json';
import cardlessStatus from 'm5Schema/schemas/m5/cardlessStatus.json';

import forceCardOut from 'm5Schema/schemas/ewallet/forceCardOut.json';
import forceCardOutAck from 'm5Schema/schemas/ewallet/forceCardOutAck.json';
import getQRcode from 'm5Schema/schemas/ewallet/getQRcode.json';
import qrCode from 'm5Schema/schemas/ewallet/qrCode.json';

import vpcButtonPress from 'm5Schema/schemas/vpc/vpcButtonPress.json';
import vpcGetPlayerData from 'm5Schema/schemas/vpc/vpcGetPlayerData.json';
import vpcPIN from 'm5Schema/schemas/vpc/vpcPIN.json';
import vpcPlayerData from 'm5Schema/schemas/vpc/vpcPlayerData.json';

import getUserToken from 'm5Schema/schemas/spin/getUserToken.json';
import languageUpdate from 'm5Schema/schemas/spin/languageUpdate.json';
import userTokenUpdate from 'm5Schema/schemas/spin/userTokenUpdate.json';

const schemas = {
    activateDiagnostics: activateDiagnostics,
    buttonPress: buttonPress,
    hostEventButtonPress: hostEventButtonPress,
    screenTrigger: screenTrigger,
    subscription: subscription,
    subscriptionAck: subscriptionAck,
    tokenUpdate: tokenUpdate,

    be2BasicFunction: be2BasicFunction,
    be2BasicResponse: be2BasicResponse,
    bonusHit: bonusHit,
    drawStatus: drawStatus,
    fullscreenState: fullscreenState,
    gameEnd: gameEnd,
    gameEvent: gameEvent,
    gameStart: gameStart,
    getServerAddresses: getServerAddresses,
    ismUpdate: ismUpdate,
    levelUpdate: levelUpdate,
    patronData: patronData,
    patronDataExt: patronDataExt,
    serverAddresses: serverAddresses,
    showWindow: showWindow,
    statusUpdate: statusUpdate,
    statusmap: statusmap,
    urlmap: urlmap,
    windowState: windowState,
    cardlessStatus: cardlessStatus,

    forceCardOut: forceCardOut,
    forceCardOutAck: forceCardOutAck,
    getQRcode: getQRcode,
    qrCode: qrCode,

    vpcButtonPress: vpcButtonPress,
    vpcGetPlayerData: vpcGetPlayerData,
    vpcPIN: vpcPIN,
    vpcPlayerData: vpcPlayerData,

    getUserToken: getUserToken,
    languageUpdate: languageUpdate,
    userTokenUpdate: userTokenUpdate
};

let validator = null;

const getValidator = function () {
    if (!validator) {
        validator = new ZSchema();
        const requiredUrl = "http://json-schema.org/draft-04/schema";
        validator.setRemoteReference(requiredUrl, jsonschema);
    }

    return validator;
};

/**
 * @constructor this.schemas
 * @param callback {Function} A callback in the style of node error first
 */
export default class Schemas {
    /**
     * @memberof this.schemas
     * @method validate
     * @instance
     * @param data {Object} The data to validate against a schema
     * @param schema {String} The name of the schema to validate against.
     */
    static async validate(data, schema) {
        return new Promise((resolve, reject) => {
            const validator = getValidator();

            validator.validate(data, schemas[schema], (err, valid) => {
                if (err) {
                    reject(err);
                }
                else if (valid) {
                    resolve();
                }
            });
        });
    };
}
