import Schemas from './Schemas';
import ShellController from './ShellController';
import JSONFileParser from './JSONFileParser';
import Logger from './Logger';
import param from './Param';
//globals
require('./pollyfill').default;

'use strict';
/**
 * shell -
 * @param pageUrl {string} - url of the current page.
 * @param urlMap {object} - An object that contains a mapping of screenTriggers to URLs.
 * @param statusMapUrl {string} - A string that contains the location of the custom status map
 * @param m5ConfigUrl {string} - A string that contains the location of M5 Config.
 * @param ngxSubscriptionUrl {string} - A string that contains the location of ngx subscription Config.
 * @constructor
 */
export default class Shell {
    constructor(pageUrl, urlMapUrl, statusMapUrl, m5ConfigUrl, ngxSubscriptionUrl) {
        const deviceId = param.getDeviceId(pageUrl);
        let urlMap = {};
        let statusMap = {};
        let m5Config = {};
        let ngxSubscriptionConfig = {};

        JSONFileParser.getJSONFile(urlMapUrl).then(function (Data) {
            urlMap = Data;
            const hostUrl = param.getHostUrl(urlMap, pageUrl);
            const loggerUrl = param.getLoggerUrl(urlMap, pageUrl);
            const logLevel = param.getLogLevel(urlMap, pageUrl);
            const paramsObj = param.getUrlParamsObject(pageUrl);
            //get M5Config
            JSONFileParser.getJSONFile(m5ConfigUrl).then(function (Data) {
                const logger = new Logger(loggerUrl, deviceId, logLevel, paramsObj, Data);
                m5Config = Data;
                JSONFileParser.getJSONFile(ngxSubscriptionUrl).then(function (Data) {
                    ngxSubscriptionConfig = Data;

                    //get statusmap
                    JSONFileParser.getJSONFile(statusMapUrl).then(function (Data) {
                        statusMap = Data;

                        logger.info({
                            DeviceId: deviceId,
                            HostUrl: hostUrl,
                            LoggerUrl: loggerUrl,
                            LogLevel: logLevel,
                            DiagnosticTargets: logger.m5Config.diagnosticTargets
                        });

                        //validate both json files before instantiating
                        Promise.all([Schemas.validate(urlMap, 'urlmap'),Schemas.validate(statusMap, 'statusmap')]).then( values =>{
                            Shell.createShellController(hostUrl, pageUrl, urlMap, statusMap, logger, m5Config, ngxSubscriptionConfig);
                        }).catch(err =>{
                            logger.error({error: err}, true);
                        });

                    }).catch(function (error) {
                        const statusMapErrorMsg = "Shell: Error Parsing " + statusMapUrl + " - " + error.toString();
                        logger.error({error: statusMapErrorMsg}, true);
                    });

                }).catch(function (error) {
                    const ngxSubscriptionErrorMsg = "Shell: Error Parsing " + ngxSubscriptionUrl + " - " + error.toString();
                    logger.error({error: ngxSubscriptionErrorMsg}, true);
                });

            }).catch(function (error) {
                const m5ConfigErrorMsg = "Shell: Error Parsing " + m5ConfigUrl + " - " + error.toString();
                throw new Error(m5ConfigErrorMsg);
            });
        }).catch(function (error) {
            const urlMapErrorMsg = "Shell: Error Parsing " + urlMapUrl + " - " + error.toString();
            throw new Error(urlMapErrorMsg);
        });
    }
    // dependency injection for unit tests
    static createShellController (hostUrl, pageUrl, urlMap, statusMap, logger, m5Config, ngxSubscriptionConfig){
        new ShellController(
            hostUrl,
            param.filterPageUrl(pageUrl, ['localWebSocketPort', 'localXmlSocketPort']),
            urlMap,
            statusMap,
            logger,
            m5Config,
            ngxSubscriptionConfig
        );
    }
}
