import NgxCommMsgHelper from '../NgxCommMsgHelper';
import PubSub from '../PubSub';
'use strict';
/**
 * PointPayService contains Point Pay related API calls between shell services, apps, content, and nexgen.
 * @param ngxComm {object} Comm Interface service instance
 * @param this.logger {object} this.logger service instance
 * @constructor
 */
export default class PointPayService {
    constructor(ngxComm, logger) {
        this.ngxComm = ngxComm;
        this.logger = logger;

        // Topics
        this.CHECK_POINT_PAY_ENABLED_TOPIC = 'checkPointPayEnabled';
        this.CHECK_POINT_PAY_ENABLED_RESP_TOPIC = 'checkPointPayEnabledResp';
        this.CHECK_POINT_PAY_CODE_TOPIC = 'checkPointPayCode';
        this.CHECK_POINT_PAY_CODE_RESP_TOPIC = 'checkPointPayCodeResp';
        this.INITIATE_POINT_PAY_TOPIC = 'initiatePointPay';
        this.INITIATE_POINT_PAY_RESP_TOPIC = 'initiatePointPayResp';

        // used for cleanup() function
        this.ppEnabledCB = null;
        this.ppCodeCB = null;
        this.ppInitiateCB = null;

        this.checkPointPayEnabledFn = this.checkPointPayEnabled.bind(this);
        this.checkPointPayCodeFn = this.checkPointPayCode.bind(this);
        this.initiatePointPayFn = this.initiatePointPay.bind(this);
        // PubSub registers
        PubSub.subscribe(this.CHECK_POINT_PAY_ENABLED_TOPIC, this.checkPointPayEnabledFn);
        PubSub.subscribe(this.CHECK_POINT_PAY_CODE_TOPIC, this.checkPointPayCodeFn);
        PubSub.subscribe(this.INITIATE_POINT_PAY_TOPIC, this.initiatePointPayFn);
    }

    /**
     * This function sends a request to the BEII to collect information related to the
     * player's Point Pay enabled status
     */
    checkPointPayEnabled() {
        this.logger.debug({
            debug: 'Point Pay Service: Sending BE2 Basic Function request [PT_Get_Point_Pay_Available]',
            data: ''
        }, true);

        const pointPayEnabled = [
            [NgxCommMsgHelper.enumBE2BasicFunc.PT_Get_Point_Pay_Available, [0, 0, 0, 0]]
        ];

        const be2req = NgxCommMsgHelper.createBE2BasicFunctionArr(pointPayEnabled);
        this.ngxComm.sendBe2Message(be2req, this.processCheckPointPayEnabledResp.bind(this));

    }

    /**
     * This function sends a response back to content with information related to the
     * player's Point Pay enabled status
     */
    processCheckPointPayEnabledResp(err, data) {
        if (err) {
            this.logger.error({
                error: 'Point Pay Service: Received an error in processCheckPointPayEnabledResp.',
                data: err
            }, true);
        } else {

            const resp = {
                cmd: 'checkPointPayEnabledResp',
                ver: '1.0',
                enabled: false
            };

            const be2BasicResp = data[NgxCommMsgHelper.BE2_BASIC_RESPONSE];

            if (be2BasicResp.length === 0) {
                this.logger.debug({debug: 'Point Pay Service: No BE2 responses.', data: data}, true);
            } else {
                be2BasicResp.forEach((element) => {
                    if (element.functionCode === NgxCommMsgHelper.enumBE2BasicFunc.PT_Get_Point_Pay_Available) {
                        element.retvals[0] === 1 ? resp.enabled = true : resp.enabled = false;
                    } else {
                        this.logger.debug({
                            debug: 'Unknown BE2Response encountered in processCheckPointPayEnabledResp.',
                            data: ''
                        }, true);
                    }
                });

                this.logger.debug({
                    debug: 'Point Pay Service: Point Pay enabled is ' + resp.enabled,
                    data: ''
                }, true);
            }

            PubSub.publish(this.CHECK_POINT_PAY_ENABLED_RESP_TOPIC, resp);

        }

    }

    /**
     * This function sends a request to the BEII to check if the player's Point Pay Code
     * is valid.
     */
    checkPointPayCode(e) {
        const pointPayCode = parseInt(e.detail.code, 10);

        this.logger.debug({
            debug: 'Point Pay Service: Sending BE2 Basic Function request [MP_CheckPointPayCode]',
            data: ''
        }, true);

        const checkPointPayCode = [
            [NgxCommMsgHelper.enumBE2BasicFunc.MP_CheckPointPayCode, [parseInt(pointPayCode.toString(), 10), pointPayCode.toString().length, 0, 0]]
        ];

        const be2req = NgxCommMsgHelper.createBE2BasicFunctionArr(checkPointPayCode);
        this.ngxComm.sendBe2Message(be2req, this.processCheckPointPayCodeResp.bind(this));

    }

    /**
     * This function sends a response back to content with information related to whether the
     * Point Pay Code is valid or not.
     */
    processCheckPointPayCodeResp(err, data) {
        if (err) {
            this.logger.error({
                error: 'Point Pay Service: Received an error in processCheckPointPayCodeResp.',
                data: err
            }, true);
        } else {

            const resp = {
                cmd: 'checkPointPayCodeResp',
                ver: '1.0',
                valid: false
            };

            const be2BasicResp = data[NgxCommMsgHelper.BE2_BASIC_RESPONSE];

            if (be2BasicResp.length === 0) {
                this.logger.debug({debug: 'Point Pay Service: No BE2 responses.', data: data}, true);
            } else {
                be2BasicResp.forEach((element) => {
                    if (element.functionCode === NgxCommMsgHelper.enumBE2BasicFunc.MP_CheckPointPayCode) {
                        element.retvals[2] === 1 ? resp.valid = true : resp.valid = false;
                    } else {
                        this.logger.debug({
                            debug: 'Unknown BE2Response encountered in processCheckPointPayCodeResp.',
                            data: ''
                        }, true);
                    }
                });

                this.logger.debug({
                    debug: 'Point Pay Service: Pay Code matched is ' + resp.valid,
                    data: ''
                }, true);
            }

            PubSub.publish(this.CHECK_POINT_PAY_CODE_RESP_TOPIC, resp);
        }
    }

    /**
     * This function sends a request to the BEII to initiate a Point Pay conversion based off
     * the passed in points.
     */
    initiatePointPay(e) {
        const points = e.detail.points;

        this.logger.debug({
            debug: 'Point Pay Service: Sending BE2 Basic Function request [MP_PointsToPayment]',
            data: ''
        }, true);

        const initiatePointPayArray = [
            [NgxCommMsgHelper.enumBE2BasicFunc.MP_PointsToPayment, [points, 0, 0, 0]]
        ];

        const be2req = NgxCommMsgHelper.createBE2BasicFunctionArr(initiatePointPayArray);
        this.ngxComm.sendBe2Message(be2req, this.processInitiatePointPayResp.bind(this));

    }

    /**
     * This function sends a response back to content with information related to the
     * status of the initiate Point Pay transaction
     */
    processInitiatePointPayResp(err, data) {
        const resp = {
            cmd: 'initiatePointPayResp',
            ver: '1.0',
            status: 0
        };

        if (err) {
            this.logger.error({
                error: 'Point Pay Service: Received an error in processInitiatePointPayResp.',
                data: err
            }, true);
        } else {

            const be2BasicResp = data[NgxCommMsgHelper.BE2_BASIC_RESPONSE];

            if (be2BasicResp.length === 0) {
                this.logger.debug({debug: 'Point Pay Service: No BE2 responses.', data: data}, true);
            } else {
                be2BasicResp.forEach((element) => {
                    if (element.functionCode === NgxCommMsgHelper.enumBE2BasicFunc.MP_PointsToPayment) {
                        resp.status = element.retvals[1];
                    } else {
                        this.logger.debug({
                            debug: 'Unknown BE2Response encountered in processInitiatePointPayResp.',
                            data: ''
                        }, true);
                    }
                });

                this.logger.debug({
                    debug: 'Point Pay Service: Transaction ' + (resp.status === 1 ? 'succeeded ' : 'failed ') + 'with status: ' + resp.status,
                    data: ''
                }, true);
            }

        }

        PubSub.publish(this.INITIATE_POINT_PAY_RESP_TOPIC, resp);
    }

    /**
     * Function to register Point Pay Enabled response callback.
     * The callback will be invoked when Point Pay Service publishes on
     * CHECK_POINT_PAY_ENABLED_RESP_TOPIC.
     *
     * @param callback function to be called when PointPayEnabled result is available.
     **/
    registerPointPayEnabledRespCB(callback) {
        this.ppEnabledCB = callback;
        PubSub.subscribe(this.CHECK_POINT_PAY_ENABLED_RESP_TOPIC, callback);
    }

    /**
     * Function to register Point Pay Code response callback.
     * The callback will be invoked when Point Pay Service publishes on
     * CHECK_POINT_PAY_CODE_RESP_TOPIC.
     *
     * @param callback function to be called when PointPayCode result is available.
     **/
    registerPointPayCodeRespCB(callback) {
        this.ppCodeCB = callback;
        PubSub.subscribe(this.CHECK_POINT_PAY_CODE_RESP_TOPIC, callback);
    }

    /**
     * Function to register Initiate Point Pay response callback.
     * The callback will be invoked when Point Pay Service publishes on
     * INITIATE_CHECK_POINT_PAY_RESP_TOPIC.
     *
     * @param callback function to be called when InitiatePointPay result is available.
     **/
    registerInitiatePointPayRespCB(callback) {
        this.ppInitiateCB = callback;
        PubSub.subscribe(this.INITIATE_POINT_PAY_RESP_TOPIC, callback);
    }

    /**
     * Cleanup subscriptions. Typically should be called in Unit tests before Point Pay service
     * instance is destroyed.
     */
    cleanup() {
        PubSub.unsubscribe(this.CHECK_POINT_PAY_ENABLED_TOPIC, this.checkPointPayEnabledFn);
        PubSub.unsubscribe(this.CHECK_POINT_PAY_CODE_TOPIC, this.checkPointPayCodeFn);
        PubSub.unsubscribe(this.INITIATE_POINT_PAY_TOPIC, this.initiatePointPayFn);

        PubSub.unsubscribe(this.CHECK_POINT_PAY_ENABLED_RESP_TOPIC, this.ppEnabledCB);
        PubSub.unsubscribe(this.CHECK_POINT_PAY_CODE_RESP_TOPIC, this.ppCodeCB);
        PubSub.unsubscribe(this.INITIATE_POINT_PAY_RESP_TOPIC, this.ppInitiateCB);

        this.logger.debug({debug: 'Point Pay Service: Cleaning up.'}, true);
    }
}

