import ViewListener from './ViewListener';
import SessionService from './services/SessionService';
import W2GAccrualService from './services/W2GAccrualService';
import NgxCommSubscriptionManager from './NgxCommSubscriptionManager';
import ContentCache from './ContentCache';
import AppStore from './AppStore';
import StatusMessageCache from './StatusMessageCache';
import NgxComm from './NgxComm';
import ViewControllers from './ViewControllers';
import PINService from './services/PINService';
import XCService from './services/XCService';
import PointPlayService from './services/PointPlayService';
import CashlessService from './services/CashlessService';
import PointPayService from './services/PointPayService';
import FullScreenToggle from './services/FullScreenToggle';
import ShellCache from './ShellCache';
import screenIds from 'igt-nexgen-codes/lib/screenIds';
import param from './Param';
import WorkersController from "./WorkersController";
import AttendantService from './services/AttendantService';
import CardlessService from "./services/CardlessService";
import Schemas from './Schemas';

'use strict';

/**
 * Private helper function to setup session service.
 * @endpoint{String} - The security service endpoint
 * @return {SessionService} - An instance of the session service
 */
const setupSessionService = ({securityServiceEndpoint: endpoint = null}, logger, ngxComm) => {
    return new SessionService({
        endpoint: endpoint,
        auth: '/identity_zones/m5zone/user/authenticate',
        invalidate: '/user/access_token',
        retryDelay: 500
    }, logger, ngxComm);
};

/**
 * Private helper function to setup content cache with triggers and token.
 *
 * @ngxComm{Object} - ngxComm instance
 * @triggers{Array} - array of trigger ids
 * @tokens{Array} - array of token ids
 */
const setupCache = (ngxComm, {subscription: {screenTrigger: triggers, tokenUpdate: tokens}}, contentCache) => {
    triggers.forEach(id => {
        ngxComm.registerScreenTrigger(id, ({detail: {screenTrigger: st}}) => {
            contentCache.storeTrigger(st.screenId, st.displayMessage, st.messageTokens);
        });
    });

    tokens.forEach(id => {
        ngxComm.registerTokenUpdate(id, ({detail: {tokenUpdate: tu}}) => {
            contentCache.storeToken(tu[0].id, tu[0].value);
        });
    });
};

const setupAppStore = (ngxComm, {subscription: {screenTrigger: triggers, tokenUpdate: tokens}}, appStore) => {
    triggers.forEach(id => {
        ngxComm.registerScreenTrigger(id, ({detail: {screenTrigger: st}}) => {
            appStore.handleTriggerUpdate(st.screenId, st.displayMessage, st.messageTokens);
        });
    });

    tokens.forEach(id => {
        ngxComm.registerTokenUpdate(id, ({detail: {tokenUpdate: tu}}) => {
            appStore.handleTokenUpdate(tu[0].id, tu[0].value);
        });
    });
};

const getAndSetupIframe = async (vc, url, qs, logger) => {
    const div = window.parent.document.querySelector(qs);

    if (div !== null) {
        await vc.setupIframe(div, url);
    }
    else {
        const msg = `Cannot find iframe element with id of ${qs}. Please ensure it exists.`;

        logger.error({
            error: msg
        }, true);

        return Promise.reject(msg);
    }
};

/**
 * NGX Shell
 * @param ngxCommHost {string} url for the NgxComm Host.
 * @param  pageUrl {string} url parameters that are part of the page url.
 * @param urlMap {object} An object that contains a mapping of screenTriggers to URLs.
 * @param logger {object} Logger service instance.
 * @param m5Config {object} M5 Configuration.
 * @param ngxSubscriptionUrl {object} Adv Ngx subscription configuration.
 * @constructor
 */
export default class ShellController {
    constructor(ngxCommHost, pageUrl, urlMap, statusMap, logger, m5Config, ngxSubscriptionConfig) {
        this.logger = logger;
        this.m5Config = m5Config;
        this.ngxSubscriptionConfig = ngxSubscriptionConfig;
        /**
         * Private helper function for loading new content into the content view.
         * @param url The content url.
         **/
        this.loadContent = async (url) => {
            await getAndSetupIframe(this.triggerContentVC, url, '#WrapperFrame', this.logger);
        };
        let normUrlMap;

        try {
            normUrlMap = param.normalizeUrlMap(urlMap);
        } catch (e) {
            logger.error({error: e}, true);
        }

        const screenTriggerIds = Object.keys(normUrlMap.screenIdMap);
        const subscribeAllTriggers = urlMap.subscribeAllTriggers;

        const subManager = new NgxCommSubscriptionManager(
            screenTriggerIds,
            logger,
            subscribeAllTriggers,
            normUrlMap.legacyIdList,
            Schemas,
            this.ngxSubscriptionConfig);

        const ngxComm = new NgxComm(ngxCommHost, subManager, logger, this.m5Config);
        ngxComm.setupWebSocket().then(() => {
            logger.debug({
                debug: 'Content interface web socket setup at shell controller is complete.',
            }, true);

            // initialization of WorkerController
            new WorkersController(urlMap.webWorkers, logger);

            const contentCache = new ContentCache();
            const appStore = new AppStore(ngxComm, logger, this.m5Config);
            // Setup content cache before view controller so that the cache gets updated first
            setupCache(ngxComm, subManager.getSubscription(), contentCache);
            setupAppStore(ngxComm, subManager.getSubscription(), appStore);

            this.bannerVC = ViewControllers.VCWRegistration(logger, pageUrl, 'BannerVC', ngxComm, subManager);
            this.pinContentVC = ViewControllers.VCWRegistration(logger, pageUrl, 'PINContentVC', ngxComm, subManager);
            this.triggerContentVC = ViewControllers.VCWRegAndTrigger(logger, pageUrl, 'TriggerContentVC', ngxComm, subManager, normUrlMap, m5Config);
            const sessionService = setupSessionService(normUrlMap, logger, ngxComm);
            const w2gEnabled = Object.keys(normUrlMap.screenIdMap).indexOf(screenIds.W2G_ACCRUAL_SCREEN) !== -1 ;
            const w2gConfig = {
                enabled: w2gEnabled,
                resetTimeout: 2000,
                waitForCardTypeInterval: 2000
            };
            const w2gAccrualService = new W2GAccrualService(ngxComm, logger, w2gConfig, appStore, this.m5Config);
            const shellCache = new ShellCache();
            this.pinService = new PINService(ngxComm, sessionService, logger, appStore, screenTriggerIds);
            this.fullScreenToggle = new FullScreenToggle(ngxComm, shellCache, logger, param.getUrlParamsObject(pageUrl));
            this.xtraCreditService = new XCService(ngxComm, logger, appStore, statusMap);
            this.pointPlayService = new PointPlayService(ngxComm, logger, appStore);
            this.cashlessService = new CashlessService(ngxComm, logger, appStore);
            this.pointPayService = new PointPayService(ngxComm, logger);
            this.attendantService = new AttendantService(ngxComm, logger);
            this.cardlessService = new CardlessService(ngxComm, logger,m5Config, appStore, ngxSubscriptionConfig.cardlessStatus);
            this.statusMessageCache = new StatusMessageCache();
            this.statusMessageCache.setStatusMessages(statusMap);
            const broadcastToView = data => {
                this.bannerVC.sendMessage(data);
                this.triggerContentVC.sendMessage(data);
                WorkersController.Instance().broadcastMessage(data);
            };

            /**
             * Private Shell helper function that broadcasts data to the content view.
             * @param data The data to broadcast.
             **/
            const broadcastToPin = data => {
                this.pinContentVC.sendMessage(data);
            };

            new ViewListener(
                ngxComm,
                broadcastToView,
                this.loadContent,
                subManager,
                shellCache,
                contentCache,
                logger,
                sessionService,
                w2gAccrualService,
                param.getUrlParamsObject(pageUrl),
                this.statusMessageCache,
                this.cardlessService
            );

            this.pinService.registerValidatePINRespCB(broadcastToView);
            this.pinService.registerValidatePINRespCB(broadcastToPin);
            this.pinService.registerShowPINEntryScreenCB(broadcastToPin);
            this.pinService.registerHidePINEntryScreenCB(broadcastToPin);

            this.xtraCreditService.registerEnableXtraCreditRespCB(broadcastToView);
            this.xtraCreditService.registerActivateXtraCreditRespCB(broadcastToView);
            this.xtraCreditService.registerCancelXtraCreditRespCB(broadcastToView);
            this.xtraCreditService.registerIsActiveXCRespCB(broadcastToView);

            this.pointPlayService.registerEnablePointPlayRespCB(broadcastToView);
            this.pointPlayService.registerInitiatePointPlayRespCB(broadcastToView);
            this.pointPlayService.registerConvertPPRespCB(broadcastToView);

            this.cashlessService.registerCashlessEnabledRespCB(broadcastToView);
            this.cashlessService.registerGetCashlessBalanceRespCB(broadcastToView);
            this.cashlessService.registerInitiateTransferRespCB(broadcastToView);

            this.pointPayService.registerPointPayEnabledRespCB(broadcastToView) ;
            this.pointPayService.registerPointPayCodeRespCB(broadcastToView);
            this.pointPayService.registerInitiatePointPayRespCB(broadcastToView);

            this.attendantService.registerCardTypeRespCB(broadcastToView);

            this.cardlessService.registerCardlessStatusUpdateCB(broadcastToView);

            ngxComm.registerLanguageUpdate(broadcastToView);
            ngxComm.registerLanguageUpdate(broadcastToPin);
            ngxComm.registerFullScreenState(broadcastToView);
            ngxComm.registerFullScreenState(broadcastToPin);

            Promise.all([
                getAndSetupIframe(this.triggerContentVC, normUrlMap.indexUrl, '#WrapperFrame', logger),
                getAndSetupIframe(this.bannerVC, normUrlMap.bannerUrl, '#BannerFrame', logger),
                getAndSetupIframe(this.pinContentVC, normUrlMap.pinContentUrl, '#PinContentFrame', logger),
            ]).then(() => {
                ngxComm.sendSubscription();
            }).catch((e) => {
                logger.error({
                    error: 'Unable to setup iframes',
                    data: e
                }, true);
            });

        }).catch(e => {
            logger.error({
                error: 'Error in shell controller setup',
                data: e.message
            }, true);
        });
    }

    /**
     * Cleanup subscriptions. Typically should be called in
     * Unit tests before Shell instance is destroyed.
     */
    cleanup() {
        this.pinService.cleanup();
        this.fullScreenToggle.cleanup();
        this.xtraCreditService.cleanup();
        this.pointPlayService.cleanup();
        this.cashlessService.cleanup();
        this.pointPayService.cleanup();
        this.attendantService.cleanup();
        this.cardlessService.cleanup();
    };
}
