'use strict';

import NgxCommMsgHelper from '../NgxCommMsgHelper';
import PubSub from '../PubSub';



/**
 * CashlessService contains cashless related work-flows between shell services, apps, content, and
 * nexgen. Other services can also perform PIN validation using appropriate amplify request.
 * @param this.ngxComm {object} Comm Interface service instance
 * @param this.logger {object} this.logger service instance
 * @constructor
 */
export default class CashlessService {
    constructor(ngxComm, logger, appStore) {
        this.ngxComm = ngxComm;
        this.logger = logger;
        this.appStore = appStore;

        this.CHECK_CASHLESS_ENABLED_TOPIC = 'checkCashlessEnabled';
        this.CHECK_CASHLESS_ENABLED_RESP_TOPIC = 'checkCashlessEnabledResp';
        this.GET_CASHLESS_BALANCE_TOPIC = 'getCashlessBalance';
        this.GET_CASHLESS_BALANCE_RESP_TOPIC = 'getCashlessBalanceResp';
        this.INITIATE_CASHLESS_TRANSFER_FROM_GAME_TOPIC = 'initiateTransferFromEgm';
        this.INITIATE_CASHLESS_TRANSFER_RESP_TOPIC = 'initiateTransferResp';
        this.INITIATE_CASHLESS_TRANSFER_FROM_CARD_TOPIC = 'initiateTransferFromCard';
        this.VALIDATE_PIN_TOPIC = 'validatePIN';
        this.VALIDATE_PIN_RESP_TOPIC = 'validatePINResp';

        // Get Cashless Status retry logic vars
        this.GET_CASHLESS_STATUS_MAX_ATTEMPTS = 30;
        this.GET_CASHLESS_STATUS_INTERVAL = 1000;

        this.enumCashlessResult = {
            TRANSFER_PENDING: 0x00,
            TRANSFER_SUCCESS: 0x01,
            TRANSFER_ABORTED: 0x02,
            TRANSFER_FAILED: 0x03,
            TRANSFER_ABORTED_OPERATION_STARTED: 0x05,
            TRANSFER_FAILED_OPERATION_STARTED: 0x06
        };

        this.enumCashlessTransferStatus = {
            SUCCESSFUL_OPERATION: 0,
            ABORTED_OPERATION: 1,
            FAILED_OPERATION: 2,
            ABORTED_OPERATION_STARTED: 3,
            FAILED_OPERATION_STARTED: 4,
            FAILED_OTHER_REASON: 5,
            TIMEOUT: 6,
            UNLOCK_SUCCESSFUL: 7,
            CREDITS_EXIST: 8,
            GAME_IN_PROGRESS: 9,
            UNLOCK_FAILED_OTHER_REASON: 10,
            INVALID_TRANSFER_REQUEST: 11,
            REQUESTED_AMOUNT_TO_LOW: 12,
            REQUESTED_AMOUNT_LESS_THAN_MIN_FR: 13,
            REQUESTED_AMOUNT_GREATER_THAN_MAX_FR: 14,
            REQUESTED_AMOUNT_EXCEEDS_CARD_BALANCE: 15,
            CARD_BALANCE_TOO_LOW: 16,
            REQUESTED_AMOUNT_LESS_THAN_MIN_TO: 17,
            REQUESTED_AMOUNT_GREATER_THAN_MAX_TO: 18,
            PIN_LOCKED: 19,
            PIN_VALIDATION_FAILED: 20
        };

        this.enumXferDirection = {
            INVALID_DIRECTION : 0,
            XFER_FROM_GAME: 1,
            XFER_FROM_CARD: 2
        };

        this.enumPINStatus = {
            VALID: 1,
            INVALID: 2,
            LOCKED: 3,
            NO_RESPONSE: 4,
            NOT_ENTERED: 5
        };

        // Set AutoPlay Status retry logic vars
        this.GET_AUTOPLAY_STATUS_MAX_ATTEMPTS = 5;
        this.GET_AUTOPLAY_STATUS_INTERVAL = 1000;

        // Set AutoPlay Status retry logic vars
        this.SET_EJECT_CARD_MAX_ATTEMPTS = 6;
        this.SET_EJECT_CARD_INTERVAL = 5000;

        this.cashlessCheckResp1 = false;
        this.cashlessCheckResp2 = false;

        this.transferStatusCount = 0;
        this.cashlessStatusCheckIntervalID = 0;
        this.transferAmount = 0;
        this.cashoutRequest = false;
        this.ejectCard = false;
        this.disableAutoPlay = false;
        this.getAutoPlayStatusCount = 0;
        this.getAutoPlayStatusCheckIntervalID = 0;
        this.allowTransferOverMaxBal = false;
        this.validatePin = true;
        this.xferDirection = this.enumXferDirection.INVALID_DIRECTION;

        this.enumBE2Func = NgxCommMsgHelper.enumBE2BasicFunc;

        // Properties
        // Keeping track of subscribers for clean up. This is needed when each unit test creates a new
        // instance of PPService
        this.checkCashlessEnabledSubscribers = [];
        this.checkCashlessEnabledSubscribersResp = [];
        this.getCashlessBalanceSubscribers = [];
        this.getCashlessBalanceSubscribersResp = [];
        this.getInitiateTransferFromSubscribers = [];
        this.getInitiateTransferToSubscribers = [];
        this.getInitiateTransferSubscribersResp = [];

        this.handleValidatePINRespFn = this.handleValidatePINResp.bind(this);
        let checkCashlessEnabledFn = this.checkCashlessEnabled.bind(this);
        this.checkCashlessEnabledSubscribers.push(checkCashlessEnabledFn);
        PubSub.subscribe(this.CHECK_CASHLESS_ENABLED_TOPIC, checkCashlessEnabledFn);

        let getCashlessBalancesFn = this.getCashlessBalances.bind(this);
        this.getCashlessBalanceSubscribers.push(getCashlessBalancesFn);
        PubSub.subscribe(this.GET_CASHLESS_BALANCE_TOPIC, getCashlessBalancesFn);

        let initiateTransferFromGameFn = this.initiateTransferFromGame.bind(this);
        this.getInitiateTransferFromSubscribers.push(initiateTransferFromGameFn);
        PubSub.subscribe(this.INITIATE_CASHLESS_TRANSFER_FROM_GAME_TOPIC, initiateTransferFromGameFn);

        let initiateTransferFromCardFn = this.initiateTransferFromCard.bind(this)
        this.getInitiateTransferToSubscribers.push(initiateTransferFromCardFn);
        PubSub.subscribe(this.INITIATE_CASHLESS_TRANSFER_FROM_CARD_TOPIC, initiateTransferFromCardFn);

    }
    // Private Functions
    /**
     * This function sends two requests to the BEII to collect the information related to the
     * players cashless status
     */
    checkCashlessEnabled() {
        this.processCheckCashlessEnabledResp();
    }

    /**
     * Processes BE2 basic function response to check if Cashless is enabled for the player
     * It requires both responses to be received before publishing a response.
     * @param err An error object that if defined should describe the error that occurred
     * @param data BE2 basic function response message
     */
    processCheckCashlessEnabledResp() {
        const resp = {
            cmd: 'checkCashlessEnabledResp',
            ver: '1.0',
            enabled: false,
            responseData: {
                cardType: 0,
                bonusing: false,
                haveData: false,
                haveLock: false,
                gameEnabled: false,
                coinlessAvailable: false,
                balanceActive: false,
                cashlessEnabled: false,
                abandonedStatus: false,
                haveCTSData: false,
                haveCardData: false,
                coinlessEnabled: false
            }
        };
        let appStoreCache = this.appStore.getAppStoreCache();
        resp.enabled = appStoreCache.playerBonuses.cashless.enable;
        resp.responseData.cardType = appStoreCache.playerBonuses.cashless.cardType;
        resp.responseData.abandonedStatus = appStoreCache.player_info.isAbandoned;
        resp.responseData.bonusing = appStoreCache.playerBonuses.cashless.flags.bonusing;
        resp.responseData.haveData = appStoreCache.playerBonuses.cashless.flags.haveData;
        resp.responseData.haveLock = appStoreCache.playerBonuses.cashless.flags.haveLock;
        resp.responseData.gameEnabled = appStoreCache.playerBonuses.cashless.flags.gameEnabled;
        resp.responseData.coinlessAvailable = appStoreCache.playerBonuses.cashless.flags.coinlessAvailable;
        resp.responseData.balanceActive = appStoreCache.playerBonuses.cashless.flags.balanceActive;
        resp.responseData.cashlessEnabled = appStoreCache.playerBonuses.cashless.flags.cashlessEnabled;
        resp.responseData.haveCTSData = appStoreCache.playerBonuses.cashless.flags.haveCTSData;
        resp.responseData.haveCardData = appStoreCache.playerBonuses.cashless.flags.haveCardData;
        resp.responseData.coinlessEnabled = appStoreCache.playerBonuses.cashless.flags.coinlessEnabled;

        /* Cashless being enabled implies that all the following data fields must be true */
        if(appStoreCache.player_info.isAbandoned === true){
            resp.enabled = false;
            this.logger.debug({debug: 'CashlessService: Cashless is NOT available Card is abandoned', data: resp},true);
        }
        else if(resp.enabled){
            this.logger.debug({debug: 'CashlessService: Cashless is available', data: resp},true);
        }
        else{
            this.logger.debug({debug: 'CashlessService: Cashless is NOT available.', data: resp},true);
        }
        PubSub.publish(this.CHECK_CASHLESS_ENABLED_RESP_TOPIC, resp);
    }

    /**
     * This function processes requests to the BEII to retrieve the players cashless
     * balance and limits.
     */
    getCashlessBalances() {
        this.logger.debug(
            {
                debug: 'CashlessService: Sending BE2 Basic Function request ' +
                '[GetCashlessParameters, MP_GetEgmDenom].',
                data: ''
            },
            true
        );

        const cashlessArray = [
            [this.enumBE2Func.GetCashlessParameters, [0, 0, 0, 0]],
            [this.enumBE2Func.MP_GetEgmDenom, [0, 0, 0, 0]]
        ];

        const be2req1 = NgxCommMsgHelper.createBE2BasicFunctionArr(cashlessArray);
        this.ngxComm.sendBe2Message(be2req1, this.processGetCashlessBalanceResp.bind(this));
    }

    /**
     * Processes BE2 basic function response to retrieve the cashless balance and limits
     * for the player
     * @param err An error object that if defined should describe the error that occurred
     * @param data BE2 basic function response message
     */
    processGetCashlessBalanceResp(err, data) {
        const resp = {
            cmd: 'getCashlessBalanceResp',
            ver: '1.0',
            cashlessBalances: {
                cardCents: 0,
                egmCents: 0,
                minToEgm: 0,
                maxToEgm: 0,
                minFrEgm: 0,
                maxFrEgm: 0,
                maxCardBal: 0,
                egmDenom: 0
            }
        };

        if (err) {
            this.logger.error(
                {
                    error: 'CashlessService: Received an error in processGetCashlessBalanceResp.',
                    data: err
                },
                true
            );
        } else {
            let appStoreCache = this.appStore.getAppStoreCache();

            resp.cashlessBalances.minToEgm = appStoreCache.playerBonuses.cashless.transferLimits.minToEgm;
            resp.cashlessBalances.maxToEgm = appStoreCache.playerBonuses.cashless.transferLimits.maxToEgm;
            resp.cashlessBalances.minFrEgm = appStoreCache.playerBonuses.cashless.transferLimits.minFrEgm;
            resp.cashlessBalances.maxFrEgm = appStoreCache.playerBonuses.cashless.transferLimits.maxFrEgm;
            resp.cashlessBalances.maxCardBal = appStoreCache.playerBonuses.cashless.transferLimits.maxBal;

            let be2BasicResp;

            be2BasicResp = data[NgxCommMsgHelper.BE2_BASIC_RESPONSE];

            if (be2BasicResp.length === 0) {
                this.logger.debug({debug: 'CashlessService: No BE2 responses.', data: data}, true);
                return;
            }

            be2BasicResp.forEach( element => {
                switch (element.functionCode) {
                    case this.enumBE2Func.GetCashlessParameters:
                        resp.cashlessBalances.cardCents = element.retvals[0];
                        resp.cashlessBalances.egmCents = element.retvals[1];
                        //update appstore cache value
                        appStoreCache.playerBonuses.cashless.balances.cardCents = element.retvals[0];
                        appStoreCache.playerBonuses.cashless.balances.egmCents = element.retvals[1];
                        break;
                    case this.enumBE2Func.MP_GetEgmDenom:
                        resp.cashlessBalances.egmDenom = element.retvals[0];
                        appStoreCache.playerBonuses.cashless.balances.egmDenom = element.retvals[0];
                        break;
                    default:
                        this.logger.debug({debug: 'Unknown BE2Response!', data: ''}, true);
                        break;
                }
            });
            // Send back the data to the requester
            PubSub.publish(this.GET_CASHLESS_BALANCE_RESP_TOPIC, resp);
        }
    }

    doNothing(err, data) {
        if (err) {
            this.logError('doNothing', err);
            return;
        }
    }

    handleValidatePINResp(e) {
        const data = e.detail;
        this.logger.info({info: 'CashlessService: PIN validation response received.', data: data }, true);
        //Dont unsusbcribe for invalid, since it can become valid and can be entered later.
        if(data.status === this.enumPINStatus.VALID || data.status === this.enumPINStatus.LOCKED || data.status === this.enumPINStatus.NO_RESPONSE || data.status === this.enumPINStatus.NOT_ENTERED) {
            PubSub.unsubscribe(this.VALIDATE_PIN_RESP_TOPIC, this.handleValidatePINRespFn);
        }

        if (data.status === this.enumPINStatus.VALID) {
            if (this.xferDirection === this.enumXferDirection.XFER_FROM_GAME){
                this.completeXferFromGame();

            }
            else if(this.xferDirection === this.enumXferDirection.XFER_FROM_CARD){
                this.completeXferFromCard();
            }
        }
        //do not publish a response for invalid pin, wait for pin to lock, be cancelled, or successful or initial transfer promise will lose scope
        else if (data.status === this.enumPINStatus.INVALID) {
            this.logger.debug({info: 'CashlessService: PIN validation response received as INVALID', data: data }, true);
        }
        else{
            PubSub.publish(
                this.INITIATE_CASHLESS_TRANSFER_RESP_TOPIC,
                this.createInitiateTransferInvalidRequestResp(this.enumCashlessTransferStatus.PIN_VALIDATION_FAILED)
            );
        }
    }
    /**
     * This function processes requests to initiate a transfer from the game.
     * @param e Contains the amount in cents for the transfer from the card to be performed,
     * as well as whether the request is for a cashout, and if there is a smart card, whether
     * autoplay should be stopped, and the card ejected.
     */
    initiateTransferFromGame(e) {
        const data = e.detail;
        this.transferAmount = data.amount;
        this.cashoutRequest = data.cashoutRequest;

        this.disableAutoPlay = data.smartCard.autoPlayInUse;
        this.ejectCard = data.smartCard.ejectCard;
        this.allowTransferOverMaxBal = data.allowTransferOverMaxBal;
        this.validatePin = data.validatePin;

        const getLimitsArray = [
            [this.enumBE2Func.GetCashlessParameters, [0, 0, 0, 0]]
        ];

        const getLimitsReq = NgxCommMsgHelper.createBE2BasicFunctionArr(getLimitsArray);
        this.ngxComm.sendBe2Message(getLimitsReq, (err, data) => {
            if (err) {
                this.logger.error(
                    {
                        error: 'CashlessService: Received an error in initiateTransferFromGame.',
                        data: err
                    },
                    true
                );

                return;
            }

            const be2BasicResp = data[NgxCommMsgHelper.BE2_BASIC_RESPONSE];
            if (be2BasicResp.length === 0) {
                this.logger.debug({debug: 'CashlessService: No BE2 responses.', data: data}, true);
                return;
            }
            let appStoreCache = this.appStore.getAppStoreCache();

            let minFrEgm = appStoreCache.playerBonuses.cashless.transferLimits.minFrEgm;
            let maxCardBal = appStoreCache.playerBonuses.cashless.transferLimits.maxBal;
            let cardCents = null;
            let egmCents = null;

            be2BasicResp.forEach(element => {
                switch (element.functionCode) {
                    case this.enumBE2Func.GetCashlessParameters:
                        cardCents = element.retvals[0];
                        egmCents = element.retvals[1];
                        //update appstore cached values
                        appStoreCache.playerBonuses.cashless.balances.cardCents = element.retvals[0];
                        appStoreCache.playerBonuses.cashless.balances.egmCents = element.retvals[1];
                        break;
                    default:
                        this.logger.debug({debug: 'Unknown BE2Response!', data: ''}, true);
                        break;
                }
            }, this);

            // if cashout request use egmCents to do validation checks
            if (typeof this.cashoutRequest !== 'undefined' && this.cashoutRequest === true) {
                this.transferAmount = egmCents;
            }

            // Transfer Amount request of 0 should never be sent to the BEII.
            if (this.transferAmount <= 0) {
                this.logger.debug({
                    debug: 'CashlessService: Invalid transfer request, transfer amount is less than or equal to 0.',
                    data: data
                }, true);
                PubSub.publish(
                    this.INITIATE_CASHLESS_TRANSFER_RESP_TOPIC,
                    this.createInitiateTransferInvalidRequestResp(this.enumCashlessTransferStatus.REQUESTED_AMOUNT_TO_LOW)
                );
                return;
            }

            // Transfer Amount request which is less than min_from_egm should not be allowed.
            if (parseInt(this.transferAmount, 10) < minFrEgm) {
                this.logger.debug({
                    debug: 'CashlessService: Invalid transfer request, transfer amount can not be less than the min from egm.',
                    data: data
                }, true);
                PubSub.publish(
                    this.INITIATE_CASHLESS_TRANSFER_RESP_TOPIC,
                    this.createInitiateTransferInvalidRequestResp(this.enumCashlessTransferStatus.REQUESTED_AMOUNT_LESS_THAN_MIN_FR)
                );
                return;
            }

            /* Transfer should not be allowed to be initiated if the card balance is already greater than the max card balance.
             * Note this is allowed by Ezpay configuration, so sending allow overmax balance will attempt the transfer but it
             * will be rejected by Ezpay if the option is not available.
             */
            if ((cardCents > maxCardBal) && (this.allowTransferOverMaxBal !== undefined && this.allowTransferOverMaxBal === false)) {
                this.logger.debug({
                    debug: 'CashlessService: Invalid transfer request, amount on card cannot be greater than max card balance.',
                    data: data
                }, true);
                PubSub.publish(
                    this.INITIATE_CASHLESS_TRANSFER_RESP_TOPIC,
                    this.createInitiateTransferInvalidRequestResp(this.enumCashlessTransferStatus.REQUESTED_AMOUNT_GREATER_THAN_MAX_FR)
                );
                return;
            }

            if (appStoreCache.pin.locked) {
                const be2FunctArray = NgxCommMsgHelper.createBE2BasicFunctionArr([
                    [NgxCommMsgHelper.enumBE2BasicFunc.PIN_NotEntered, [0, 0, 0, 0]]
                ]);
                this.ngxComm.sendBe2Message(be2FunctArray, this.doNothing.bind(this));
                PubSub.publish(
                    this.INITIATE_CASHLESS_TRANSFER_RESP_TOPIC,
                    this.createInitiateTransferInvalidRequestResp(this.enumCashlessTransferStatus.PIN_LOCKED)
                );
                return;
            }

            //TODO: this.isAppPinRequired (handle M5 requires pin flag later)
            if ((appStoreCache.pin.check_pin_needed || appStoreCache.pin.force_pin) && this.validatePin === true){
                this.logger.debug({debug: 'CashlessService: PIN required. Validating PIN.', data: null}, true);
                this.xferDirection = this.enumXferDirection.XFER_FROM_GAME;
                //lets clear all previous subscription for pin validation and start new here
                PubSub.unsubscribe(this.VALIDATE_PIN_RESP_TOPIC, this.handleValidatePINRespFn);
                PubSub.subscribe(this.VALIDATE_PIN_RESP_TOPIC, this.handleValidatePINRespFn);
                PubSub.publish(this.VALIDATE_PIN_TOPIC, this.createValidatePINReq());
                return;
            }
            // Subscribe to PIN validation response status from PIN service.
            this.completeXferFromGame();
        });
    }

     completeXferFromGame() {
        let cashlessArray;
        let be2req1;

        if (this.cashoutRequest) {
            // issue a generic cashout command, ignore the egm balance
            const cashlessArray = [
                [this.enumBE2Func.Cashless_TransferFromGame, [0, 0xFFFFFFFF, 1, 0]]
            ];

            const be2req1 = NgxCommMsgHelper.createBE2BasicFunctionArr(cashlessArray);
            this.ngxComm.sendBe2Message(be2req1, this.processInitiateTransferFromGameResp.bind(this));
            // Begin Checking the transfer status
            this.transferStatusCount = 0;
            this.cashlessStatusCheckIntervalID = window.setInterval(
                this.checkTransferStatus.bind(this),
                this.GET_CASHLESS_STATUS_INTERVAL
            );

        } else {
            if (this.disableAutoPlay) {
                // Begin Checking if setting AutoPlay is allowed.
                this.getAutoPlayStatusCount = 0;
                this.getAutoPlayStatusCheckIntervalID = window.setInterval(
                    this.checkGetAutoPlayStatus.bind(this),
                    this.GET_AUTOPLAY_STATUS_INTERVAL
                );
            }
            else {
                cashlessArray = [
                    [this.enumBE2Func.Cashless_TransferFromGame, [0, parseInt(this.transferAmount, 10), 1, 0]]
                ];

                be2req1 = NgxCommMsgHelper.createBE2BasicFunctionArr(cashlessArray);
                this.ngxComm.sendBe2Message(be2req1, this.processInitiateTransferFromGameResp.bind(this));
                // Begin Checking the transfer status
                this.transferStatusCount = 0;
                this.cashlessStatusCheckIntervalID = window.setInterval(
                    this.checkTransferStatus.bind(this),
                    this.GET_CASHLESS_STATUS_INTERVAL
                );
            }
        }
    }


    /**
     * Processes BE2 basic function response from the initiation of the transfer from the game.
     * @param err An error object that if defined should describe the error that occurred
     * @param data BE2 basic function response message
     */
    processInitiateTransferFromGameResp(err, data) {
        if (err) {
            this.logger.error(
                {
                    error: 'CashlessService: Received an error in ' +
                    'processInitiateTransferFromGameResp.',
                    data: err
                },
                true
            );
        } else {
            let be2BasicResp;

            be2BasicResp = data[NgxCommMsgHelper.BE2_BASIC_RESPONSE];

            if (be2BasicResp.length === 0) {
                this.logger.debug({debug: 'CashlessService: No BE2 responses.', data: data}, true);
                return;
            }

            be2BasicResp.forEach( element => {
                if (element.functionCode !== this.enumBE2Func.Cashless_TransferFromGame) {
                    this.logger.debug({debug: 'Unknown BE2Response!', data: ''}, true);
                }
            });
        }
    }

    /**
     * This function creates a Transfer response message for a timeout.
     */
    createInitiateTransferTimeoutResp() {
        return {
            cmd: 'initiateTransferResp',
            ver: '1.0',
            transferData: {
                status: this.enumCashlessTransferStatus.TIMEOUT,
                transferAmount: this.transferAmount
            }
        };
    }

    /**
     * This function creates a Transfer response message for an invalid tranfer amount.
     */
    createInitiateTransferInvalidRequestResp(reason) {
        return {
            cmd: 'initiateTransferResp',
            ver: '1.0',
            transferData: {
                status: reason,
                transferAmount: this.transferAmount
            }
        };
    }

    /**
     * This function initiates sending a message to the BEII to get the current status of the
     * active cashless transfer.  If the max transfer attempts is exceeded, this function will send
     * out the failure message.
     */
    checkTransferStatus() {
        this.logger.debug(
            {
                debug: 'CashlessService: Sending BE2 Basic Function request [Cashless_GetStatus].',
                data: ''
            },
            true
        );
        ++this.transferStatusCount;

        // Max attempts exceeded. Either BE2 is offline, not responding, or has sent WAITING status
        // every time.
        if (this.transferStatusCount > this.GET_CASHLESS_STATUS_MAX_ATTEMPTS) {
            // Stop timer to avoid further triggers
            window.clearInterval(this.cashlessStatusCheckIntervalID);

            this.logger.error(
                {
                    error: 'CashlessService: checkTransferStatus max attempts exceeded. ' +
                    'Publishing InitiateTransferResp with NO_RESPONSE.'
                },
                true
            );
            PubSub.publish(
                this.INITIATE_CASHLESS_TRANSFER_RESP_TOPIC,
                this.createInitiateTransferTimeoutResp()
            );
        }

        const cashlessArray = [[this.enumBE2Func.Cashless_GetStatus, [0, 0, 0, 0]]];
        const be2req1 = NgxCommMsgHelper.createBE2BasicFunctionArr(cashlessArray);
        this.ngxComm.sendBe2Message(be2req1, this.processCheckTransferStatusResp.bind(this));
    }

    /**
     * This function initiates sending a message to the BEII to set the AutoPlay status for the
     * active cashless transfer.  If the max transfer attempts is exceeded, this function will send
     * out the failure message.
     */
    checkGetAutoPlayStatus() {
        ++this.getAutoPlayStatusCount;
        // Max attempts exceeded. Either BE2 is offline, not responding, or has sent WAITING status
        // every time.
        if (this.getAutoPlayStatusCount > this.GET_AUTOPLAY_STATUS_MAX_ATTEMPTS) {
            // Stop timer to avoid further triggers
            window.clearInterval(this.getAutoPlayStatusCheckIntervalID);

            this.logger.error(
                {
                    error: 'CashlessService: getAutoPlayStatus max attempts exceeded. ' +
                    'Publishing InitiateTransferResp with NO_RESPONSE.'
                },
                true
            );
            this.sendCashlessStatus(
                {
                    cmd: 'initiateTransferResp',
                    ver: '1.0',
                    transferData: {
                        status: this.enumCashlessTransferStatus.FAILED_OTHER_REASON,
                        transferAmount: this.transferAmount
                    }
                });
        }

        //Request if AutoPlay is allowed to be set.
        const be2req = NgxCommMsgHelper.createBE2BasicFunctionArr([[this.enumBE2Func.MENU_GetAutoPlayStatus, [0, 0, 0, 0]]]);
        this.ngxComm.sendBe2Message(be2req, this.processGetAutoPlayStatusResp.bind(this));
    }

    /**
     * Processes BE2 basic function response for the transfer status, based on the status
     * a retry may be made, before a result is sent back the the requester
     * @param err An error object that if defined should describe the error that occurred
     * @param data BE2 basic function response message
     */
    processCheckTransferStatusResp(err, data) {
        const resp = {
            cmd: 'initiateTransferResp',
            ver: '1.0',
            transferData: {
                status: this.enumCashlessTransferStatus.FAILED_OTHER_REASON,
                transferAmount: this.transferAmount
            }
        };

        if (err) {
            this.logger.error(
                {
                    error: 'CashlessService: Received an error in processCheckTransferStatusResp.',
                    data: err
                },
                true
            );
        } else {
            let be2BasicResp;

            be2BasicResp = data[NgxCommMsgHelper.BE2_BASIC_RESPONSE];

            if (be2BasicResp.length === 0) {
                this.logger.debug({debug: 'CashlessService: No BE2 responses.', data: data}, true);
                return;
            }

            be2BasicResp.forEach( element => {
                if (element.functionCode === this.enumBE2Func.Cashless_GetStatus) {
                    switch (element.retvals[0]) {
                        case this.enumCashlessResult.TRANSFER_PENDING: // Transfer is pending
                            this.logger.debug({debug: 'Cashless Transfer Pending', data: ''}, true);
                            break;
                        case this.enumCashlessResult.TRANSFER_SUCCESS:
                            if (this.ejectCard) {
                                //Success status is sent after attempting the card unlock/eject.
                                //Unlock/Eject the player card.
                                const be2FuncReq = NgxCommMsgHelper.createBE2BasicFunctionArr([[this.enumBE2Func.LOLSCH_PatronRequestUnlock, [0, 0, 0, 0]]]);
                                this.ngxComm.sendBe2Message(be2FuncReq, this.processEjectCardResp.bind(this));
                            }
                            else {
                                resp.transferData.status = this.enumCashlessTransferStatus.SUCCESSFUL_OPERATION;
                                this.sendCashlessStatus(resp);
                            }

                            break;
                        case this.enumCashlessResult.TRANSFER_ABORTED:
                            resp.transferData.status = this.enumCashlessTransferStatus.ABORTED_OPERATION;
                            this.sendCashlessStatus(resp);
                            break;
                        case this.enumCashlessResult.TRANSFER_FAILED:
                            resp.transferData.status = this.enumCashlessTransferStatus.FAILED_OPERATION;
                            this.sendCashlessStatus(resp);
                            break;
                        case this.enumCashlessResult.TRANSFER_ABORTED_OPERATION_STARTED:
                            resp.transferData.status = this.enumCashlessTransferStatus.ABORTED_OPERATION_STARTED;
                            this.sendCashlessStatus(resp);
                            break;
                        case this.enumCashlessResult.TRANSFER_FAILED_OPERATION_STARTED:
                            resp.transferData.status = this.enumCashlessTransferStatus.FAILED_OPERATION_STARTED;
                            this.sendCashlessStatus(resp);
                            break;
                        default:
                            this.logger.debug({debug: 'Unknown BE2Response!', data: ''}, true);
                            resp.transferData.status = this.enumCashlessTransferStatus.FAILED_OTHER_REASON;
                            this.sendCashlessStatus(resp);
                            break;
                    }
                } else {
                    this.logger.debug({debug: 'Unknown BE2Response!', data: ''}, true);
                }
            });
        }
    }

    /**
     * Sends the cashless transfer result status and clears the status check interval
     * @param resp
     */
    sendCashlessStatus(resp) {
        // Clear setInterval to avoid further triggers
        window.clearInterval(this.cashlessStatusCheckIntervalID);
        // Send back the data to the requester
        PubSub.publish(this.INITIATE_CASHLESS_TRANSFER_RESP_TOPIC, resp);
    }

    /**
     * Processes BE2 basic function response for the get AutoPlay status [MENU_GetAutoPlayStatus], based on the status
     * a retry may be made, before a result is sent back the the requester.
     * @param err An error object that if defined should describe the error that occurred
     * @param data BE2 basic function response message
     */
    processGetAutoPlayStatusResp(err, data) {
        if (err) {
            this.logger.error(
                {
                    error: 'CashlessService: Received an error in processGetAutoPlayStatusResp.',
                    data: err
                },
                true
            );
        } else {
            let be2BasicResp;

            be2BasicResp = data[NgxCommMsgHelper.BE2_BASIC_RESPONSE];

            if (be2BasicResp.length === 0) {
                this.logger.debug({
                    debug: 'CashlessService: No BE2 responses for processGetAutoPlayStatusResp.',
                    data: data
                }, true);
                return;
            }

            be2BasicResp.forEach( element => {
                if (element.functionCode === this.enumBE2Func.MENU_GetAutoPlayStatus && element.retvals[0] === 0) {
                    // Clear setInterval to avoid further triggers
                    window.clearInterval(this.getAutoPlayStatusCheckIntervalID);

                    //Set the AutoPlay status to disabled
                    const be2req = NgxCommMsgHelper.createBE2BasicFunctionArr([[this.enumBE2Func.MENU_SetAutoPlayStatus, [0, 0, 0, 0]]]);
                    this.ngxComm.sendBe2Message(be2req, (err, data) => {
                        if (err) {
                            this.logger.error({
                                error: 'Cashless Service: Received an error in SetAutoPlayStatus.',
                                data: err
                            }, true);
                            return;
                        }
                        this.logger.debug({
                            debug: 'Cashless Service: Received BE2 response for SetAutoPlayStatus.',
                            data: data
                        }, true);
                    });

                    //Request the transfer
                    const be2req2 = NgxCommMsgHelper.createBE2BasicFunctionArr([[this.enumBE2Func.Cashless_TransferFromGame, [0, parseInt(this.transferAmount, 10), 1, 0]]]);
                    this.ngxComm.sendBe2Message(be2req2, this.processInitiateTransferFromGameResp.bind(this));
                    // Begin Checking the transfer status
                    this.transferStatusCount = 0;
                    this.cashlessStatusCheckIntervalID = window.setInterval(
                        this.checkTransferStatus.bind(this),
                        this.GET_CASHLESS_STATUS_INTERVAL
                    );
                }
            });
        }
    }

    /**
     * Processes BE2 basic function response for the eject player card status (LOLSCH_PatronRequestUnlock), based on the status
     * a retry may be made, before a result is sent back the the requester.
     * @param err An error object that if defined should describe the error that occurred
     * @param data BE2 basic function response message
     */
    processEjectCardResp(err, data) {
        this.logger.debug(
            {
                debug: 'CashlessService: Received BE2 Basic Function response [LOLSCH_PatronRequestUnlock].',
                data: data
            },
            true
        );

        const resp = {
            cmd: 'initiateTransferResp',
            ver: '1.0',
            transferData: {
                status: this.enumCashlessTransferStatus.UNLOCK_FAILED_OTHER_REASON,
                transferAmount: this.transferAmount
            }
        };

        if (err) {
            this.logger.error(
                {
                    error: 'CashlessService: Received an error in processEjectCardResp.',
                    data: err
                },
                true
            );
        } else {
            let be2BasicResp;

            be2BasicResp = data[NgxCommMsgHelper.BE2_BASIC_RESPONSE];

            if (be2BasicResp.length === 0) {
                this.logger.debug({
                    debug: 'CashlessService: No BE2 responses for LOLSCH_PatronRequestUnlock.',
                    data: data
                }, true);
                return;
            }

            be2BasicResp.forEach( element => {
                if (element.functionCode === this.enumBE2Func.LOLSCH_PatronRequestUnlock && element.retvals[0] === 0) {
                    resp.transferData.status = this.enumCashlessTransferStatus.UNLOCK_SUCCESSFUL;
                    this.sendCashlessStatus(resp);
                    this.logger.debug({
                        debug: 'CashlessService: Card unlocked.',
                        data: data
                    }, true);
                }
                else if (element.functionCode === this.enumBE2Func.LOLSCH_PatronRequestUnlock && element.retvals[0] === 1) {
                    resp.transferData.status = this.enumCashlessTransferStatus.CREDITS_EXIST;
                    this.sendCashlessStatus(resp);
                    this.logger.debug({
                        debug: 'CashlessService: Cannot unlock card, credits exist on the machine.',
                        data: data
                    }, true);
                }
                else if (element.functionCode === this.enumBE2Func.LOLSCH_PatronRequestUnlock && element.retvals[0] === 2) {
                    resp.transferData.status = this.enumCashlessTransferStatus.GAME_IN_PROGRESS;
                    this.sendCashlessStatus(resp);
                    this.logger.debug({
                        debug: 'CashlessService: Cannot unlock card, game in progress.',
                        data: data
                    }, true);
                }
            });
        }
    }

    /**
     * This function processes requests to initiate a transfer from the card.
     * @param e Contains the amount in cents for the transfer from the card to be performed.
     */

    initiateTransferFromCard(e) {
        const data = e.detail;
        this.transferAmount = data.transferAmount;
        this.validatePin = data.validatePin;
        let appStoreCache = this.appStore.getAppStoreCache();

        let minToEgm = appStoreCache.playerBonuses.cashless.transferLimits.minToEgm;
        let maxToEgm = appStoreCache.playerBonuses.cashless.transferLimits.maxToEgm;
        let cardCents = appStoreCache.playerBonuses.cashless.balances.cardCents;


        // Card Balance is 0 should not allow a transfer to be initiated, i.e. transfer amount of 0 should never be sent to the BEII.
        if (cardCents <= 0) {
            this.logger.debug({
                debug: 'CashlessService: Invalid transfer request, amount on card is less than or equal to 0.',
                data: data
            }, true);
            PubSub.publish(
                this.INITIATE_CASHLESS_TRANSFER_RESP_TOPIC,
                this.createInitiateTransferInvalidRequestResp(this.enumCashlessTransferStatus.CARD_BALANCE_TOO_LOW)
            );
            return;
        }
        // Transfer amount of 0 should never be sent to the BEII.
        if (parseInt(this.transferAmount, 10) <= 0) {
            this.logger.debug({
                debug: 'CashlessService: Invalid transfer request, transfer amount is less than or equal to 0.',
                data: data
            }, true);
            PubSub.publish(
                this.INITIATE_CASHLESS_TRANSFER_RESP_TOPIC,
                this.createInitiateTransferInvalidRequestResp(this.enumCashlessTransferStatus.REQUESTED_AMOUNT_TO_LOW)
            );
            return;
        }
        const skip = true;
        if (!skip && parseInt(this.transferAmount, 10) > maxToEgm) {
            this.logger.debug({
                debug: 'CashlessService: Invalid transfer request, transfer amount can not be greater than max to egm.',
                data: data
            }, true);
            PubSub.publish(
                this.INITIATE_CASHLESS_TRANSFER_RESP_TOPIC,
                this.createInitiateTransferInvalidRequestResp(this.enumCashlessTransferStatus.REQUESTED_AMOUNT_GREATER_THAN_MAX_TO)
            );
            return;
        }

        // No transfer will be done when Transfer Amount requested is less than the min_to_egm.
        if (parseInt(this.transferAmount, 10) < minToEgm) {
            this.logger.debug({
                debug: 'CashlessService: Invalid transfer request, transfer amount can not be less than min to egm.',
                data: data
            }, true);
            PubSub.publish(
                this.INITIATE_CASHLESS_TRANSFER_RESP_TOPIC,
                this.createInitiateTransferInvalidRequestResp(this.enumCashlessTransferStatus.REQUESTED_AMOUNT_LESS_THAN_MIN_TO)
            );
            return;
        }
        if (appStoreCache.pin.locked) {
            const be2FunctArray = NgxCommMsgHelper.createBE2BasicFunctionArr([
                [NgxCommMsgHelper.enumBE2BasicFunc.PIN_NotEntered, [0, 0, 0, 0]]
            ]);
            this.ngxComm.sendBe2Message(be2FunctArray, this.doNothing.bind(this));
            PubSub.publish(
                this.INITIATE_CASHLESS_TRANSFER_RESP_TOPIC,
                this.createInitiateTransferInvalidRequestResp(this.enumCashlessTransferStatus.PIN_LOCKED)
            );
            return;
        }

        //TODO: this.isAppPinRequired (handle M5 requires pin flag later)
        if ((appStoreCache.pin.check_pin_needed || appStoreCache.pin.force_pin) && this.validatePin === true) {
            this.logger.debug({debug: 'CashlessService: PIN required. Validating PIN.', data: null}, true);
            this.xferDirection = this.enumXferDirection.XFER_FROM_CARD;
            //lets clear all previous subscription for pin validation and start new here
            PubSub.unsubscribe(this.VALIDATE_PIN_RESP_TOPIC, this.handleValidatePINRespFn);
            PubSub.subscribe(this.VALIDATE_PIN_RESP_TOPIC, this.handleValidatePINRespFn);
            PubSub.publish(this.VALIDATE_PIN_TOPIC, this.createValidatePINReq());
            return;
        }
        // Subscribe to PIN validation response status from PIN service.
        this.completeXferFromCard();
    }

    completeXferFromCard(){

        this.logger.debug(
            {
                debug: 'CashlessService: Sending BE2 Basic Function request ' +
                '[Cashless_TransferFromCard].',
                data: ''
            },
            true
        );

        this.ejectCard = false;

        const cashlessArray = [
            [this.enumBE2Func.Cashless_TransferFromCard, [0, parseInt(this.transferAmount, 10), 1, 0]]
        ];

        const be2req = NgxCommMsgHelper.createBE2BasicFunctionArr(cashlessArray);
        this.ngxComm.sendBe2Message(be2req, this.processInitiateTransferFromCardResp.bind(this));
        // Begin Checking the transfer status
        this.transferStatusCount = 0;
        this.cashlessStatusCheckIntervalID = window.setInterval(
            this.checkTransferStatus.bind(this),
            this.GET_CASHLESS_STATUS_INTERVAL
        );
    }

    /**
     * Processes BE2 basic function response for initiation of a transfer from the card.
     * @param err An error object that if defined should describe the error that occurred
     * @param data BE2 basic function response message
     */

    processInitiateTransferFromCardResp(err, data) {
        if (err) {
            this.logger.error(
                {
                    error: 'CashlessService: Received an error in ' +
                    'processInitiateTransferFromCardResp.',
                    data: err
                },
                true
            );
        } else {
            let be2BasicResp;

            be2BasicResp = data[NgxCommMsgHelper.BE2_BASIC_RESPONSE];

            if (be2BasicResp.length === 0) {
                this.logger.debug({debug: 'CashlessService: No BE2 responses.', data: data}, true);
                return;
            }

            be2BasicResp.forEach( element => {
                if (element.functionCode === this.enumBE2Func.Cashless_TransferFromCard) {
                    this.logger.debug(
                        {
                            debug: 'CashlessService: Received Response to ' +
                            'Cashless_TransferFromCard',
                            data: data
                        },
                        true
                    );
                } else {
                    this.logger.debug({debug: 'Unknown BE2Response!', data: ''}, true);
                }
            });
        }
    }

    createValidatePINReq() {
        return {
            cmd: 'validatePIN',
            ver: '1.0'
        };
    }
    // Public Functions
    /**
     * Function to register Cashless Enabled response callback.
     * The callback will be invoked when Cashless Service publishes on
     * CHECK_CASHLESS_ENABLED_RESP_TOPIC.
     *
     * @param callback function to be called when CashlessEnabled result is available.
     **/
    registerCashlessEnabledRespCB(callback) {
        this.checkCashlessEnabledSubscribersResp.push(callback);
        PubSub.subscribe(this.CHECK_CASHLESS_ENABLED_RESP_TOPIC, callback);
    }

    /**
     * Function to register Cashless Balance response callback.
     * The callback will be invoked when Cashless Service publishes on
     * GET_CASHLESS_BALANCE_RESP_TOPIC.
     *
     * @param callback function to be called when CashlessBalance result is available.
     **/
    registerGetCashlessBalanceRespCB(callback) {
        this.getCashlessBalanceSubscribersResp.push(callback);
        PubSub.subscribe(this.GET_CASHLESS_BALANCE_RESP_TOPIC, callback);
    }

    /**
     * Function to register Cashless Transfer Result response callback.
     * The callback will be invoked when Cashless Service publishes on
     * INITIATE_CASHLESS_TRANSFER_RESP_TOPIC.
     *
     * @param callback function to be called when Cashless Transfer result is available.
     **/
    registerInitiateTransferRespCB(callback) {
        this.getInitiateTransferSubscribersResp.push(callback);
        PubSub.subscribe(this.INITIATE_CASHLESS_TRANSFER_RESP_TOPIC, callback);
    }

    /**
     * Cleanup subscriptions. Typically should be called in Unit tests before CashlessService
     * instance is destroyed.
     */
    cleanup() {
        // Un-subscribe all relevant amplify subscriptions
        if (this.checkCashlessEnabledSubscribersResp) {
            this.checkCashlessEnabledSubscribersResp.forEach( item => {
                PubSub.unsubscribe(this.CHECK_CASHLESS_ENABLED_RESP_TOPIC, item);
            });
        }

        if (this.getCashlessBalanceSubscribersResp) {
            this.getCashlessBalanceSubscribersResp.forEach( item => {
                PubSub.unsubscribe(this.GET_CASHLESS_BALANCE_RESP_TOPIC, item);
            });
        }

        if (this.getInitiateTransferSubscribersResp) {
            this.getInitiateTransferSubscribersResp.forEach( item => {
                PubSub.unsubscribe(this.INITIATE_CASHLESS_TRANSFER_RESP_TOPIC, item);
            });
        }
        if (this.checkCashlessEnabledSubscribers) {
            this.checkCashlessEnabledSubscribers.forEach((item) => {
                PubSub.unsubscribe(this.CHECK_CASHLESS_ENABLED_TOPIC, item);
            });
        }
        if (this.getCashlessBalanceSubscribers) {
            this.getCashlessBalanceSubscribers.forEach((item) => {
                PubSub.unsubscribe(this.GET_CASHLESS_BALANCE_TOPIC, item);
            });
        }
        if (this.getInitiateTransferFromSubscribers) {
            this.getInitiateTransferFromSubscribers.forEach((item) => {
                PubSub.unsubscribe(this.INITIATE_CASHLESS_TRANSFER_FROM_GAME_TOPIC, item);
            });
        }
        if (this.getInitiateTransferToSubscribers) {
            this.getInitiateTransferToSubscribers.forEach((item) => {
                PubSub.unsubscribe(this.INITIATE_CASHLESS_TRANSFER_FROM_CARD_TOPIC, item);
            });
        }
        this.logger.debug({debug: 'CashlessService: Cleaning up.'}, true);
    }
}
