'use strict';

import PubSub from '../PubSub';
import screenIds from "igt-nexgen-codes/lib/screenIds";

export default class CardlessService {
    constructor(ngxComm, logger, m5Config, appStore, startSubscriptions) {
        this.ngxComm = ngxComm;
        this.logger = logger;
        this.m5Config = m5Config;
        this.appStore = appStore;
        this.startSubscriptions = startSubscriptions==true?true: false;
        this.cardlessTimerEvent = 60;
        this.checkCardlessCacheTime = 30000;
        this.lastDisconnectReason = null;
        
        this.clearStateTime = m5Config.cardless.clearStateTimeInSecs || 60;
        if(this.m5Config && this.m5Config.cardless && this.m5Config.cardless.checkCardlessCacheTimeInSecs ) {
            this.checkCardlessCacheTime = this.m5Config.cardless.checkCardlessCacheTimeInSecs * 1000;
        }

        this.CARDLESS_STATUS_UPDATE_TOPIC = 'cardlessStatusUpdate';
        this.cardlessStatusUpdateSubscribers = [];

        this.lastReceivedTime = new Date().getTime();

        this.cardlessStatusHistory = []

        this.enumCardlessStatus = {
            OPEN: "open",
            CONNECTED: "connected",
            DISCONNECTED: "disconnected",
            CLOSED: "closed"
        };



        const checkCardlessCache = () => {
            var now = new Date().getTime();
            let currentScreenTrigger = this.appStore.getCurrentScreenTrigger();

            if(currentScreenTrigger === screenIds.IDLE_SCREEN && this.cardlessStatusHistory[0] && this.cardlessStatusHistory[0].interface) {
                var diff = ((now - this.lastReceivedTime) / 1000);
                if (diff > this.m5Config.cardless.clearStateTimeInSecs) {
                    this.logger.debug({debug: 'CardlessService: checkCardlessCache - Clear Cardless Cache, last cardless data received in  : ' + diff + ' secs'}, true);
                    this.cardlessStatusHistory.length = 0;
                }
            }
        }

        if(this.startSubscriptions){
            this.setupCardlessAdvSubscription();
            this.cardlessTimerEvent = setInterval(checkCardlessCache, this.checkCardlessCacheTime)
        }
    }


    formatEvent(event) {
        return (event && event instanceof CustomEvent) ? event.detail : event;
    }

    setupCardlessAdvSubscription() {
        this.ngxComm.registerCardlessStatus(this.handleCardlessData.bind(this));
    }

    handleCardlessData(event) {
        this.lastReceivedTime = new Date().getTime();
        const resp = {
            cmd: 'cardlessStatusUpdate',
            ver: '1.0',
            cardlessStatusUpdate : null
        };

        const data = this.formatEvent(event);
        this.logger.debug({debug: 'CardlessService:handleCardlessData - Received Cardless Data', data: data}, true);
        if(data && data.cardlessStatus) {            
            if(data.cardlessStatus.status === this.enumCardlessStatus.OPEN) {
                this.cardlessStatusHistory.length = 0;
            }

            //add timestamp for content to filter out some data
            data.cardlessStatus.timestamp = this.lastReceivedTime;
            this.cardlessStatusHistory.unshift(data.cardlessStatus);
            if(this.cardlessStatusHistory.length > 30){
                this.cardlessStatusHistory.pop();
            }
            resp.cardlessStatusUpdate =  this.cardlessStatusHistory[0];

            if(data.cardlessStatus.status === this.enumCardlessStatus.DISCONNECTED) {
                this.lastDisconnectReason = data.cardlessStatus.disconnectReason;
            }
            if(this.lastDisconnectReason){
                this.cardlessStatusHistory.forEach(el=>{
                    el.lastDisconnectReason = this.lastDisconnectReason;
                })
            }

            this.sendCardlessStatusUpdate(resp);
        }
    }

    getCardlessStatus() {
        return this.cardlessStatusHistory.length > 0 ? this.cardlessStatusHistory[0] : null;
    }

    getCardlessStatusHistory() {
        return this.cardlessStatusHistory || [];
    }


    sendCardlessStatusUpdate(resp) {
        PubSub.publish(this.CARDLESS_STATUS_UPDATE_TOPIC, resp);
    }

    registerCardlessStatusUpdateCB(callback) {
        this.cardlessStatusUpdateSubscribers.push(callback);
        PubSub.subscribe(this.CARDLESS_STATUS_UPDATE_TOPIC, callback);
    }


    cleanup() {
        if (this.cardlessStatusUpdateSubscribers) {
            this.cardlessStatusUpdateSubscribers.forEach( item => {
                PubSub.unsubscribe(this.CARDLESS_STATUS_UPDATE_TOPIC, item);
            });
        }
        this.cardlessStatusHistory.length = 0;
        clearInterval( this.cardlessTimerEvent);
        this.logger.debug({debug: 'CardlessService: Cleaning up.'}, true);
    }
}