import NgxCommMsgHelper from '../NgxCommMsgHelper';
import PubSub from '../PubSub';
'use strict';
/**
 * XCService contains Xtra Credit related work-flows between apps and nexgen.
 * @param ngxComm {object} Comm Interface service instance
 * @param logger {object} Logger service instance
 * @constructor
 */
export default class XCService {
    constructor(ngxComm, logger , appStore, statusMessageCache) {
        this.ngxComm = ngxComm;
        this.logger = logger;
        this.appStore = appStore;
        this.statusMessageCache = statusMessageCache;
        this.appStoreCache = null;

        // Constants
        // Subscribed to by XC service to initiate check XC enabled flow
        this.CHECK_XC_ENABLED_TOPIC = 'enableXtraCredit';
        // XC service publishes on this to respond to check XC enabled request
        this.CHECK_XC_ENABLED_RESP_TOPIC = 'enableXtraCreditResp';
        // Subscribed to by XC service to check if XC already active
        this.IS_XC_ACTIVE_TOPIC = 'isXtraCreditActive';
        // XC service publishes on this to respond to check XC already active request
        this.IS_XC_ACTIVE_RESP_TOPIC = 'isXtraCreditActiveResp';
        // Subscribed to by XC service to activate XC flow
        this.ACTIVATE_XC_TOPIC = 'activateXtraCredit';
        // Subscribed to by XC service to activate XC with Amount flow
        this.ACTIVATE_XC_WITH_AMOUNT_TOPIC = 'activateXtraCreditWithAmount';
        // XC service publishes the response to activate XC request
        this.ACTIVATE_XC_RESP_TOPIC = 'activateXtraCreditResp';
        // Subscribed to by XC service to cancel XC flow
        this.CANCEL_XC_TOPIC = 'cancelXtraCredit';
        // XC service publishes the response to cancel XC request
        this.CANCEL_XC_RESP_TOPIC = 'cancelXtraCreditResp';

        this.enumActivateXCStatus = {
            XC_ACTIVE: 0,
            XC_ACTIVE_NOT_CANCELABLE: 1,
            XC_ALREADY_ACTIVE: 2,
            XC_INACTIVE_BALANCE_TOO_LOW: 3,
            XC_INACTIVE_NOT_AVAILABLE_ON_DEVICE: 4,
            XC_INACTIVE_SEE_PLAYERS_CLUB: 5,
            XC_INACTIVE_DUPLICATE_CARD: 6,
            XC_INACTIVE_ABANDONED_CARD: 7,
            XC_INACTIVE_PIN_LOCKED: 8,
            XC_INACTIVE_BONUSING_UNAVAILABLE: 9,
            XC_INACTIVE_PIN_VALIDATION_FAILED: 10,
            XC_INACTIVE_INPUT_AMOUNT_INVALID:11,
            XC_INACTIVE_OVER_LIMIT:12,
            XC_INACTIVE_AMOUNT_LESS_THAN_DENOM:13,
            XC_INACTIVE_LAST_IN_PROCESSING:14,
            XC_INACTIVE_ABORTED_E: 15,
            XC_INACTIVE_FAILED_E: 16,
            XC_INACTIVE_RETRIED_OUT: 17
        };

        this.enumTransferXCStatus = {            
            PENDING: 0,
            SUCCESS: 1,
            ABORTED: 2,
            FAILED: 3,
            IN_PROGRESS: 4,
            ABORTED_E: 5,
            FAILED_E: 6,
            OVER_LIMIT: 255
        };

        this.enumPINStatus = {
            VALID: 1,
            INVALID: 2,
            LOCKED: 3,
            NO_RESPONSE: 4,
            NOT_ENTERED: 5
        };

        this.enumCancelXCStatus = {
            XC_CANCELLED: 0,
            XC_NOT_CANCELABLE: 1,
            XC_NOT_CANCELLED_IN_GAME_CYCLE: 2
        };

        this.amountInput = 0;
        this.isAtiveWithAmount = false;
        this.isAppPinRequired = false;

        // Private vars
        this.VALIDATE_PIN_TOPIC = 'validatePIN';
        this.VALIDATE_PIN_RESP_TOPIC = 'validatePINResp';

        // Properties
        // Keeping track of subscribers for clean up. This is needed when each unit test creates a new
        // instance of XC service
        this.checkIsActiveXCRespSubscribers = [];
        this.checkXCEnabledReqSubscribers = [];
        this.checkXCEnabledRespSubscribers = [];
        this.activateXCReqSubscribers = [];
        this.activateXCRespSubscribers = [];
        this.cancelXCReqSubscribers = [];
        this.cancelXCRespSubscribers = [];
        this.handleValidatePINRespSubscribers = [];

        // Subscribe to pubSub topics to receive relevant messages
        let checkXCEnabledFn  = this.checkXCEnabled.bind(this);
        this.checkXCEnabledReqSubscribers.push(checkXCEnabledFn);
        PubSub.subscribe(this.CHECK_XC_ENABLED_TOPIC, checkXCEnabledFn);

        let activateXCFn = this.activateXC.bind(this);
        this.activateXCReqSubscribers.push(activateXCFn);
        PubSub.subscribe(this.ACTIVATE_XC_TOPIC, activateXCFn);

        let activateXCWithAmountFn = this.activateXCWithAmount.bind(this);
        this.activateXCReqSubscribers.push(activateXCWithAmountFn);
        PubSub.subscribe(this.ACTIVATE_XC_WITH_AMOUNT_TOPIC, activateXCWithAmountFn);

        let cancelXCFn = this.cancelXC.bind(this);
        this.cancelXCReqSubscribers.push(cancelXCFn);
        PubSub.subscribe(this.CANCEL_XC_TOPIC, cancelXCFn);

        let isAlreadyActiveXCFn = this.isAlreadyActiveXC.bind(this);
        this.checkIsActiveXCRespSubscribers.push(isAlreadyActiveXCFn);
        PubSub.subscribe(this.IS_XC_ACTIVE_TOPIC, isAlreadyActiveXCFn);

        //Will be subscribed only if XC determines pin is required.
        this.handleValidatePINRespFn = this.handleValidatePINResp.bind(this);

    }

    // Private Functions

    isAlreadyActiveXC(){
        this.processIsAlreadyActiveXC();
    }

    processIsAlreadyActiveXC(){
        let xcActiveRespMsg = this.createXCResp(this.ACTIVATE_XC_RESP_TOPIC, this.enumActivateXCStatus.XC_INACTIVE_SEE_PLAYERS_CLUB, this.statusMessageCache.xtraCredit.activate[this.enumActivateXCStatus.XC_INACTIVE_SEE_PLAYERS_CLUB]);
        const isXCAlreadyActiveResp = {cmd: this.IS_XC_ACTIVE_RESP_TOPIC, xtraCreditActive: false, xtraCreditRespMsg: xcActiveRespMsg};
        this.appStoreCache = this.appStore.getAppStoreCache();
        if(this.appStoreCache.playerBonuses.xtraCredit.active) {
            xcActiveRespMsg.status =  this.enumActivateXCStatus.XC_ALREADY_ACTIVE;
            xcActiveRespMsg.statusMessage =  this.statusMessageCache.xtraCredit.activate[this.enumActivateXCStatus.XC_ALREADY_ACTIVE];
            isXCAlreadyActiveResp.xtraCreditActive = true;
            isXCAlreadyActiveResp.xtraCreditRespMsg = xcActiveRespMsg;
            PubSub.publish(this.IS_XC_ACTIVE_RESP_TOPIC, isXCAlreadyActiveResp);
        }
        else {
            PubSub.publish(this.IS_XC_ACTIVE_RESP_TOPIC, isXCAlreadyActiveResp);
        }
    }

    checkXCEnabled() {
        this.processCheckXCEnabled();
    }

    processCheckXCEnabled(){
        this.appStoreCache = this.appStore.getAppStoreCache();
        if(this.appStoreCache.playerBonuses.xtraCredit.enable){
            PubSub.publish(this.CHECK_XC_ENABLED_RESP_TOPIC,this.createXCEnabledResp(this.CHECK_XC_ENABLED_RESP_TOPIC, true));
        }
        else {
            PubSub.publish(this.CHECK_XC_ENABLED_RESP_TOPIC,this.createXCEnabledResp(this.CHECK_XC_ENABLED_RESP_TOPIC, false));
        }
    }

    /**
     * This function handles requests to activate XC
     * @param e Contains a flag to indicate if PIN validation is required
     */
    activateXC(e) {
        const data = e.detail;
        if (data.validatePin === undefined) {
            this.logger.error({error: 'XCService: Received Activate XC request without validatePin parameter.', data: data}, true);
        } else {
            this.isAppPinRequired = data.validatePin;
            this.processActivateXC();
        }
    }

    /**
     * This function handles requests to activate XC with amount
     * @param e Contains a flag to indicate if PIN validation is required
     */
    activateXCWithAmount(e) {
        const data = e.detail;
        this.isAppPinRequired = data.validatePin;
        this.isAtiveWithAmount = true;
        this.amountInput = data.amount;
        this.logger.info({ info: 'XCService: Received Active XC With Amount:', data: data }, true);
        this.processActivateXC(true, data.amount);        
    }
    

    processActivateXC(isSpecificAmount = false,amount = 0) {
        this.logger.debug({ info: 'XCService: process Activate XC,amount:', data: amount }, true)
        let isAbandonedCard = false;
        let isDuplicateCard = false;
        let resp;
        let validXCPreConditions = false;

        this.appStoreCache = this.appStore.getAppStoreCache();

        if(this.appStoreCache.player_info.dupCard !== null && this.appStoreCache.player_info.dupCard === true){
            isDuplicateCard = true;
        }

        if(this.appStoreCache.player_info.isAbandoned !== null && this.appStoreCache.player_info.isAbandoned === true){
            isAbandonedCard = true;
        }

        resp = this.createXCResp(this.ACTIVATE_XC_RESP_TOPIC, this.enumActivateXCStatus.XC_INACTIVE_SEE_PLAYERS_CLUB, this.statusMessageCache.xtraCredit.activate[this.enumActivateXCStatus.XC_INACTIVE_SEE_PLAYERS_CLUB]);

        if(isDuplicateCard){
            resp.status = this.enumActivateXCStatus.XC_INACTIVE_DUPLICATE_CARD;
            resp.statusMessage = this.statusMessageCache.xtraCredit.activate[this.enumActivateXCStatus.XC_INACTIVE_DUPLICATE_CARD];
        }
        else if(isAbandonedCard){
            resp.status = this.enumActivateXCStatus.XC_INACTIVE_ABANDONED_CARD;
            resp.statusMessage = this.statusMessageCache.xtraCredit.activate[this.enumActivateXCStatus.XC_INACTIVE_ABANDONED_CARD];
        }
        else if(!this.appStoreCache.builtPatronDataCache){
            resp.status = this.enumActivateXCStatus.XC_INACTIVE_BONUSING_UNAVAILABLE;
            resp.statusMessage = this.statusMessageCache.xtraCredit.activate[this.enumActivateXCStatus.XC_INACTIVE_BONUSING_UNAVAILABLE];
        }
        else if(!this.appStoreCache.machine.xcEnable){
            resp.status = this.enumActivateXCStatus.XC_INACTIVE_NOT_AVAILABLE_ON_DEVICE;
            resp.statusMessage = this.statusMessageCache.xtraCredit.activate[this.enumActivateXCStatus.XC_INACTIVE_NOT_AVAILABLE_ON_DEVICE];
        }
        else if(!this.appStoreCache.player_info.xcEnable || !this.appStoreCache.playerBonuses.xtraCredit.enable){
            resp.status = this.enumActivateXCStatus.XC_INACTIVE_SEE_PLAYERS_CLUB;
            resp.statusMessage = this.statusMessageCache.xtraCredit.activate[this.enumActivateXCStatus.XC_INACTIVE_SEE_PLAYERS_CLUB];
        }
        //when using activateXtraCreditWithAmount, it could transfer several times before available XC is zero
        else if(!isSpecificAmount && this.appStoreCache.playerBonuses.xtraCredit.active){
            resp.status = this.enumActivateXCStatus.XC_ALREADY_ACTIVE;
            resp.statusMessage = this.statusMessageCache.xtraCredit.activate[this.enumActivateXCStatus.XC_ALREADY_ACTIVE];
        }
        else if(this.appStoreCache.playerBonuses.xtraCredit.total_balance <= 0 && this.appStoreCache.playerBonuses.xtraCredit.inActiveBalance <=0){
            resp.status = this.enumActivateXCStatus.XC_INACTIVE_BALANCE_TOO_LOW;
            resp.statusMessage = this.statusMessageCache.xtraCredit.activate[this.enumActivateXCStatus.XC_INACTIVE_BALANCE_TOO_LOW];
        }
        else {
            validXCPreConditions = true;
        }

        if(!validXCPreConditions) {
            PubSub.publish(this.ACTIVATE_XC_RESP_TOPIC, resp);
            return;
        }
        if (this.appStoreCache.pin.locked) {
            const be2FunctArray = NgxCommMsgHelper.createBE2BasicFunctionArr([
                [NgxCommMsgHelper.enumBE2BasicFunc.PIN_NotEntered, [0, 0, 0, 0]]
            ]);
            this.ngxComm.sendBe2Message(be2FunctArray, this.doNothing.bind(this));
            resp.status = this.enumActivateXCStatus.XC_INACTIVE_PIN_LOCKED;
            resp.statusMessage = this.statusMessageCache.xtraCredit.activate[this.enumActivateXCStatus.XC_INACTIVE_PIN_LOCKED];
            PubSub.publish(this.ACTIVATE_XC_RESP_TOPIC, resp);
            return;
        }

        //TODO: this.isAppPinRequired (handle M5 requires pin flag later)
        if (this.appStoreCache.pin.check_pin_needed || this.appStoreCache.pin.force_pin) {
            this.logger.info({info: 'XCService: PIN required. Validating PIN.', data: null}, true);
            //lets clear all previous subscription for pin validation and start new here
            PubSub.unsubscribe(this.VALIDATE_PIN_RESP_TOPIC, this.handleValidatePINRespFn);
            PubSub.subscribe(this.VALIDATE_PIN_RESP_TOPIC, this.handleValidatePINRespFn);

            PubSub.publish(this.VALIDATE_PIN_TOPIC, this.createValidatePINReq());
            return;
        }

        this.sendActivateXCResp(isSpecificAmount,amount);
    }

    doNothing(err, data) {
        if (err) {
            this.logError('doNothing', err);
            return;
        }
    }

    /**
     * Does polling of status after sending Cancel XC BE2 msg.
     */
    doStatusPolling() {
        const code = NgxCommMsgHelper.enumBE2BasicFunc.CancelXtraCredit;
        const be2Msg = NgxCommMsgHelper.createBE2BasicFunctionArr([[code, [0, 0, 0, 0]]]);

        let hasProcessResp = false;
        let counter = 0;
        let handle;

        const callbackPolling = () => {
            ++counter;

            // Do polling of 30 tries for cancel status.
            if (counter > 30 ) {
                clearInterval(handle);
                PubSub.publish(this.CANCEL_XC_RESP_TOPIC,this.createXCResp(this.CANCEL_XC_RESP_TOPIC,this.enumCancelXCStatus.XC_NOT_CANCELLED_IN_GAME_CYCLE, this.statusMessageCache.xtraCredit.cancel[this.enumCancelXCStatus.XC_NOT_CANCELLED_IN_GAME_CYCLE]));

                return;
            }

            const handleStatusResp = (err, data) => {
                const be2BasicResp = data[NgxCommMsgHelper.BE2_BASIC_RESPONSE];
                const args = be2BasicResp[0].retvals;

                if (args[0] === 0) {
                    this.logger.debug({debug: 'XCService: Xtra Credit cancelled from status polling.', data: ''}, true);

                    clearInterval(handle);
                    PubSub.publish(this.CANCEL_XC_RESP_TOPIC,this.createXCResp(this.CANCEL_XC_RESP_TOPIC,this.enumCancelXCStatus.XC_CANCELLED,this.statusMessageCache.xtraCredit.cancel[this.enumCancelXCStatus.XC_CANCELLED]));
                    return;
                }

                if (args[0] === 1) {
                    this.logger.debug({debug: 'XCService: Xtra Credit not cancelable, in game cycle from ' + 'status polling.', data: ''}, true);

                    clearInterval(handle);
                    PubSub.publish(this.CANCEL_XC_RESP_TOPIC, this.createXCResp(this.CANCEL_XC_RESP_TOPIC, this.enumCancelXCStatus.XC_NOT_CANCELABLE,this.statusMessageCache.xtraCredit.cancel[this.enumCancelXCStatus.XC_NOT_CANCELABLE]));
                    return;
                }

                hasProcessResp = true;
            };

            // First time run or send it again if it has process the response be2 msg
            // and the status coming back is not what expected.
            if (counter === 1 || hasProcessResp) {
                hasProcessResp = false;
                this.ngxComm.sendBe2Message(be2Msg, handleStatusResp.bind(this));
            }
        };

        handle = setInterval(callbackPolling, 1000);
    }

    doCashlessStatusPolling() {
        const code = NgxCommMsgHelper.enumBE2BasicFunc.Cashless_GetStatus;
        const be2Msg = NgxCommMsgHelper.createBE2BasicFunctionArr([[code, [0, 0, 0, 0]]]);

        let hasProcessResp = false;
        let counter = 0;
        let handle;

        const callbackPolling = () => {
            ++counter;

            // Do polling of 30 tries for Cashless_GetStatus.
            if (counter > 30) {
                clearInterval(handle);
                PubSub.publish(this.ACTIVATE_XC_RESP_TOPIC, this.createXCResp(this.ACTIVATE_XC_RESP_TOPIC, this.enumActivateXCStatus.XC_INACTIVE_RETRIED_OUT, this.statusMessageCache.xtraCredit.activate[this.enumActivateXCStatus.XC_INACTIVE_RETRIED_OUT]));

                return;
            }

            const handleStatusResp = (err, data) => {
                const be2BasicResp = data[NgxCommMsgHelper.BE2_BASIC_RESPONSE];
                const args = be2BasicResp[0].retvals;

                if (args[0] === this.enumTransferXCStatus.PENDING) { 
                    this.logger.debug({ debug: 'XCService: It is pending, still need to poll status by Cashless_GetStatus', data: '' }, true);
                    hasProcessResp = true;
                }
                else if (args[0] === this.enumTransferXCStatus.SUCCESS) {
                    this.logger.debug({ debug: 'XCService: Xtra Credit is successfully activated.' + ' by CashlessStatus Polling.', data: '' }, true);

                    clearInterval(handle);
                    this.checkCancellableResp();    
                    return;
                }
                else if (args[0] === this.enumTransferXCStatus.ABORTED) {
                    this.logger.debug({ debug: 'XCService: Fail to activate Xtra Credit, Reason: Input Amount is less than machine denom.' + ' by CashlessStatus Polling.', data: '' }, true);

                    clearInterval(handle);
                    PubSub.publish(this.ACTIVATE_XC_RESP_TOPIC, this.createXCResp(this.ACTIVATE_XC_RESP_TOPIC, this.enumActivateXCStatus.XC_INACTIVE_AMOUNT_LESS_THAN_DENOM, this.statusMessageCache.xtraCredit.activate[this.enumActivateXCStatus.XC_INACTIVE_AMOUNT_LESS_THAN_DENOM]));
                    return;
                }
                else if (args[0] === this.enumTransferXCStatus.IN_PROGRESS) {
                    this.logger.debug({ debug: 'XCService: Fail to activate Xtra Credit, Reason: Last one is still in processing.' + ' by CashlessStatus Polling.', data: '' }, true);

                    clearInterval(handle);
                    PubSub.publish(this.ACTIVATE_XC_RESP_TOPIC, this.createXCResp(this.ACTIVATE_XC_RESP_TOPIC, this.enumActivateXCStatus.XC_INACTIVE_LAST_IN_PROCESSING, this.statusMessageCache.xtraCredit.activate[this.enumActivateXCStatus.XC_INACTIVE_LAST_IN_PROCESSING]));
                    return;
                }
                else  if (args[0] === this.enumTransferXCStatus.OVER_LIMIT) { 
                    this.logger.debug({ debug: 'Xtra Credit can not be activated by over limit', data: '' }, true);

                    clearInterval(handle);
                    PubSub.publish(this.ACTIVATE_XC_RESP_TOPIC, this.createXCResp(this.ACTIVATE_XC_RESP_TOPIC, this.enumActivateXCStatus.XC_INACTIVE_OVER_LIMIT, this.statusMessageCache.xtraCredit.activate[this.enumActivateXCStatus.XC_INACTIVE_OVER_LIMIT]));
                    return;
                }
                else  if (args[0] === this.enumTransferXCStatus.ABORTED_E) { 
                    this.logger.debug({ debug: 'Xtra Credit can not be activated by aborted_e', data: '' }, true);

                    clearInterval(handle);
                    PubSub.publish(this.ACTIVATE_XC_RESP_TOPIC, this.createXCResp(this.ACTIVATE_XC_RESP_TOPIC, this.enumActivateXCStatus.XC_INACTIVE_ABORTED_E, this.statusMessageCache.xtraCredit.activate[this.enumActivateXCStatus.XC_INACTIVE_ABORTED_E]));
                    return;
                }
                else  if (args[0] === this.enumTransferXCStatus.FAILED_E) { 
                    this.logger.debug({ debug: 'Xtra Credit can not be activated by failed_e', data: '' }, true);

                    clearInterval(handle);
                    PubSub.publish(this.ACTIVATE_XC_RESP_TOPIC, this.createXCResp(this.ACTIVATE_XC_RESP_TOPIC, this.enumActivateXCStatus.XC_INACTIVE_FAILED_E, this.statusMessageCache.xtraCredit.activate[this.enumActivateXCStatus.XC_INACTIVE_FAILED_E]));
                    return;
                }                 
                else {
                    this.logger.debug({ debug: 'XCService: Xtra Credit can not be activated ' + ' by CashlessStatus Polling.', data: '' }, true);

                    clearInterval(handle);
                    PubSub.publish(this.ACTIVATE_XC_RESP_TOPIC, this.createXCResp(this.ACTIVATE_XC_RESP_TOPIC, this.enumActivateXCStatus.XC_INACTIVE_SEE_PLAYERS_CLUB, this.statusMessageCache.xtraCredit.activate[this.enumActivateXCStatus.XC_INACTIVE_SEE_PLAYERS_CLUB]));
                    return;
                }               
                
            };

            // First time run or send it again if it has process the response be2 msg
            // and the status coming back is not what expected.
            if (counter === 1 || hasProcessResp) {
                hasProcessResp = false;
                this.ngxComm.sendBe2Message(be2Msg, handleStatusResp.bind(this));
            }
        };

        handle = setInterval(callbackPolling, 1000);
    }

    /**
     * This function handles requests to cancel XC
     */
    cancelXC() {
        this.logger.debug(
            {debug: 'XCService: Sending BE2 Basic Function request CancelXtraCredit.', data: ''},
            true
        );
        const code = NgxCommMsgHelper.enumBE2BasicFunc.CancelXtraCredit;
        const message = NgxCommMsgHelper.createBE2BasicFunctionArr([[code, [0, 0, 0, 0]]]);
        this.ngxComm.sendBe2Message(message, this.processCancelXC.bind(this));
    }

    processCancelXC(err, data){

        let be2BasicResp;
        be2BasicResp = data[NgxCommMsgHelper.BE2_BASIC_RESPONSE];
        if (be2BasicResp.length === 0) {
            this.logger.debug({debug: 'XCService: No BE2 responses.', data: data}, true);
            return;
        }

        be2BasicResp.forEach((element) => {
            switch (element.functionCode) {
                case NgxCommMsgHelper.enumBE2BasicFunc.CancelXtraCredit:
                    // Known issue in NextGen device when we get "Game in cycle status" coming back,
                    // have to do polling for status till we get a successful cancel status.
                    if (element.retvals[0] === 2) {
                        this.doStatusPolling();
                    } else {
                        if (element.retvals[0] === 0) {
                            this.logger.debug({debug: 'XCService: Xtra Credit cancelled.', data: ''}, true);
                            PubSub.publish(this.CANCEL_XC_RESP_TOPIC, this.createXCResp(this.CANCEL_XC_RESP_TOPIC, this.enumCancelXCStatus.XC_CANCELLED,this.statusMessageCache.xtraCredit.cancel[this.enumCancelXCStatus.XC_CANCELLED])
                            );
                        } else if (element.retvals[0] === 1) {
                            // args[0] === 1 Should not happen, as if XC is not cancelable, content will get
                            // that information in activateXtraCreditResp with status 2 and should not display
                            // a Cancel XC option.
                            this.logger.debug({debug: 'XCService: Xtra Credit not cancelable, in game cycle.', data: ''}, true);
                            PubSub.publish(this.CANCEL_XC_RESP_TOPIC, this.createXCResp(this.CANCEL_XC_RESP_TOPIC, this.enumCancelXCStatus.XC_NOT_CANCELABLE,this.statusMessageCache.xtraCredit.cancel[this.enumCancelXCStatus.XC_NOT_CANCELABLE]));
                        }
                    }
                    break;
                default:
                    this.logger.debug({debug: 'XCService: Unknown BE2Response!', data: data}, true);
                    break;
            }
        });
    }

    processTransferXC(err, data) {
        let be2BasicResp;
        be2BasicResp = data[NgxCommMsgHelper.BE2_BASIC_RESPONSE];
        if (be2BasicResp.length === 0) {
            this.logger.debug({ debug: 'XCService: No BE2 responses.', data: data }, true);
            return;
        }

        be2BasicResp.forEach((element) => {
            switch (element.functionCode) {
                case NgxCommMsgHelper.enumBE2BasicFunc.TransferXtraCredit:   
                    
                    //get success from be2
                    let retValue = element.retvals[0];
                    if (retValue === this.enumTransferXCStatus.SUCCESS) {
                        this.logger.debug({ debug: 'XCService: Xtra Credit is successfully activated.', data: '' }, true);
                        this.checkCancellableResp();
                        return;                        
                    }
                    else if (retValue === this.enumTransferXCStatus.PENDING) {
                        this.logger.debug({ debug: 'XCService: Pending status, need to poll status by Cashless_GetStatus till getting result.', data:''},true);
                        this.doCashlessStatusPolling();
                    }
                    else if (retValue === this.enumTransferXCStatus.ABORTED) {
                        this.logger.debug({ debug: 'XCService: Fail to activate Xtra Credit, Reason: Input Amount is less than machine denom.' + ' by CashlessStatus Polling.', data: '' }, true);
                        PubSub.publish(this.ACTIVATE_XC_RESP_TOPIC, this.createXCResp(this.ACTIVATE_XC_RESP_TOPIC, this.enumActivateXCStatus.XC_INACTIVE_AMOUNT_LESS_THAN_DENOM, this.statusMessageCache.xtraCredit.activate[this.enumActivateXCStatus.XC_INACTIVE_AMOUNT_LESS_THAN_DENOM]));
                        return;
                    }
                    else if (retValue === this.enumTransferXCStatus.IN_PROGRESS) {
                        this.logger.debug({ debug: 'XCService: Fail to activate Xtra Credit, Reason: Last one is still in processing.' + ' by CashlessStatus Polling.', data: '' }, true);
                        PubSub.publish(this.ACTIVATE_XC_RESP_TOPIC, this.createXCResp(this.ACTIVATE_XC_RESP_TOPIC, this.enumActivateXCStatus.XC_INACTIVE_LAST_IN_PROCESSING, this.statusMessageCache.xtraCredit.activate[this.enumActivateXCStatus.XC_INACTIVE_LAST_IN_PROCESSING]));
                        return;
                    }
                    else  if (retValue === this.enumTransferXCStatus.OVER_LIMIT) { 
                        this.logger.debug({ debug: 'Xtra Credit can not be activated by over limit', data: '' }, true);
                        PubSub.publish(this.ACTIVATE_XC_RESP_TOPIC, this.createXCResp(this.ACTIVATE_XC_RESP_TOPIC, this.enumActivateXCStatus.XC_INACTIVE_OVER_LIMIT, this.statusMessageCache.xtraCredit.activate[this.enumActivateXCStatus.XC_INACTIVE_OVER_LIMIT]));
                        return;
                    } 
                    else  if (retValue === this.enumTransferXCStatus.ABORTED_E) { 
                        this.logger.debug({ debug: 'Xtra Credit can not be activated by aborted_e', data: '' }, true);
                        PubSub.publish(this.ACTIVATE_XC_RESP_TOPIC, this.createXCResp(this.ACTIVATE_XC_RESP_TOPIC, this.enumActivateXCStatus.XC_INACTIVE_ABORTED_E, this.statusMessageCache.xtraCredit.activate[this.enumActivateXCStatus.XC_INACTIVE_ABORTED_E]));
                        return;
                    }
                    else  if (retValue === this.enumTransferXCStatus.FAILED_E) { 
                        this.logger.debug({ debug: 'Xtra Credit can not be activated by failed_e', data: '' }, true);
                        PubSub.publish(this.ACTIVATE_XC_RESP_TOPIC, this.createXCResp(this.ACTIVATE_XC_RESP_TOPIC, this.enumActivateXCStatus.XC_INACTIVE_FAILED_E, this.statusMessageCache.xtraCredit.activate[this.enumActivateXCStatus.XC_INACTIVE_FAILED_E]));
                        return;
                    }             
                    else {
                        this.logger.debug({ debug: 'XCService: Xtra Credit can not be activated ' + ' by CashlessStatus Polling.', data: '' }, true);                        
                        PubSub.publish(this.ACTIVATE_XC_RESP_TOPIC, this.createXCResp(this.ACTIVATE_XC_RESP_TOPIC, this.enumActivateXCStatus.XC_INACTIVE_SEE_PLAYERS_CLUB, this.statusMessageCache.xtraCredit.activate[this.enumActivateXCStatus.XC_INACTIVE_SEE_PLAYERS_CLUB]));
                        return;
                    }
                    break;
                default:
                    this.logger.debug({ debug: 'Unknown BE2Response!', data: data }, true);
                    break;
            }
        });
    }

    checkCancellableResp(){
        this.appStoreCache = this.appStore.getAppStoreCache(); 
        // 0 = not supported by this BE2, 1 = not cancelable, 2 = cancelable
        let cancelable = this.appStoreCache.playerBonuses.xtraCredit.cancellable;
        if (cancelable < 2) {
            this.logger.debug({debug: 'XCService: Sending activateXCResp, but cancellation is either not supported (0) or not allowed (1). See data field for value.', data: cancelable}, true);
            PubSub.publish(this.ACTIVATE_XC_RESP_TOPIC, this.createXCResp(this.ACTIVATE_XC_RESP_TOPIC, this.enumActivateXCStatus.XC_ACTIVE_NOT_CANCELABLE,this.statusMessageCache.xtraCredit.activate[this.enumActivateXCStatus.XC_ACTIVE_NOT_CANCELABLE]));
        }           
        else {
            this.logger.debug({debug: 'XCService: Sending activateXCResp, Xtra Credit cancellation is supported.', data: ''}, true);
            PubSub.publish(this.ACTIVATE_XC_RESP_TOPIC, this.createXCResp(this.ACTIVATE_XC_RESP_TOPIC, this.enumActivateXCStatus.XC_ACTIVE,this.statusMessageCache.xtraCredit.activate[this.enumActivateXCStatus.XC_ACTIVE]));
        }
    }

    /**
     * Handle requested PIN validation response.
     * @param e validatePINResponse cmd
     */
    handleValidatePINResp(e) {        
        const data = e.detail;

        this.logger.info({ info: 'XCService: PIN validation response received.', data: data }, true);
        //Dont unsusbcribe for invalid scenario, since it can become valid and can be entered later.
        if (data.status === this.enumPINStatus.VALID || data.status === this.enumPINStatus.LOCKED || data.status === this.enumPINStatus.NO_RESPONSE || data.status === this.enumPINStatus.NOT_ENTERED) {
            PubSub.unsubscribe(this.VALIDATE_PIN_RESP_TOPIC, this.handleValidatePINRespFn);
        }

        if (data.status === this.enumPINStatus.VALID) {
            this.sendActivateXCResp(this.isAtiveWithAmount, this.amountInput);

            //reset values
            this.isAtiveWithAmount = false;
            this.amountInput = 0;
        } else if (data.status === this.enumPINStatus.LOCKED) {
            PubSub.publish(this.ACTIVATE_XC_RESP_TOPIC, this.createXCResp(this.ACTIVATE_XC_RESP_TOPIC, this.enumActivateXCStatus.XC_INACTIVE_PIN_LOCKED, this.statusMessageCache.xtraCredit.activate[this.enumActivateXCStatus.XC_INACTIVE_PIN_LOCKED]));
        }
        else {
            PubSub.publish(this.ACTIVATE_XC_RESP_TOPIC, this.createXCResp(this.ACTIVATE_XC_RESP_TOPIC, this.enumActivateXCStatus.XC_INACTIVE_PIN_VALIDATION_FAILED, this.statusMessageCache.xtraCredit.activate[this.enumActivateXCStatus.XC_INACTIVE_PIN_VALIDATION_FAILED]));
        }
    }
    
    
    /**
     * Helper function to send out relevant activateXtraCreditResp depending on cancelable value.
     * cancelable: 0 = not supported by this BE2, 1 = not cancelable, 2 = cancelable
     * @param cancelable
     */
    sendActivateXCResp(isSpecificAmount = false,amount = 0) {      
        
        if (!isSpecificAmount) {           
            this.checkCancellableResp();
            this.logger.debug({debug: 'XCService: Sending BE2 Basic Function request MoveCredits.', data: ''}, true);
            const code = NgxCommMsgHelper.enumBE2BasicFunc.MoveCredits;
            const message = NgxCommMsgHelper.createBE2BasicFunctionArr([[code, [0, 0, 0, 0]]]);
            this.ngxComm.sendBe2Message(message, this.doNothing.bind(this));
        }
        else {
            this.logger.debug({ debug: 'XCService: Sending BE2 Basic Function request TransferXtraCredit, the amount is:', data: parseInt(amount)}, true);
            const code = NgxCommMsgHelper.enumBE2BasicFunc.TransferXtraCredit;            
            const message = NgxCommMsgHelper.createBE2BasicFunctionArr([[code, [0, parseInt(amount), 1, 1]]]);
            this.ngxComm.sendBe2Message(message, this.processTransferXC.bind(this));
        }
    }

    /**
     * Helper function to create XCEnabled response
     * @param cmd
     * @param status true if enabled, false otherwise.
     * @returns {{cmd: *, ver: string, xtraCreditEnabled: *}}
     */
    createXCEnabledResp(cmd, status) {
        return {
            cmd: cmd,
            ver: '1.0',
            xtraCreditEnabled: status
        };
    }

    /**
     * Helper function to create validate PIN request
     * @returns {{cmd: *, ver: string, status: *}}
     */
    createValidatePINReq() {
        return {
            cmd: 'validatePIN',
            ver: '1.0'
        };
    }

    /**
     * Helper function to create activateXC response
     * @param cmd
     * @param status Status of activateXC request
     * @returns {{cmd: *, ver: string, status: *}}
     */
    createXCResp(cmd, status, statusMessage) {
        return {
            cmd: cmd,
            ver: '1.0',
            status: status,
            statusMessage : statusMessage
        };
    }

    // Public functions
    /**
     * Function to register isXCActive response callback.
     * The callback will be invoked when XCService publishes on IS_XC_ACTIVE_RESP_TOPIC.
     *
     * @param callback function to be called when XC is Active check result is available.
     **/
    registerIsActiveXCRespCB(callback) {
        this.checkIsActiveXCRespSubscribers.push(callback);
        PubSub.subscribe(this.IS_XC_ACTIVE_RESP_TOPIC, callback);
    };

    /**
     * Function to register Enable XC response callback.
     * The callback will be invoked when XCService publishes on CHECK_XC_ENABLED_RESP_TOPIC.
     *
     * @param callback function to be called when XC enabled check result is available.
     **/
    registerEnableXtraCreditRespCB(callback) {
        this.checkXCEnabledRespSubscribers.push(callback);
        PubSub.subscribe(this.CHECK_XC_ENABLED_RESP_TOPIC, callback);
    };

    /**
     * Function to register XC activation response callback.
     * The callback will be invoked when XCService publishes on ACTIVATE_XC_RESP_TOPIC.
     *
     * @param callback function to be called when XC activation result is available.
     **/
    registerActivateXtraCreditRespCB(callback) {
        this.activateXCRespSubscribers.push(callback);
        PubSub.subscribe(this.ACTIVATE_XC_RESP_TOPIC, callback);
    };

    /**
     * Function to register XC cancellation response callback.
     * The callback will be invoked when XCService publishes on CANCEL_XC_RESP_TOPIC.
     * @param callback function to be called when XC cancellation result is available.
     */
    registerCancelXtraCreditRespCB(callback) {
        this.cancelXCRespSubscribers.push(callback);
        PubSub.subscribe(this.CANCEL_XC_RESP_TOPIC, callback);
    };

    /**
     * Cleanup subscriptions. Typically should be called in Unit tests before the service instance is
     * destroyed.
     */
    cleanup() {
        // Un-subscribe all relevant amplify subscriptions
        if (this.checkIsActiveXCRespSubscribers !== undefined) {
            this.checkIsActiveXCRespSubscribers.forEach((item) => {
                PubSub.unsubscribe(this.IS_XC_ACTIVE_RESP_TOPIC, item);
            });
        }
        if (this.checkXCEnabledReqSubscribers !== undefined) {
            this.checkXCEnabledReqSubscribers.forEach((item) => {
                PubSub.unsubscribe(this.CHECK_XC_ENABLED_TOPIC, item);
            });
        }
        if (this.checkXCEnabledRespSubscribers !== undefined) {
            this.checkXCEnabledRespSubscribers.forEach((item) => {
                PubSub.unsubscribe(this.CHECK_XC_ENABLED_RESP_TOPIC, item);
            });
        }
        if (this.activateXCReqSubscribers !== undefined) {
            this.activateXCReqSubscribers.forEach((item) => {
                PubSub.unsubscribe(this.ACTIVATE_XC_TOPIC, item);
            });
        }
        if (this.activateXCRespSubscribers !== undefined) {
            this.activateXCRespSubscribers.forEach((item) => {
                PubSub.unsubscribe(this.ACTIVATE_XC_RESP_TOPIC, item);
            });
        }
        if (this.cancelXCReqSubscribers !== undefined) {
            this.cancelXCReqSubscribers.forEach((item) => {
                PubSub.unsubscribe(this.CANCEL_XC_TOPIC, item);
            });
        }
        if (this.cancelXCRespSubscribers !== undefined) {
            this.cancelXCRespSubscribers.forEach((item) => {
                PubSub.unsubscribe(this.CANCEL_XC_RESP_TOPIC, item);
            });
        }

        if (this.handleValidatePINRespSubscribers !== undefined) {
            this.handleValidatePINRespSubscribers.forEach((item) => {
                PubSub.unsubscribe(this.VALIDATE_PIN_RESP_TOPIC, item);
            });
        }
        this.logger.debug({debug: 'XCService: Cleaning up'}, true);
    };
};
