import PubSub from './PubSub';

'use strict';
/**
 * WorkerController provides a wrapper for web workers
 * @return {Object} The web worker controller.
 */

let instance = null;

export default class WorkersController {
    /**
     * constructor: Creates an instance of the WorkerController class stored in the instance property
     * @param urls: An array of Web Workers to create (defaulted to empty array if not supplied)
     * @param logger: An instance of the logger service
     */
    constructor(urls = [], logger) {
        if (instance == null) {
            this.workers = new Map();

            urls.forEach((url) => {
                let blob = new Blob([`importScripts("${url}")`]);
                let worker = new Worker(URL.createObjectURL(blob));
                this.workers.set(url, worker);
                this.receiveMessage(url, (event) => {
                    PubSub.publish('ww_message', event);
                });
            });

            instance = this;
        }
        else {
            logger.debug({debug: 'Attempted to create an instance of WorkerController when one already existed - Returning existing instance.'})
        }

    }

    /**
     * Instance: Returns the current instance of WorkerController
     * @returns {*}: Instance of WorkerController. Throws error if no instance of WorkerController exists
     * @constructor
     */
    static Instance() {
        if (instance == null) {
            throw new Error('Attempting to access an instance of WorkerController before instantiation.');
        }
        return instance;
    }

    formatEvent(event) {
        return (event && event instanceof CustomEvent) ? event.detail : event;
    }

    /**
     * broadcastMessage: Sends a message to all Web Workers
     * @param event: The msg for the Web Worker to sendf
     */
    broadcastMessage(event) {
        let formattedEvent = this.formatEvent(event);
        this.workers.forEach((worker) => {
            worker.postMessage(formattedEvent);
        });
    }

    /**
     * sendMessage: a handler function for sending messages from Web Workers
     * @param url: The url of the Web Worker to have sendMessage
     * @param event: The msg for the Web Worker to send
     */
    sendMessage(url, event) {
        this.workers.get(url).postMessage(event);
    };

    /**
     * receiveMessage: a handler function for Web Workers receiving messages
     * @param url: The url of the Web Worker to send the message to
     * @param callback: The callback function to call when the message is received
     */
    receiveMessage(url, callback) {
        this.workers.get(url).onmessage = function (event) {
            callback(event);
        };
    };

    /**
     * onError: a handler function for when Web Workers receive an error
     * @param url: The url of the Web Worker receiving the error
     * @param callback: The callback function to call when the error is received
     */
    onError(url, callback) {
        this.workers.get(url).onerror = function (error) {
            callback(error);
        };
    };

    /**
     * Destroy: Destroy the instance of WorkerController
     * NOTE: Destroy() only works when using Instance(), this will not invalidate pointers to a valid instance
     * @constructor
     */
    static Destroy() {
        instance = null;
    }
}
