'use strict';

/**
 * This module persists shell State.
 */

export default class ShellCache {
    constructor() {
        //Shell state that is persisted across carded sessions.
        this.shellState = {};
    }

    addShellStateElement(id, value, expireTime) {
        return new Promise((resolve) => {
            if (expireTime !== 0) {
                let timeoutInstance;

                timeoutInstance = setTimeout(() => {
                    delete this.shellState[id];
                }, expireTime);

                value = {
                    value: value,
                    timeoutinstance: timeoutInstance
                };
                //if the state element previously existed, check and remove previous timeout if it exists
                if (typeof this.shellState[id] !== 'undefined' && typeof this.shellState[id].timeoutinstance !== 'undefined') {
                    clearTimeout(this.shellState[id].timeoutinstance);
                }

                this.shellState[id] = value;
            }
            else {
                this.shellState[id] = {value: value};
            }
            return resolve();
        });
    };

    removeShellStateElement(id) {
        return new Promise((resolve) => {
            //if the state element previously existed, check and remove previous timeout if it exists
            if (typeof this.shellState[id] !== 'undefined' && typeof this.shellState[id].timeoutinstance !== 'undefined') {
                clearTimeout(this.shellState[id].timeoutinstance);
            }

            delete this.shellState[id];
            return resolve();
        });
    };

    /**
     * Stores a shell state element in the shellState.
     * @param id The id of the element.
     * @param value The value of the element.
     * @param expireTime (optional) The time in milliseconds till the element expires, 0 = never
     * @return Object shellState after a promise
     */
    storeShellStateElement(id, value, expireTime = 0) {
        return new Promise((resolve) => {
            return this.addShellStateElement(id, value, expireTime).then(() => {
                return resolve(this.shellState);
            });
        });
    }

    /**
     * Remove an element from the shellState
     * @param id The id of the element
     * @return shellState after a promise
     */
    deleteShellStateElement(id) {
        return this.removeShellStateElement(id);
    }

    /**
     * Retrieve an element from the shellState
     * @param id The id of the element.
     * if undefined, will return undefined
     * @return the value of the element by id
     */
    getShellStateElement(id) {
        if (typeof this.shellState[id] === 'undefined') {
            return undefined;
        }
        return this.shellState[id].value;
    }

    /**
     * Retrieve the shellState object
     * @return Object shellState
     */
    getShellState() {
        return this.shellState;
    }

    /**
     * Clear the shellState
     * @return Object empty ShellState
     */
    clearShellState() {
        this.shellState = {};
        return this.shellState;
    }
}

